import { useEffect, useRef, useState } from "react";
import { useSelector } from "react-redux";
import cls from "classnames";
import Elements from "./Elements";
import Settings from "./Settings";
import store, { RootState } from "@/store";
import BlockControl from "./BlockControl";
import Responsive from "./Responsive";
import { setSettings } from "@/store/editor";

interface Props {
  id: string;
}

export default function ControlPanel({ id }: Props) {
  const [active, setActive] = useState<"elements" | "settings" | "responsive">(
    "elements"
  );
  const lastActive = useRef<"elements" | "settings" | "responsive">(active);
  const activeBlock = useSelector(
    (state: RootState) => state.editorSettings.activeBlock
  );

  useEffect(() => {
    const last = lastActive.current;
    lastActive.current = active;
    if (last !== "responsive") {
      return;
    }
    store.dispatch(setSettings({
      devicePreview: "desktop",
      previewWidth: undefined
    }));
  }, [active]);

  return (
    <div className="wptb-control-panel">
      <div className="wptb-control-top">
        <button
          type="button"
          className={cls("wptb-tab-btn", {
            "wptb-active": !activeBlock && active === "elements",
          })}
          onClick={() => setActive("elements")}
        >
          Elements
        </button>
        <button
          type="button"
          className={cls("wptb-tab-btn", {
            "wptb-active": !activeBlock && active === "settings",
          })}
          onClick={() => setActive("settings")}
        >
          Settings
        </button>
        <button
          type="button"
          className={cls("wptb-tab-btn", {
            "wptb-active": !activeBlock && active === "responsive",
          })}
          onClick={() => setActive("responsive")}
        >
          Responsive
        </button>
      </div>
      {activeBlock ? (
        <BlockControl blockPath={activeBlock} id={id} />
      ) : (
        (active === "elements" && <Elements />) ||
        (active === "settings" && <Settings id={id} />) ||
        (active === "responsive" && <Responsive id={id} />)
      )}
    </div>
  );
}
