import store, { RootState } from "@/store";
import {
  addNotification,
  clearNotification,
  setNotificationItems,
  updateNotification as update,
} from "@/store/ui";
import { LoadingSpinnerIcon } from "@wptb/ui/icons/loaders";
import { ErrorSign, CheckSign } from "@wptb/ui/icons/sign";
import { NotificationType, UINotification } from "@wptb/dev/types";
import { createPortal } from "react-dom";
import { useSelector } from "react-redux";

let isFrameRunning = false;
const frame = () => {
  const notifications = store.getState().ui.notifications.items;
  const now = Date.now();
  const remaining: UINotification[] = [];
  let top = 100,
    with_duration = 0;

  notifications.forEach((n) => {
    const el = document.getElementById(`wptb-v2-notification-${n.id}`);
    if (n.type === "progress") {
      el?.style.setProperty("--progress", n.progress?.toString() || "0");
      with_duration++;
    } else if (n.duration) {
      const diff = now - n.spawned_at;
      if (diff > n.duration && !n.freeze) {
        return;
      }
      with_duration++;
      if (!n.freeze) {
        const pr = (1 - diff / n.duration) * 100;
        el?.style.setProperty("--progress", `${pr}`);
      }
    }
    remaining.push(n);
    if (!el) {
      return;
    }
    el.style.top = `${top}px`;
    top += el.getBoundingClientRect().height + 10;
  });
  if (remaining.length !== notifications.length) {
    store.dispatch(setNotificationItems(remaining));
  }
  if (with_duration) {
    requestAnimationFrame(frame);
  } else {
    isFrameRunning = false;
  }
};

export const Notifications = () => {
  const notifications = useSelector(
    (state: RootState) => state.ui.notifications.items
  );
  if (!isFrameRunning) {
    isFrameRunning = true;
    requestAnimationFrame(frame);
  }

  return (
    <>
      {notifications.map(
        ({ id, type, message, duration, progress, spawned_at, freeze }) =>
          createPortal(
            <div
              id={`wptb-v2-notification-${id}`}
              className={`wptb-v2-notification wptb-v2-notification-${type}`}
              onClick={() => store.dispatch(clearNotification(id))}
              onMouseEnter={() =>
                store.dispatch(
                  update({
                    id,
                    freeze: Date.now(),
                  })
                )
              }
              onMouseLeave={() =>
                store.dispatch(
                  update({
                    id,
                    freeze: undefined,
                    spawned_at: spawned_at + (Date.now() - freeze!),
                  })
                )
              }
            >
              <div className="wptb-v2-notification-icon">
                {type === "error" && ErrorSign}
                {type === "success" && CheckSign}
                {type === "progress" && LoadingSpinnerIcon}
                {!duration && !progress ? null : (
                  <svg
                    viewBox="0 0 36 36"
                    xmlns="http://www.w3.org/2000/svg"
                    className="wptb-v2-notification-progress"
                  >
                    <path
                      d="M18 2.0845
       a 15.9155 15.9155 0 0 1 0 31.831
       a 15.9155 15.9155 0 0 1 0 -31.831"
                      fill="none"
                      stroke="#eee"
                      strokeWidth="2.5"
                    />
                    <path
                      className="wptb-v2-notification-circle"
                      d="M18 2.0845
       a 15.9155 15.9155 0 0 1 0 31.831
       a 15.9155 15.9155 0 0 1 0 -31.831"
                      fill="none"
                      stroke="#00acc1"
                      strokeWidth="2.5"
                      strokeDasharray="var(--progress), 100"
                    />
                  </svg>
                )}
              </div>
              <div className="wptb-v2-notification-message">{message}</div>
            </div>,
            document.body
          )
      )}
    </>
  );
};

const notify = (
  message: string | React.ReactNode,
  type: NotificationType = "success",
  duration: number = 5000
): number => {
  const id = {
    value: 0,
  };
  store.dispatch(addNotification({ message, type, duration, captureId: id }));
  return id.value;
};

export default notify;

export const updateNotification = (
  id: number,
  change: Partial<UINotification>
) => {
  store.dispatch(update({ id, ...change }));
};
