import { BLOCK_MAP } from "@/block_registry";
import InvalidBlock from "./InvalidBlock";
import { useSelector } from "react-redux";
import { RootState } from "@/store";
import { useMemo } from "react";

interface BlockRCProps {
  id: string;
  index: number;
  ancestor: string[];
}

const Block = ({ id, index, ancestor }: BlockRCProps) => {
  const tableId = ancestor[0];
  const blockInfo = useSelector(
    (state: RootState) => state.table[tableId].innerBlocks[id]
  );
  const newAncestor = useMemo(() => [...ancestor, id], [ancestor, id]);

  const { name, blockOrder } = blockInfo;

  const BlockComponent = BLOCK_MAP[blockInfo.name]?.Edit ?? InvalidBlock;
  return (
    <BlockComponent blockPath={newAncestor} index={index} name={name}>
      {blockOrder.map((blockId, idx) => (
        <Block key={blockId} id={blockId} index={idx} ancestor={newAncestor} />
      ))}
    </BlockComponent>
  );
};

export default Block;
