import { BorderRadius } from "@wptb/dev/types";
import { useState } from "react";
import SizeControl from "../size-control";
import UnitControl from "../unit-control";
import ControlLabel from "../control-label";

interface BorderRadiusControlProps {
  value?: BorderRadius;
  label?: string;
  onChange?: (value: BorderRadius) => void;
  onReset?: () => void;
}

const BorderRadiusControl = ({
  value,
  label,
  onChange,
  onReset,
}: BorderRadiusControlProps) => {
  const isSplitInit = false;

  if (typeof value === "string") {
    if (value.indexOf(" ") !== -1) {
      // isSplitInit = true;
      const parts = value.split(" ");
      if (parts.length === 4) {
        value = {
          topLeft: parts[0],
          topRight: parts[1],
          bottomRight: parts[2],
          bottomLeft: parts[3],
        };
      } else if (parts.length === 2) {
        value = {
          topLeft: parts[0],
          topRight: parts[1],
          bottomRight: parts[0],
          bottomLeft: parts[1],
        };
      } else {
        value = {
          topLeft: parts[0],
          topRight: parts[1],
          bottomRight: parts[2],
          bottomLeft: parts[1],
        };
      }
      value = value.topLeft;
    }
  } else if (value?.topRight) {
    // isSplitInit = true;
    value = value.topRight;
  }

  const [isSplit] = useState(isSplitInit);

  const changeSplit = () => {
    // if (isSplit) {
    //   onChange?.((value as any).topLeft);
    //   setIsSplit(false);
    // } else {
    //   onChange?.({
    //     topLeft: value as any,
    //     topRight: value as any,
    //     bottomRight: value as any,
    //     bottomLeft: value as any,
    //   });
    //   setIsSplit(true);
    // }
    WPTB.utils.notify("This is an upcoming feature", "error");
  };

  return (
    <div className="wptb-border-radius-control wptb-control">
      <ControlLabel label={label} onReset={onReset}>
        <button type="button" onClick={changeSplit} className="wptb-split-btn">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 24 24"
            width="24"
            height="24"
            focusable="false"
          >
            {isSplit ? (
              <path d="M17.031 4.703 15.576 4l-1.56 3H14v.03l-2.324 4.47H9.5V13h1.396l-1.502 2.889h-.95a3.694 3.694 0 0 1 0-7.389H10V7H8.444a5.194 5.194 0 1 0 0 10.389h.17L7.5 19.53l1.416.719L15.049 8.5h.507a3.694 3.694 0 0 1 0 7.39H14v1.5h1.556a5.194 5.194 0 0 0 .273-10.383l1.202-2.304Z" />
            ) : (
              <path d="M10 17.389H8.444A5.194 5.194 0 1 1 8.444 7H10v1.5H8.444a3.694 3.694 0 0 0 0 7.389H10v1.5ZM14 7h1.556a5.194 5.194 0 0 1 0 10.39H14v-1.5h1.556a3.694 3.694 0 0 0 0-7.39H14V7Zm-4.5 6h5v-1.5h-5V13Z" />
            )}
          </svg>
        </button>
      </ControlLabel>
      {isSplit ? (
        <>
          <div className="wptb-border-radius-control-split">
            <UnitControl
              value={(value as any)?.topLeft}
              onChange={(topLeft) => onChange?.({ ...(value as any), topLeft })}
              autoUnit
            />
            <UnitControl
              value={(value as any)?.topRight}
              onChange={(topRight) =>
                onChange?.({ ...(value as any), topRight })
              }
              autoUnit
            />
          </div>
          <div className="wptb-border-radius-control-split">
            <UnitControl
              value={(value as any)?.bottomRight}
              onChange={(bottomRight) =>
                onChange?.({ ...(value as any), bottomRight })
              }
              autoUnit
            />
            <UnitControl
              value={(value as any)?.bottomLeft}
              onChange={(bottomLeft) =>
                onChange?.({ ...(value as any), bottomLeft })
              }
              autoUnit
            />
          </div>
        </>
      ) : (
        <div>
          <SizeControl
            value={value as any}
            onChange={onChange}
            allowedUnits={{
              px: {
                max: 100,
              },
              "%": {
                max: 100,
              },
              em: {
                max: 10,
              },
              rem: {
                max: 10,
              },
            }}
            allowOutOfRange
          />
        </div>
      )}
    </div>
  );
};

export default BorderRadiusControl;
