import { v4 as uuidv4 } from "uuid";
import store from "@/store";
import { setTable, TABLE_ID } from "@/store/table";
import { BlockState, TableState, TableStateDevice } from "@wptb/dev/types";
import { CellProps, TableProps } from "@wptb/dev/types";
import { defaultTableProps } from "@/builder/blocks/default-props";

export const createNewTable = (
  rows: number,
  cols: number,
  contents?: string[][]
) => {
  const tableProps: TableProps = {
    ...defaultTableProps,
    cols,
    rows,
  };

  const blocksProps: Record<string, any> = {};
  const innerBlocks: Record<string, BlockState> = {};
  const cellOrder: Record<string, string[]> = {};
  const rowOrder: string[] = [];

  blocksProps[TABLE_ID] = tableProps;

  for (let i = 0; i < rows; i++) {
    const rowId = uuidv4();
    rowOrder.push(rowId);
    cellOrder[rowId] = [];
    for (let j = 0; j < cols; j++) {
      const cellId = uuidv4();
      cellOrder[rowId].push(cellId);
      innerBlocks[cellId] = {
        name: "cell",
        blockOrder: [],
        parentId: rowId,
      };
      blocksProps[cellId] = {
        row: i,
        col: j,
        colspan: 1,
        rowspan: 1,
      } as CellProps;

      const textId = uuidv4();
      innerBlocks[textId] = {
        name: "text",
        blockOrder: [],
        parentId: cellId,
      };
      innerBlocks[cellId].blockOrder.push(textId);
      blocksProps[textId] = {
        text: contents?.[i][j] || "",
      };
    }
    blocksProps[rowId] = {};
  }

  const table: TableStateDevice = {
    innerBlocks: cellOrder,
    blockOrder: rowOrder,
  };

  const state: TableState = {
    innerBlocks,
    blocksProps,
    blocksPropsResponsive: {},
    desktop: table,
    mobile: table,
    tablet: table,
  };

  const id = uuidv4();

  store.dispatch(setTable({ id, table: state }));

  return id;
};

export const isBuilderDiry = (): boolean => {
  const { editorSettings } = store.getState();
  const timeTravelState = editorSettings.timeTravel;
  return (
    editorSettings.isSaving ||
    timeTravelState.lastSaved !== timeTravelState.cursor
  );
};
