import { WPTBBlock, WPTBInnerBlock } from "@wptb/dev/types";
import { RequiredExcept } from "@wptb/dev/types";

export const FREE_BLOCKS: WPTBBlock[] = [];
export const PRO_BLOCKS: WPTBBlock[] = [];

export const BLOCK_MAP: Record<string, WPTBInnerBlock<any>> = {};

export const ENHANCED_CONTROLS: Record<string, any> = {};
export const ENHANCEMENT_MAP: Record<string, any> = {};

export const registerBlock = <T = Record<string, any>, U = any>(
  block: WPTBBlock &
    RequiredExcept<
      WPTBInnerBlock<T, U>,
      "defaultInnerBlocks" | "supportedChildren" | "supportedParents"
    >
) => {
  const inner: WPTBInnerBlock<T> = {
    name: block.name,
    Control: block.Control,
    Edit: block.Edit,
    Preview: block.Preview,
    isBlock: block.isBlock,
    parse: block.parse,
    defaultProps: block.defaultProps,
    defaultInnerBlocks: block.defaultInnerBlocks,
    supportedParents: block.supportedParents,
    supportedChildren: block.supportedChildren,
  };

  const inserter: WPTBBlock = {
    name: block.name,
    title: block.title,
    icon: block.icon,
    isPro: block.isPro,
  };

  if (inserter.isPro) {
    const idx = PRO_BLOCKS.findIndex((b) => b.name === inserter.name);
    if (idx > -1) {
      PRO_BLOCKS[idx] = inserter;
    } else {
      PRO_BLOCKS.push(inserter);
    }
  } else {
    FREE_BLOCKS.push(inserter);
  }
  BLOCK_MAP[inner.name] = inner;
};

export const registerInnerBlock = <T = Record<string, any>, U = any>(
  block: WPTBInnerBlock<T, U>
) => {
  BLOCK_MAP[block.name] = block;
};

export const registerUpsell = (block: WPTBBlock) => {
  PRO_BLOCKS.push(block);
};

export const registerEnhancement = (name: string, enhancement: any) => {
  ENHANCEMENT_MAP[name] = enhancement;
};
