import { Border, BorderSide, SpacingValue } from "@wptb/dev/types";

export const parseBorder = (
  el: HTMLElement
): Border | BorderSide | undefined => {
  const style = el.style;
  if (style.border) {
    const [width, bstyle, ...color] = style.border.split(" ");
    return {
      width,
      style: bstyle,
      color: hexToRgbA(color.join(" ")),
    } as BorderSide;
  }

  if (style.borderWidth) {
    const width = style.borderWidth;
    const fIdx = width.indexOf(" ");
    if (fIdx > -1) {
      return;
    }
    return {
      width,
      style: style.borderStyle as any,
      color: hexToRgbA(style.borderColor),
    };
  }

  if (!style.borderTop) {
    return;
  }

  const borderStyle: Partial<Border> = {};
  if (style.borderTop) {
    const [width, bstyle, ...color] = style.borderTop.split(" ");
    borderStyle.top = {
      width,
      style: bstyle,
      color: hexToRgbA(color.join(" ")),
    } as BorderSide;
  }
  if (style.borderRight) {
    const [width, bstyle, ...color] = style.borderRight.split(" ");
    borderStyle.right = {
      width,
      style: bstyle,
      color: hexToRgbA(color.join(" ")),
    } as BorderSide;
  }
  if (style.borderBottom) {
    const [width, bstyle, ...color] = style.borderBottom.split(" ");
    borderStyle.bottom = {
      width,
      style: bstyle,
      color: hexToRgbA(color.join(" ")),
    } as BorderSide;
  }
  if (style.borderLeft) {
    const [width, bstyle, ...color] = style.borderLeft.split(" ");
    borderStyle.left = {
      width,
      style: bstyle,
      color: hexToRgbA(color.join(" ")),
    } as BorderSide;
  }
  return borderStyle as any;
};

export interface TableDirectives {
  innerBorders?: {
    active?: "all" | "row" | "column";
    borderWidth?: number;
    borderRadiuses?: {
      all?: number;
      row?: number;
      column?: number;
    };
  };
}

export const parseInnerBorder = (
  table: HTMLTableElement,
  tableBorder: Border | BorderSide | undefined,
  directives: TableDirectives
): Border | BorderSide | undefined => {
  const inner = directives.innerBorders || {};
  if (!tableBorder && !directives.innerBorders?.borderWidth) {
    return;
  }
  let width = inner.borderWidth
    ? parseInt(inner.borderWidth?.toString() || "0") + "px"
    : undefined;
  let style: any = "solid";

  const COLOR_MAP: Record<string, number> = {};

  const cells = table.querySelectorAll("td, th");
  for (let i = 0; i < cells.length; i++) {
    const cell = cells[i] as HTMLElement;
    const color = cell.style.borderColor;
    if (color) {
      COLOR_MAP[color] = (COLOR_MAP[color] || 0) + 1;
    }
  }

  let color: string | undefined;
  let maxCount = 0;
  for (const key in COLOR_MAP) {
    if (COLOR_MAP[key] > maxCount) {
      maxCount = COLOR_MAP[key];
      color = key;
    }
  }

  if ((tableBorder as BorderSide)?.style) {
    style = (tableBorder as BorderSide).style;
  } else if ((tableBorder as Border)?.left?.style) {
    style = (tableBorder as Border).left.style;
  }
  if (!width && (tableBorder as BorderSide)?.width) {
    width = parseInt((tableBorder as BorderSide).width) + "px";
  } else if ((tableBorder as Border)?.left?.width) {
    width = parseInt((tableBorder as Border).left.width) + "px";
  }

  return {
    width,
    style,
    color,
  };
};

export const getInnerBorderRadius = (directives: TableDirectives) => {
  const inner = directives.innerBorders || {};
  const radius = inner.borderRadiuses?.[inner.active || "all"] || 0;
  return radius.toString() + "px";
};

export const hexToRgbA = (hex?: string): string => {
  if (!hex || !hex.startsWith("#")) {
    return hex;
  }

  let cleanHex = hex.substring(1);
  if (cleanHex.length === 3) {
    cleanHex = cleanHex
      .split("")
      .map((char) => char + char)
      .join("");
  }

  const r = parseInt(cleanHex.substring(0, 2), 16);
  const g = parseInt(cleanHex.substring(2, 4), 16);
  const b = parseInt(cleanHex.substring(4, 6), 16);

  if (hex.length > 6) {
    const alpha = parseInt(cleanHex.substring(6, 8), 16) / 255;
    if (alpha < 1) {
      return `rgba(${r}, ${g}, ${b}, ${alpha})`;
    }
  }
  return `rgb(${r}, ${g}, ${b})`;
};

export const parseSpacing = (
  name: "margin" | "padding",
  style?: CSSStyleDeclaration
): SpacingValue | undefined => {
  const value = style?.[name];
  if (value) {
    if (value.toString().includes(" ")) {
      const all = value.split(" ");
      return {
        top: all[0],
        right: all[1] || all[0],
        bottom: all[2] || all[0],
        left: all[3] || all[0],
      };
    }
    return value;
  }
  const value2: any = {};
  ["Top", "Right", "Bottom", "Left"].map((side) => {
    const val = style?.[name + side];
    if (val) {
      value2[side.toLowerCase()] = val;
    }
  });
  if (Object.keys(value2).length) {
    return value2;
  }
};
