import cls from "classnames";
import { useEffect, useRef } from "react";
import FloatingActions from "../actions/floating";

interface ElementProps {
  name: string;
  icon: any;
  title: string;
  isUpsell?: boolean;
  showUpsell?: (name: string) => void;
}

export default function Element({
  name,
  icon,
  title,
  isUpsell,
  showUpsell,
}: ElementProps) {
  const elRef = useRef<HTMLButtonElement>();
  useEffect(() => {
    if (!elRef.current) {
      return;
    }

    function dragEvt(this: HTMLButtonElement, e: MouseEvent) {
      e.preventDefault();
      if (!isUpsell) {
        FloatingActions.startInsertion(name, this);
      } else {
        showUpsell?.(name);
      }
    }

    elRef.current.addEventListener("mousedown", dragEvt);
    return () => {
      elRef.current?.removeEventListener("mousedown", dragEvt);
    };
  }, [elRef.current]);
  return (
    <button
      type="button"
      className={cls("wptb-element", {
        "wptb-upsell-element": isUpsell,
      })}
      ref={elRef as any}
    >
      {icon}
      <span>{title}</span>
    </button>
  );
}
