import { TablesState, TableState } from "@wptb/dev/types";
import store from "..";
import { customReducer } from "../table";
import TimeTravelCapturer from "./capturer";
import { redoChanges, undoChanges } from "./apply-changes";
import BuilderCtx from "@/builder/ctx";
import { timeTravel, timeTravelAdded } from "../editor";

const propsUpdater: {
  fn?: () => void;
  id?: string;
  last: number;
  old?: any;
} = {
  last: Date.now(),
};

function saveUnsavedProps() {
  if (propsUpdater.fn && Date.now() - propsUpdater.last > 700) {
    propsUpdater.fn();
    propsUpdater.fn = undefined;
    propsUpdater.id = undefined;
  }
  requestAnimationFrame(saveUnsavedProps);
}
saveUnsavedProps();

const INSTANCES: Record<string, TimeTravel> = {};

export default class TimeTravel {
  entries: any[] = [];
  cursor = 0;
  total = 0;

  static init() {
    if (BuilderCtx.TABLE_ID! in INSTANCES) {
      return;
    }
    INSTANCES[BuilderCtx.TABLE_ID!] = new TimeTravel();
  }

  static by(steps: number) {
    INSTANCES[BuilderCtx.TABLE_ID!]?.travel(steps);
  }

  public travel(steps: number) {
    if (propsUpdater.fn) {
      propsUpdater.fn();
      propsUpdater.fn = undefined;
      propsUpdater.id = undefined;
    }

    this.cursor += steps;
    if (steps > 0) {
      this.cursor -= 1;
    }

    if (this.cursor >= this.total) {
      this.cursor = this.total;
      return;
    }

    if (this.cursor < 0) {
      this.cursor = 0;
      return;
    }

    const changes = this.entries[this.cursor];
    if (!changes) {
      return;
    }

    const device = store.getState().editorSettings.devicePreview;

    if (steps < 0) {
      store.dispatch(
        customReducer((tables: TablesState) => {
          undoChanges(tables[BuilderCtx.TABLE_ID!], changes, device);
        })
      );
    } else {
      store.dispatch(
        customReducer((tables: TablesState) => {
          redoChanges(tables[BuilderCtx.TABLE_ID!], changes, device);
        })
      );
      this.cursor += 1;
    }
    store.dispatch(timeTravel(this.cursor));
  }

  static captured<T extends { capturer: TimeTravelCapturer }>(
    reducer: (arg: T) => any,
    payload: Omit<T, "capturer" | "tableId">,
    responsive: boolean = true
  ) {
    const capturer = TimeTravelCapturer.init();
    // @ts-expect-error avoiding destructure as this can be called a lot
    payload.capturer = capturer;
    // @ts-expect-error avoiding destructure as this can be called a lot
    payload.tableId = BuilderCtx.TABLE_ID!;
    store.dispatch(reducer(payload as any));
    propsUpdater.fn?.();
    propsUpdater.fn = undefined;
    propsUpdater.id = undefined;
    INSTANCES[BuilderCtx.TABLE_ID!]?.saveEntry(capturer.getChanges(responsive));
  }

  static capturedFn<T extends { capturer: TimeTravelCapturer }>(
    fn: (table: TableState, arg: T) => any,
    payload: Omit<T, "capturer" | "tableId">,
    responsive: boolean = true
  ) {
    const capturer = TimeTravelCapturer.init();
    // @ts-expect-error avoiding destructure as this can be called a lot
    payload.capturer = capturer;
    // @ts-expect-error avoiding destructure as this can be called a lot
    payload.tableId = BuilderCtx.TABLE_ID!;
    store.dispatch(
      customReducer((tables) =>
        fn(tables[BuilderCtx.TABLE_ID!], payload as any)
      )
    );
    propsUpdater.fn?.();
    propsUpdater.fn = undefined;
    propsUpdater.id = undefined;
    INSTANCES[BuilderCtx.TABLE_ID!]?.saveEntry(capturer.getChanges(responsive));
  }

  private saveEntry(entry: any) {
    this.entries[this.cursor] = entry;
    this.cursor += 1;
    this.total = this.cursor;
    store.dispatch(timeTravelAdded(this.cursor));
  }

  static updateProps<T extends Record<string, any>>(blockId: string, props: T) {
    if (propsUpdater.id !== blockId) {
      propsUpdater.fn?.();
      propsUpdater.old =
        store.getState().table[BuilderCtx.TABLE_ID!].blocksProps[blockId];
    }
    propsUpdater.id = blockId;
    store.dispatch(
      customReducer((tables: TablesState) => {
        Object.assign(tables[BuilderCtx.TABLE_ID!].blocksProps[blockId], props);
      })
    );
    propsUpdater.last = Date.now();
    propsUpdater.fn = () => {
      propsUpdater.last = Date.now();
      const newProps =
        store.getState().table[BuilderCtx.TABLE_ID!].blocksProps[blockId];
      INSTANCES[BuilderCtx.TABLE_ID!]?.saveEntry({
        from: propsUpdater.old,
        to: newProps,
        blockId: blockId,
      });
    };
  }
}
