import {
  Border,
  BorderRadius,
  BorderSide,
  SpacingValue,
} from "@wptb/dev/types";
import { CellProps, TableProps } from "@wptb/dev/types";
import { CSSProperties } from "react";

export const getSingleBorderStr = (border?: BorderSide): string => {
  if (!border) return "";
  return border.width + " " + border.style + " " + border.color;
};

export const getCellBorderWithRadius = (
  props: CellProps,
  tableProps: TableProps
) => {
  const rad = tableProps.innerBorderRadius as string;
  if (
    !tableProps.enableInnerBorder ||
    (!tableProps.headerInnerBorder && props.row === 0)
  ) {
    return {
      borderRadius: rad,
    };
  }
  const borderColor =
    (props.border as BorderSide)?.color ||
    (tableProps.innerBorder as BorderSide)?.color ||
    "#000";

  const style: CSSProperties = {
    borderColor,
    borderStyle: "solid",
  };

  let width = (props.border as BorderSide)?.width;

  if (!width) {
    const width2 = (tableProps.innerBorder as BorderSide)?.width || "1px";
    if (tableProps.columnBorderOnly) {
      if (props.row === 0) {
        width = `${width2} ${width2} 0px ${width2}`;
        if (rad) {
          style.borderTopLeftRadius = rad;
          style.borderTopRightRadius = rad;
        }
      } else if (props.row + props.rowspan === tableProps.rows) {
        width = `0px ${width2} ${width2} ${width2}`;
        if (rad) {
          style.borderBottomLeftRadius = rad;
          style.borderBottomRightRadius = rad;
        }
      } else {
        width = `0px ${width2}`;
      }
    } else if (tableProps.rowBorderOnly) {
      if (props.col === 0) {
        width = `${width2} 0px ${width2} ${width2}`;
        if (rad) {
          style.borderTopLeftRadius = rad;
          style.borderBottomLeftRadius = rad;
        }
      } else if (props.col + props.colspan === tableProps.cols) {
        width = `${width2} ${width2} ${width2} 0px`;
        if (rad) {
          style.borderTopRightRadius = rad;
          style.borderBottomRightRadius = rad;
        }
      } else {
        width = `${width2} 0px`;
      }
    } else {
      width = width2;
      style.borderRadius = rad;
    }
  } else {
    if (tableProps.columnBorderOnly) {
      if (props.row === 0) {
        if (rad) {
          style.borderTopLeftRadius = rad;
          style.borderTopRightRadius = rad;
        }
      } else if (props.row + props.rowspan === tableProps.rows) {
        if (rad) {
          style.borderBottomLeftRadius = rad;
          style.borderBottomRightRadius = rad;
        }
      }
    } else if (tableProps.rowBorderOnly) {
      if (props.col === 0) {
        if (rad) {
          style.borderTopLeftRadius = rad;
          style.borderBottomLeftRadius = rad;
        }
      } else if (props.col + props.colspan === tableProps.cols) {
        if (rad) {
          style.borderTopRightRadius = rad;
          style.borderBottomRightRadius = rad;
        }
      }
    } else {
      style.borderRadius = rad;
    }
  }
  if (!width) {
    return {
      borderRadius: rad,
    };
  }
  style.borderWidth = width;
  return style;
};

export const getBorderStyle = (border?: Border | BorderSide): CSSProperties => {
  if (!border) return {};
  const style: CSSProperties = {};
  if ((border as Border).top) {
    style.borderTop = getSingleBorderStr((border as Border).top);
    style.borderRight = getSingleBorderStr((border as Border).right);
    style.borderBottom = getSingleBorderStr((border as Border).bottom);
    style.borderLeft = getSingleBorderStr((border as Border).left);
  } else if ((border as BorderSide).width) {
    style.border = getSingleBorderStr(border as BorderSide);
  }
  return style;
};

export const getSpacingStyle = (
  spacing?: SpacingValue,
  prop: "margin" | "padding" = "margin"
): CSSProperties => {
  const style: CSSProperties = {};
  if (!spacing) {
    return style;
  }
  if (typeof spacing === "string") {
    style[prop] = spacing;
  } else if (spacing.top) {
    style[prop + "Top"] = spacing.top;
    style[prop + "Right"] = spacing.right;
    style[prop + "Bottom"] = spacing.bottom;
    style[prop + "Left"] = spacing.left;
  }

  return style;
};

export const getBorderRadiusStyle = (
  borderRadius?: BorderRadius
): CSSProperties => {
  const style: CSSProperties = {};
  if (!borderRadius) {
    return style;
  }
  if (typeof borderRadius === "string") {
    style.borderRadius = borderRadius;
  } else if (borderRadius.topLeft) {
    style.borderTopLeftRadius = borderRadius.topLeft;
    style.borderTopRightRadius = borderRadius.topRight;
    style.borderBottomRightRadius = borderRadius.bottomRight;
    style.borderBottomLeftRadius = borderRadius.bottomLeft;
  }
  return style;
};

export const getBorderRadiusCss = (borderRadius?: BorderRadius): string => {
  if (!borderRadius) {
    return "";
  }
  if (typeof borderRadius === "string") {
    return borderRadius;
  }

  return (
    borderRadius.topLeft +
    " " +
    borderRadius.topRight +
    " " +
    borderRadius.bottomRight +
    " " +
    borderRadius.bottomLeft
  );
};

export const convertUnits = (
  oldUnit: string,
  newUnit: string,
  value: number
) => {
  const rootFontSize = 16;
  const parentFontSize = 16;

  let valueInPx;

  switch (oldUnit) {
    case "px":
      valueInPx = value;
      break;
    case "rem":
      valueInPx = value * rootFontSize;
      break;
    case "em":
      valueInPx = value * parentFontSize;
      break;
    default:
      return value;
  }

  let convertedValue;
  switch (newUnit) {
    case "px":
      convertedValue = valueInPx;
      break;
    case "rem":
      convertedValue = valueInPx / rootFontSize;
      break;
    case "em":
      convertedValue = valueInPx / parentFontSize;
      break;
    default:
      return value;
  }

  return convertedValue;
};
