import { useCallback, useState } from "react";
import { useSelector } from "react-redux";

import { TableProps } from "@wptb/dev/types";
import PanelBody from "@wptb/ui/components/panel-body";
import SizeControl from "@wptb/ui/components/size-control";
import RadioGroup from "@wptb/ui/components/radio-group";
import SwitchControl from "@wptb/ui/components/switch-control";
import ColorControl from "@wptb/ui/components/color-control";
import SelectControl from "@wptb/ui/components/select-control";
import BorderControl from "@wptb/ui/components/border-control";

import {
  AlignLeftIcon,
  AlignCenterIcon,
  AlignRightIcon,
} from "@wptb/ui/icons/alignment";
import { RootState } from "@/store";
import UpsellControl from "@/builder/components/upsell-control";
import BorderRadiusControl from "@wptb/ui/components/border-radius-control";
import RangeControl from "@wptb/ui/components/range-control";
import Tab from "@wptb/ui/components/tab";
import { BlockControlProps } from "@wptb/dev/types";
import { TABLE_ID } from "@/store/table";
import TimeTravel from "@/store/time-travel";
import { EnhancementGroup } from "@/builder/components/enhanced-control";
import { LongTextControl } from "@wptb/ui/components";

const BGColorTab = ({
  blockProps,
  setBlockProps,
}: BlockControlProps<TableProps>) => {
  return (
    <div className="wptb-control-group">
      <ColorControl
        label="Header Background"
        value={blockProps?.headerBg}
        onReset={() => setBlockProps({ headerBg: undefined })}
        onChange={(headerBg: any) => setBlockProps({ headerBg })}
      />
      <ColorControl
        label="Odd Row Background"
        value={blockProps?.oddRowBg}
        onReset={() => setBlockProps({ oddRowBg: undefined })}
        onChange={(oddRowBg: any) => setBlockProps({ oddRowBg })}
      />
      <ColorControl
        label="Even Row Background"
        value={blockProps?.evenRowBg}
        onReset={() => setBlockProps({ evenRowBg: undefined })}
        onChange={(evenRowBg: any) => setBlockProps({ evenRowBg })}
      />
    </div>
  );
};

const HoverBGColorTab = ({
  blockProps,
  setBlockProps,
}: BlockControlProps<TableProps>) => {
  return (
    <div className="wptb-control-group">
      <ColorControl
        label="Header Background"
        value={blockProps?.hoverHeaderBg}
        onReset={() => setBlockProps({ hoverHeaderBg: undefined })}
        onChange={(hoverHeaderBg: any) => setBlockProps({ hoverHeaderBg })}
      />
      <ColorControl
        label="Odd Row Background"
        value={blockProps?.hoverOddRowBg}
        onReset={() => setBlockProps({ hoverOddRowBg: undefined })}
        onChange={(hoverOddRowBg: any) => setBlockProps({ hoverOddRowBg })}
      />
      <ColorControl
        label="Even Row Background"
        value={blockProps?.hoverEvenRowBg}
        onReset={() => setBlockProps({ hoverEvenRowBg: undefined })}
        onChange={(hoverEvenRowBg: any) => setBlockProps({ hoverEvenRowBg })}
      />
    </div>
  );
};

interface SettingsProps {
  id: string;
}

export default function Settings({ id }: SettingsProps) {
  const tableProps: TableProps = useSelector(
    (state: RootState) => state.table[id]?.blocksProps[TABLE_ID]
  );

  const setAttrs = useCallback((attrs: Partial<TableProps>) => {
    TimeTravel.updateProps(TABLE_ID, attrs);
  }, []);

  const [bgTab, setBgTab] = useState("general");

  if (!tableProps) return null;

  return (
    <div className="wptb-controls">
      <EnhancementGroup name="table" props={tableProps} setProps={setAttrs} />
      <PanelBody title="GENERAL" className="wptb-settings">
        <SizeControl
          label="Cell Padding"
          value={tableProps.cellPadding}
          onChange={(cellPadding) => setAttrs({ cellPadding })}
        />
        <RadioGroup
          label="Alignment"
          value={tableProps.alignment}
          onChange={(alignment: any) => setAttrs({ alignment })}
          items={[
            {
              label: "Align Left",
              value: "left",
              icon: AlignLeftIcon,
            },
            {
              label: "Align Center",
              value: "center",
              icon: AlignCenterIcon,
            },
            {
              label: "Align Right",
              value: "right",
              icon: AlignRightIcon,
            },
          ]}
        />
        <SwitchControl
          label="Table Container Max Width"
          checked={tableProps.enableMaxWidth as any}
          onChange={(enableMaxWidth) => setAttrs({ enableMaxWidth })}
        />

        {tableProps.enableMaxWidth && (
          <SizeControl
            label="Table Container Max Width"
            value={tableProps.maxWidth}
            onChange={(maxWidth) => setAttrs({ maxWidth })}
            allowedUnits={{
              px: { max: 2000 },
              rem: { max: 100 },
              em: { max: 100 },
            }}
            allowOutOfRange
          />
        )}

        <SizeControl
          label="Cell Min Auto Width"
          value={tableProps.cellMinWidth}
          onChange={(cellMinWidth) => setAttrs({ cellMinWidth })}
          allowedUnits={{ px: { max: 500 } }}
          allowOutOfRange
        />

        <SwitchControl
          label="Sort Table Horizontally"
          checked={!!tableProps.sortHorizontal?.enable}
          onChange={(v) =>
            setAttrs({
              sortHorizontal: { ...tableProps.sortHorizontal, enable: v },
              sortVertical: {
                ...tableProps.sortVertical,
                enable: false,
              },
            })
          }
        />
        <SwitchControl
          label="Sort Table Vertically"
          checked={!!tableProps.sortVertical?.enable}
          onChange={(v) =>
            setAttrs({
              sortVertical: { ...tableProps.sortVertical, enable: v },
              sortHorizontal: {
                ...tableProps.sortHorizontal,
                enable: false,
              },
            })
          }
        />

        <UpsellControl name="global.separate_row_col">
          <SwitchControl
            label="Separate Rows/Columns"
            checked={tableProps.separateRowCol as any}
          />
        </UpsellControl>
      </PanelBody>
      <PanelBody title="GLOBAL FONT STYLE" className="wptb-settings">
        <UpsellControl name="global.font_style">
          <ColorControl label="Font Color" value={tableProps.fontColor} />
          <ColorControl label="Link Color" value={tableProps.linkColor} />
          <SizeControl label="Font Size" value={tableProps.fontSize} />
        </UpsellControl>
      </PanelBody>
      <PanelBody title="BORDER" className="wptb-settings">
        <BorderControl
          label="Table Border"
          value={tableProps.tableBorder}
          onChange={(tableBorder) => setAttrs({ tableBorder })}
          onReset={() => setAttrs({ tableBorder: undefined })}
        />

        <SwitchControl
          label="Enable Inner Border"
          checked={tableProps.enableInnerBorder as any}
          onChange={(enableInnerBorder) => setAttrs({ enableInnerBorder })}
        />
        {tableProps.enableInnerBorder && (
          <>
            <BorderControl
              label="Inner Border"
              value={tableProps.innerBorder}
              onChange={(innerBorder) => setAttrs({ innerBorder })}
              onReset={() => setAttrs({ innerBorder: undefined })}
            />
            <SwitchControl
              label="Header Inner Border"
              checked={tableProps.headerInnerBorder as any}
              onChange={(headerInnerBorder) => setAttrs({ headerInnerBorder })}
            />

            <UpsellControl name="global.row_col_border">
              <SwitchControl
                label="Column Only Border"
                checked={tableProps.columnBorderOnly as any}
              />

              <SwitchControl
                label="Row Only Border"
                checked={tableProps.rowBorderOnly as any}
              />
            </UpsellControl>
            <UpsellControl name="global.cell_radius">
              <BorderRadiusControl
                label="Cell Border Radius"
                value={tableProps.innerBorderRadius}
              />
            </UpsellControl>
          </>
        )}
      </PanelBody>

      <PanelBody title="BACKGROUND" className="wptb-settings">
        <Tab
          active={bgTab}
          setActive={setBgTab}
          tabs={{
            general: { title: "General", component: BGColorTab },
            hover: { title: "Hover", component: HoverBGColorTab },
          }}
          props={{ blockProps: tableProps, setBlockProps: setAttrs }}
        />
      </PanelBody>

      <PanelBody title="SCROLL" className="wptb-settings">
        <SwitchControl
          label="Enable horizontal scrolling"
          checked={tableProps.scrollX as any}
          onChange={(scrollX) => setAttrs({ scrollX })}
        />
      </PanelBody>

      <PanelBody title="STYLES" className="wptb-settings">
        <SwitchControl
          label="Disable theme styles"
          checked={tableProps.disableThemeStyles as any}
          onChange={(disableThemeStyles) => setAttrs({ disableThemeStyles })}
        />
        {tableProps.disableThemeStyles && (
          <LongTextControl
            label="Extra Styles"
            value={tableProps.extraStyles}
            onChange={(extraStyles) => setAttrs({ extraStyles })}
          />
        )}
      </PanelBody>
      <PanelBody title="STICKY" className="wptb-settings">
        <UpsellControl name="global.sticky">
          <SwitchControl
            label="Make Top Row Sticky"
            checked={tableProps.stickyTopRow as any}
          />
          <SwitchControl
            label="Make First Column Sticky"
            checked={tableProps.stickyFirstColumn as any}
          />
        </UpsellControl>
      </PanelBody>
      <PanelBody title="PAGINATION" className="wptb-settings">
        <UpsellControl name="global.pagination">
          <SwitchControl
            label="Enable pagination"
            checked={tableProps.paginationEnable as any}
          />
          <RangeControl label="Rows Per Page" value={tableProps.rowsPerPage} />
          <SwitchControl
            label="Dynamic Rows Per Page"
            checked={tableProps.dyanamicPageSize as any}
          />
        </UpsellControl>
      </PanelBody>
      <PanelBody title="SEARCH" className="wptb-settings">
        <UpsellControl name="global.search">
          <SwitchControl
            label="Enable search"
            checked={tableProps.searchEnable as any}
          />
          <SwitchControl
            label="Keep top row as header"
            checked={tableProps.searchKeepHeader as any}
          />
          <SelectControl
            label="Search Bar Position"
            value={tableProps.searchPosition}
            options={[
              { label: "Left", value: "left" },
              { label: "Right", value: "right" },
            ]}
          />
        </UpsellControl>
      </PanelBody>
    </div>
  );
}
