type ToolbarItem = keyof typeof TOOLBAR_ITEMS;
export type ToolbarOptions = ToolbarItem[] | ToolbarItem[][];

const makeToolBar = (items: ToolbarOptions) => {
  const div = document.createElement("div");
  div.className = "ql-toolbar ql-snow";
  div.role = "toolbar";
  for (const item of items) {
    if (Array.isArray(item)) {
      const group = document.createElement("span");
      group.className = "ql-formats";
      for (const subItem of item) {
        group.innerHTML += TOOLBAR_ITEMS[subItem];
      }
      div.appendChild(group);
    } else {
      div.innerHTML += TOOLBAR_ITEMS[item];
    }
  }
  const aligns = div.querySelectorAll(".ql-align");
  if (aligns.length) {
    for (let i = 0; i < aligns.length; i++) {
      aligns[i].addEventListener("pointerdown", (e) => {
        const align = e.currentTarget as HTMLButtonElement;
        align.parentElement.previousElementSibling.innerHTML = align.innerHTML;
        align.parentElement
          .querySelector(".ql-wptb-active")
          ?.classList.remove("ql-wptb-active");
        align.classList.add("ql-wptb-active");
      });
    }
  }

  div.addEventListener("pointerup", (evt) => {
    evt.stopPropagation();
  });

  return div;
};

export default makeToolBar;
