import cls from "classnames";

interface RadioGroupItem {
  label?: string;
  value: string;
  icon: any;
}
interface RadioGroupProps {
  label?: string;
  items: RadioGroupItem[];

  value?: string;
  onChange?: (value: string) => void;
}

export default function RadioGroup({
  label,
  items,
  onChange,
  value,
}: RadioGroupProps) {
  return (
    <div className="wptb-radio-group wptb-control">
      {label && <p className="wptb-radio-group-label">{label}</p>}
      <div className="wptb-radio-group-items">
        {items.map((item) => {
          const checked = item.value === value;
          return (
            <button
              key={item.value}
              className={cls("wptb-radio-group-item", {
                "wptb-checked": checked,
              })}
              onClick={() => onChange?.(item.value)}
            >
              <div className="wptb-radio-group-item-icon">{item.icon}</div>
              {item.label && (
                <div className="wptb-control-tooltip wptb-up">{item.label}</div>
              )}
            </button>
          );
        })}
      </div>
    </div>
  );
}
