import { MouseEvent, useCallback } from "react";
import { useDispatch, useSelector } from "react-redux";
import { RootState } from "@/store";
import FloatingActions from "@/builder/actions/floating";
import { setActiveBlock } from "@/store/editor";
import TimeTravel from "@/store/time-travel";

export const useBlockProps = <T>(blockPath: string[], at: number = -1): T => {
  return useSelector(
    (state: RootState) =>
      state.table[blockPath[0]]?.blocksProps[blockPath.at(at)!]
  );
};
export const useResponsiveProps = <T>(
  blockPath: string[],
  at: number = -1
): T => {
  return useSelector((state: RootState) => {
    const rootProps = state.table[blockPath[0]];
    const id = blockPath.at(at)!;
    if (state.editorSettings.devicePreview === "desktop") {
      return rootProps?.blocksProps[id];
    }
    return rootProps?.blocksPropsResponsive?.[id] ?? rootProps?.blocksProps[id];
  });
};

interface BlockAttributes {
  onClick: (evt: any) => void;
  onMouseMove?: (evt: any) => void;
  onMouseLeave?: (evt: any) => void;
  onMouseEnter?: (evt: any) => void;
}

interface BlockInfo extends Partial<BlockAttributes> {
  name: string;
  blockPath: string[];
  preventFloatingActions?: boolean;
}

interface UseBlockValue<T> {
  props: T;
  isActive: boolean;
  hasActive: boolean;
  updateProps: (newProps: Partial<T>) => void;
  attributes: BlockAttributes;
}

export const useBlock = <T>(attrs: BlockInfo): UseBlockValue<T> => {
  const dispatch = useDispatch();

  const { blockPath, name, preventFloatingActions } = attrs;
  const id = blockPath.at(-1)!;

  const props = useSelector(
    (state: RootState) => state.table[blockPath[0]].blocksProps[id]
  );
  const isActive = useSelector((state: RootState) => {
    const active = state.editorSettings.activeBlock;
    return active?.at(-1) === id && active?.[0] === blockPath[0];
  });

  const hasActive = useSelector((state: RootState) => {
    const active = state.editorSettings.activeBlock;
    if (!active) {
      return false;
    }
    let hasActive = false;
    for (let i = active.length - 2; i > 1; i--) {
      if (active[i] === id) {
        hasActive = true;
        break;
      }
    }
    return hasActive;
  });

  const updateProps = useCallback(
    (newProps: any) => {
      TimeTravel.updateProps(id, newProps);
    },
    [id]
  );

  const onClick = (evt: any) => {
    if (!evt.wptbActivatedEdit) {
      dispatch(setActiveBlock(blockPath));
      evt.wptbActivatedEdit = true;
    }
    attrs?.onClick?.(evt);
  };
  const onMouseMove = preventFloatingActions
    ? attrs?.onMouseMove
    : (evt: MouseEvent) => {
        FloatingActions.onBlockMouseMove(evt, name, id);
        attrs?.onMouseMove?.(evt);
      };
  const onMouseLeave = preventFloatingActions
    ? attrs?.onMouseLeave
    : (evt: any) => {
        FloatingActions.onBlockMouseLeave(evt.currentTarget as HTMLElement);
        attrs?.onMouseLeave?.(evt);
      };

  const onMouseEnter = preventFloatingActions
    ? attrs?.onMouseEnter
    : (evt: any) => {
        FloatingActions.onBlockMouseEnter(evt, name, id);
        attrs?.onMouseEnter?.(evt);
      };

  return {
    props,
    isActive,
    hasActive,
    updateProps,
    attributes: {
      onClick,
      onMouseMove,
      onMouseLeave,
      onMouseEnter,
    },
  };
};
