import { MouseEvent as MouseEventReact } from "react";
import { blockActionsEl, rowColIndicator } from "./helpers";
import Insertion from "./insertion";
import BlockActions from "./block-actions";
import RowColActions from "./row-col-actions";

export default class FloatingActions {
  static init() {
    document.body.appendChild(blockActionsEl);
    document.body.appendChild(rowColIndicator);
  }

  static startInsertion(name: string, el: HTMLElement) {
    Insertion.start(name, el);
  }

  static onBlockMouseEnter(evt: MouseEventReact, name: string, target: string) {
    BlockActions.onBlockEnter(evt, name, target);
  }
  static onBlockMouseMove(evt: MouseEventReact, name: string, target: string) {
    BlockActions.onBlockMove(evt, name, target);
    Insertion.setTarget(evt, target);
  }
  static onBlockMouseLeave(el: HTMLElement) {
    BlockActions.onBlockLeave(el);
  }

  static onCellEnter(evt: MouseEventReact, id: string) {
    RowColActions.onCellEnter(evt.currentTarget as HTMLTableCellElement, id);
    Insertion.onCellEnter(evt.currentTarget as any, id);
    BlockActions.onCellEnter(evt, id);
  }
  static onCellMove(evt: MouseEventReact) {
    BlockActions.onCellMove(evt);
  }

  static onCellLeave() {
    Insertion.onCellLeave();
    BlockActions.onCellLeave();
    RowColActions.onCellLeave();
  }
}
