import type Quill from "quill";
import { Range } from "quill";
import Inline from "quill/blots/inline";

interface LinkValueObj {
  url: string;
  target: string;
  title?: string;
}
type LinkValue = LinkValueObj | string;

export class Link extends Inline {
  static blotName = "link";
  static tagName = "a";

  static create(value: LinkValue) {
    const node = super.create();
    if (typeof value !== "object") {
      value = { url: value, target: "_blank" };
    }
    node.setAttribute("href", value.url);
    node.setAttribute("target", value.target);
    if (value.title) {
      node.setAttribute("title", value.title);
    }
    return node;
  }

  static formats(node: HTMLAnchorElement) {
    const href = node.getAttribute("href");
    const target = node.getAttribute("target");
    const title = node.getAttribute("title");
    return { url: href, target, title };
  }
}

const LINK_POPUP = `
  <div class="wptb-link-popup-title">
    <h2>Update Link</h2>
    <button type="button" class="wptb-link-popup-close">
      <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24">
        <path
          fill="none"
          stroke="currentColor"
          stroke-linecap="round"
          stroke-linejoin="round"
          stroke-width="2"
          d="M17 7L7 17M7 7l10 10"
        />
      </svg>
    </button>
  </div>
  <div class="wptb-link-popup-content">
    <label>URL</label>
    <input type="text" placeholder="i.e. https://wptablebuilder.com" />
    <label>Title</label>
    <input type="text" placeholder="i.e. WPTB" />
    <label>Target</label>
    <select>
      <option value="_self">Same Window</option>
      <option value="_blank">New Window</option>
    </select>
  </div>
  <div class="wptb-link-popup-actions">
    <button type="button" class="wptb-link-popup-update">OK</button>
    <button type="button" class="wptb-link-popup-remove">Remove</button>
  </div>
`;

interface LinkPopup {
  setQuill: (quill: Quill) => void;
  show: (el: HTMLElement, toolbar: HTMLElement) => void;
  remove: () => void;
}

export const makeLinkPopup = (): LinkPopup => {
  let quill: Quill | null = null;
  let selection: Range | null = null;
  let toolbar: HTMLElement | null = null;

  const div = document.createElement("div");
  div.className = "wptb-link-popup";
  div.innerHTML = LINK_POPUP;

  const fields = div.querySelectorAll("input") as NodeListOf<HTMLInputElement>;
  const targetField = div.querySelector("select") as HTMLSelectElement;
  const updateBtn = div.querySelector(
    ".wptb-link-popup-update"
  ) as HTMLButtonElement;
  const removeBtn = div.querySelector(
    ".wptb-link-popup-remove"
  ) as HTMLButtonElement;
  const cancelBtn = div.querySelector(
    ".wptb-link-popup-close"
  ) as HTMLButtonElement;

  const cleanHandler = () => {
    div.remove();
    document.removeEventListener("mousedown", cleanHandler);
  };

  updateBtn.addEventListener("click", () => {
    if (!quill || !selection) {
      return;
    }
    const value = {
      url: fields[0].value,
      title: fields[1].value,
      target: targetField.value,
    };
    quill.setSelection(selection, "silent");
    quill.format("link", value, "user");
    document.body.appendChild(toolbar);
    cleanHandler();
  });

  removeBtn.addEventListener("click", () => {
    cleanHandler();

    if (!quill || !selection) {
      return;
    }
    quill.setSelection(selection, "silent");
    document.body.appendChild(toolbar);

    const { index } = selection;

    const formats = quill.getFormat(selection);

    if (!formats.link) {
      return;
    }
    const [leaf] = quill.getLeaf(index);

    if (!leaf || !leaf.domNode) {
      return;
    }

    const parentEl = leaf.domNode.parentElement;

    const linkElement =
      parentEl.tagName === "A" ? parentEl : parentEl.closest("a");
    if (!linkElement) {
      return;
    }
    let start = index;
    let end = index;

    while (start > 0) {
      const [prevLeaf] = quill.getLeaf(start - 1);
      const parentEl = prevLeaf?.domNode.parentElement;
      const el = parentEl?.tagName === "A" ? parentEl : parentEl?.closest("a");
      if (el) {
        start--;
      } else {
        break;
      }
    }

    const length = quill.getLength();
    while (end < length) {
      const [nextLeaf] = quill.getLeaf(end);
      const parentEl = nextLeaf?.domNode.parentElement;
      const el = parentEl?.tagName === "A" ? parentEl : parentEl?.closest("a");
      if (el) {
        end++;
      } else {
        break;
      }
    }
    const len = end - start;
    quill.setSelection(start - 1, len, "silent");
    quill.format("link", null, "user");
    quill.setSelection(selection, "silent");
  });

  cancelBtn.addEventListener("click", () => {
    cleanHandler();
    if (!quill || !selection) {
      return;
    }
    quill.setSelection(selection, "silent");
    document.body.appendChild(toolbar);
  });

  div.addEventListener("pointerup", (evt) => {
    evt.stopPropagation();
  });
  div.addEventListener("mousedown", (evt) => {
    evt.stopPropagation();
  });

  return {
    setQuill(quillInstance: Quill) {
      quill = quillInstance;
    },
    show(el: HTMLElement, toolbarEl: HTMLElement) {
      selection = quill?.getSelection();

      toolbar = toolbarEl;
      const link = quill?.getFormat(selection)?.link as LinkValueObj | null;

      if (link) {
        fields[0].value = link.url;
        fields[1].value = link.title || "";
        targetField.value = link.target;
      } else if (!selection?.length) {
        return;
      } else {
        fields[0].value = "";
        fields[1].value = "";
        targetField.value = "_blank";
      }

      document.body.appendChild(div);
      const box = el.getBoundingClientRect();
      const pBox = div.getBoundingClientRect();
      div.style.top = `${box.top + box.height}px`;
      div.style.left = `${box.left - (pBox.width - box.width) / 2}px`;
      document.addEventListener("mousedown", cleanHandler);
    },
    remove: cleanHandler,
  };
};
