import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { TablesState, TableState } from "@wptb/dev/types";
import { internalSetResponsive } from "./responsive";
import {
  internalAddColumn,
  internalAddRow,
  internalRemoveCol,
  internalRemoveRow,
  internalSortColumns,
  internalSortRows,
} from "./row-col";
import { internalMergeCells, internalSplitCell } from "./merging";
import {
  internalAddBlock,
  internalDuplicateBlock,
  internalmoveBlockInto,
  internalRemoveBlock,
} from "./block";

export const TABLE_ID = "table";

const initialState: Record<string, TableState> = {};

export const tableSlice = createSlice({
  name: "table",
  initialState,
  reducers: {
    setTable: (
      state,
      action: PayloadAction<{
        id: string;
        table: TableState;
      }>
    ) => {
      const { id, table } = action.payload;
      state[id] = table;
    },

    changeId: (
      state,
      action: PayloadAction<{
        oldId: string;
        newId: string;
      }>
    ) => {
      const { oldId, newId } = action.payload;
      state[newId] = state[oldId];
      state[newId].id = newId;
      delete state[oldId];
    },

    setTableInfo: (
      state,
      action: PayloadAction<{
        id: string;
        title?: string;
      }>
    ) => {
      const { id, title } = action.payload;
      state[id].title = title;
    },

    setResponsive: internalSetResponsive,

    addBlock: internalAddBlock,
    removeBlock: internalRemoveBlock,
    duplicateBlock: internalDuplicateBlock,
    moveBlockInto: internalmoveBlockInto,

    addColumn: internalAddColumn,
    addRow: internalAddRow,
    removeCol: internalRemoveCol,
    removeRow: internalRemoveRow,
    mergeCells: internalMergeCells,
    splitCell: internalSplitCell,

    sortRows: internalSortRows,
    sortColumns: internalSortColumns,

    customReducer: (
      state,
      action: PayloadAction<(state: TablesState) => void>
    ) => {
      action.payload(state);
    },
  },
});

export const {
  setTable,
  changeId,
  setTableInfo,

  setResponsive,
  addBlock,
  removeBlock,
  duplicateBlock,
  moveBlockInto,
  mergeCells,
  splitCell,
  removeCol,
  removeRow,
  addColumn,
  addRow,

  sortRows,
  sortColumns,

  customReducer,
} = tableSlice.actions;

export const tableReducer = tableSlice.actions;
