import store from "@/store";
import { setSettings } from "@/store/editor";
import { MergeBoundary, TableStateDevice } from "@wptb/dev/types";
import { CellProps } from "@wptb/dev/types";
import { PointerEvent } from "react";
import { mergeCells, splitCell } from "@/store/table";
import TimeTravel from "@/store/time-travel";
import BuilderCtx from "@/builder/ctx";

const adjustMergeBoundary = (
  table: TableStateDevice,
  blockProps: any,
  boundary: MergeBoundary
) => {
  let selectedBlocks: Record<string, boolean> = {};
  let selection: string[] = [];
  loop1: for (let r = 0; r < table.blockOrder.length; r++) {
    const rowId = table.blockOrder[r];
    const cells = table.innerBlocks[rowId];
    for (let c = 0; c < cells.length; c++) {
      const cellId = cells[c];
      const { col, rowspan, colspan } = blockProps[cellId] as CellProps;

      const colEnd = col + colspan - 1;
      const rowEnd = r + rowspan - 1;

      const colStartsInside = col >= boundary.left && col <= boundary.right;
      const colEndsInside = colEnd >= boundary.left && colEnd <= boundary.right;
      const rowStartsInside = r >= boundary.top && r <= boundary.bottom;
      const rowEndsInside = rowEnd >= boundary.top && rowEnd <= boundary.bottom;

      if (
        !(colStartsInside || colEndsInside) ||
        !(rowStartsInside || rowEndsInside)
      ) {
        continue;
      }

      let restart = false;

      if (!colStartsInside && colEndsInside) {
        boundary.left = col;
        restart = true;
      } else if (colStartsInside && !colEndsInside) {
        boundary.right = colEnd;
        restart = true;
      }

      if (!rowStartsInside && rowEndsInside) {
        boundary.top = r;
        restart = true;
      } else if (rowStartsInside && !rowEndsInside) {
        boundary.bottom = rowEnd;
        restart = true;
      }

      if (restart) {
        selectedBlocks = {};
        selection = [];
        r = -1;
        continue loop1;
      }

      selectedBlocks[cellId] = true;

      selection.push(cellId);
    }
  }
  return [selectedBlocks, selection] as [Record<string, boolean>, string[]];
};

export class CellMerging {
  static selection: string[];
  static mergeBoundary?: MergeBoundary;

  static markCells(evt: PointerEvent, cell2: string) {
    if (!evt.shiftKey) {
      return;
    }
    const {
      table: tables,
      editorSettings: { activeBlock },
    } = store.getState();

    const { desktop: table, blocksProps } = tables[BuilderCtx.TABLE_ID!];

    if (!activeBlock?.length || activeBlock[2] === cell2) {
      return;
    }

    evt.preventDefault();
    evt.stopPropagation();

    const cell1 = activeBlock[2];
    const cell1Props: CellProps = blocksProps[cell1];
    const cell2Props: CellProps = blocksProps[cell2];

    const mergeBoundary: MergeBoundary = {
      top: Math.min(cell1Props.row, cell2Props.row),
      left: Math.min(cell1Props.col, cell2Props.col),
      bottom: Math.max(cell1Props.row, cell2Props.row),
      right: Math.max(cell1Props.col, cell2Props.col),
    };
    CellMerging.selection = [];
    const [cells, selection] = adjustMergeBoundary(
      table,
      blocksProps,
      mergeBoundary
    );

    CellMerging.mergeBoundary = mergeBoundary;
    CellMerging.selection = selection;
    store.dispatch(
      setSettings({
        selectedCells: cells,
      })
    );
  }

  static unMarkCells() {
    if (!CellMerging.mergeBoundary) {
      return;
    }
    CellMerging.mergeBoundary = undefined;
    store.dispatch(
      setSettings({
        selectedCells: undefined,
      })
    );
  }

  static doMerging() {
    if (!CellMerging.mergeBoundary) {
      return;
    }

    const device = store.getState().editorSettings.devicePreview;

    store.dispatch(
      setSettings({
        activeBlock: undefined,
        selectedCells: undefined,
      })
    );

    TimeTravel.captured(mergeCells, {
      cells: CellMerging.selection,
      boundary: CellMerging.mergeBoundary,
      device,
    });
  }
  static splitCell() {
    const { activeBlock, devicePreview } = store.getState().editorSettings;
    const cellId = activeBlock?.[2];
    if (!cellId) {
      return;
    }
    TimeTravel.captured(splitCell, {
      cellId,
      device: devicePreview,
    });
  }
}
