import store from "@/store";
import { BlockState } from "@wptb/dev/types";
import { BLOCK_MAP } from "@/block_registry";

export class FloatingCtx {
  static isInserting: boolean = false;
  static isMoving: boolean = false;
  static isRowColMoving: boolean = false;
}

export const getTargetBlock = (
  blockId: string,
  tableId: string
): BlockState | null => {
  return store.getState().table[tableId].innerBlocks[blockId];
};

export const canBeInsertedBeside = (
  tableId: string,
  childName: string,
  siblingPath: string
): boolean => {
  const parentName = getTargetBlock(siblingPath, tableId)?.name;
  return parentName ? canBeInsertedInto(childName, parentName) : false;
};

export const canBeInsertedInto = (
  childName: string,
  parentName: string
): boolean => {
  const parents = BLOCK_MAP[childName]?.supportedParents;
  if (!parents) {
    return parentName === "cell";
  }
  if (!parents.includes(parentName)) {
    return false;
  }
  const children = BLOCK_MAP[parentName]?.supportedChildren;
  if (!children || !children.includes(childName)) {
    return false;
  }
  return true;
};

export const moveIndicator = (target: HTMLElement, y: number): boolean => {
  const box = target.getBoundingClientRect();
  const half = box.top + box.height / 2;
  targetIndicator.style.width = box.width + "px";
  targetIndicator.style.left = box.left + "px";
  if (y > half) {
    targetIndicator.style.top = box.top + box.height + "px";
    targetIndicator.style.bottom = "";
    return true;
  } else {
    targetIndicator.style.top = "";
    targetIndicator.style.bottom = window.innerHeight - box.top + "px";
    return false;
  }
};

export const moveIndicatorTd = (target: HTMLElement, y: number): boolean => {
  const box = target.getBoundingClientRect();
  const half = box.top + box.height / 2;
  targetIndicator.style.width = box.width + "px";
  targetIndicator.style.left = box.left + "px";
  if (y > half) {
    targetIndicator.style.top = "";
    targetIndicator.style.bottom = window.innerHeight - box.bottom + "px";
    return true;
  } else {
    targetIndicator.style.top = box.top + "px";
    targetIndicator.style.bottom = "";
    return false;
  }
};

