import { MouseEvent as MouseEventReact } from "react";
import { FloatingCtx, moveIndicator, targetIndicator } from "./helpers";
import { addBlock } from "@/store/table";
import TimeTravel from "@/store/time-travel";

export default class Insertion {
  static newEl?: HTMLElement;
  static newName?: string;
  static newTarget?: string;
  static siblingId?: string;
  static positionAfter?: boolean;

  static start(name: string, el: HTMLElement) {
    Insertion.newName = name;
    Insertion.newEl = el.cloneNode(true) as HTMLElement;
    Insertion.newEl.classList.add("wptb-new-dragging");
    document.body.appendChild(Insertion.newEl);

    targetIndicator.innerHTML = "INSERT HERE";

    const box = el.getBoundingClientRect();
    Insertion.newEl.style.height = box.height + "px";
    Insertion.newEl.style.width = box.width + "px";
    Insertion.newEl.style.top = box.top + "px";
    Insertion.newEl.style.left = box.left + "px";

    FloatingCtx.isInserting = true;

    document.addEventListener("mousemove", Insertion.onMove);
    document.addEventListener("mouseup", Insertion.onEnd);
  }

  static onMove(evt: MouseEvent) {
    if (!FloatingCtx.isInserting) {
      return;
    }
    Insertion.newEl!.style.top = evt.clientY + "px";
    Insertion.newEl!.style.left = evt.clientX + "px";
  }

  static onEnd() {
    document.removeEventListener("mousemove", Insertion.onMove);
    document.removeEventListener("mouseup", Insertion.onEnd);

    if (!FloatingCtx.isInserting) {
      return;
    }

    FloatingCtx.isInserting = false;

    Insertion.newEl?.remove();
    targetIndicator.remove();

    if (Insertion.newName && Insertion.newTarget) {
      TimeTravel.captured(
        addBlock,
        {
          parentId: Insertion.newTarget!,
          blockName: Insertion.newName!,
          siblingId: Insertion.siblingId,
          after: Insertion.positionAfter,
        },
        false
      );
    }

    Insertion.newEl = undefined;
    Insertion.newName = undefined;
    Insertion.newTarget = undefined;
  }

  static async setTarget(evt: MouseEventReact, target: string) {
    if (!FloatingCtx.isInserting) {
      return;
    }

    Insertion.siblingId = target;

    Insertion.positionAfter = moveIndicator(
      evt.currentTarget as any,
      evt.clientY
    );
  }

  static onCellLeave() {
    if (!FloatingCtx.isInserting) {
      return;
    }
    targetIndicator.remove();
    Insertion.newTarget = undefined;
    Insertion.siblingId = undefined;
  }

  static onCellEnter(el: HTMLElement, blockId: string) {
    if (!FloatingCtx.isInserting) {
      return;
    }
    Insertion.newTarget = blockId;
    document.body.appendChild(targetIndicator);
    const box = el.getBoundingClientRect();
    targetIndicator.style.top = box.top + "px";
    targetIndicator.style.left = box.left + "px";
  }
}
