interface CheckBoxControlProps {
  label?: string;
  checked?: boolean;
  onChange?: (value: boolean) => void;
}

export default function CheckBoxControl({
  label,
  checked,
  onChange,
}: CheckBoxControlProps) {
  return (
    <label className="wptb-checkbox-control wptb-control">
      <input
        type="checkbox"
        checked={checked}
        className="btn"
        onChange={() => onChange?.(!checked)}
      />
      {label && <p className="wptb-checkbox-control-label">{label}</p>}
    </label>
  );
}
