import { v4 as uuidv4 } from "uuid";
import { CellProps, ColumnProps, RowProps, TableProps } from "@wptb/dev/types";
import { BLOCK_MAP } from "@/block_registry";
import { BlockState, TableState } from "@wptb/dev/types";
import {
  getInnerBorderRadius,
  hexToRgbA,
  parseBorder,
  parseInnerBorder,
  TableDirectives,
} from "@wptb/utils/parser";
import { MakeOptional } from "@wptb/dev/types";
import { TABLE_ID } from "@/store/table";
import { Border, BorderSide } from "@wptb/dev/types";
import { defaultTableProps } from "@/builder/blocks/default-props";

const theDiv = document.createElement("div");

const isSingleSideBorder = (
  border?: Border | BorderSide
): border is BorderSide => {
  return border ? !(border as Border).top : false;
};

const parseCellProps = (
  td: HTMLTableCellElement,
  innerBorder?: Border | BorderSide
): MakeOptional<CellProps, "row" | "col"> => {
  const tData = td.dataset;
  const borderStyle = parseBorder(td);
  let border: any;
  if (
    !isSingleSideBorder(borderStyle) ||
    !isSingleSideBorder(innerBorder) ||
    borderStyle.color !== innerBorder.color ||
    borderStyle.width !== innerBorder.width
  ) {
    border = borderStyle;
  }

  return {
    background: td.style.backgroundColor,
    border,
    vAlign: tData.wptbCellVerticalAlignment || "center",
    isEmpty: td.classList.contains("wptb-empty"),
    sortedVertical: tData.sortedVertical == "true",
    sortedHorizontal: tData.sortedHorizontal === "true",
    colspan: td.colSpan || 1,
    rowspan: td.rowSpan || 1,
  };
};

const parseRowProps = (
  row: HTMLTableRowElement,
  index: number,
  tableProps: TableProps
): RowProps => {
  const hMatch = row.className.match(/wptb-row-highlighted-([0-9]+)/)?.[1];
  let background: string | undefined = hexToRgbA(row.style.backgroundColor || row.style.background);

  if (
    background &&
    ((index === 0 && tableProps.headerBg === background) ||
      (index % 2 === 0 && tableProps.oddRowBg === background) ||
      (index % 2 === 1 && tableProps.evenRowBg === background))
  ) {
    background = undefined;
  }
  return {
    highlight: !!hMatch,
    highlightScale: parseInt(hMatch || "100"),
    background,
  };
};

const parseResponsive = (table: HTMLTableElement) => {
  let old: any = null;

  try {
    old = JSON.parse(atob(table.dataset.wptbResponsiveDirectives || "null"));
  } catch (_) {
    /* empty */
  }

  const autoMode = old?.modeOptions?.auto;

  const responsive: TableProps["responsive"] = {
    widthTarget: (old?.relativeWidth as any) || "window",
    enable: old?.responsiveEnabled || false,
    mobile: {
      disable: !!autoMode?.disabled?.mobile,
      maxWidth: `${old?.breakpoints?.mobile?.width || 300}px`,
      topRowAsHeader: autoMode?.topRowAsHeader?.mobile || true,
      staticTopRow: autoMode?.staticTopRow?.mobile || false,
      stackDirection: (autoMode?.cellStackDirection?.mobile as any) || "row",
      itemsPerHead: autoMode?.cellsPerRow?.mobile || 2,
    },
    tablet: {
      disable: !!autoMode?.disabled?.tablet,
      maxWidth: `${old?.breakpoints?.tablet?.width || 700}px`,
      topRowAsHeader: autoMode?.topRowAsHeader?.tablet || true,
      staticTopRow: autoMode?.staticTopRow?.tablet || false,
      stackDirection: (autoMode?.cellStackDirection?.tablet as any) || "row",
      itemsPerHead: autoMode?.cellsPerRow?.tablet || 2,
    },
  };

  return responsive;
};

const parseTableProps = (table: HTMLTableElement): TableProps => {
  const data = table.dataset;
  const tBodyData = table.querySelector("tbody")?.dataset || {};
  const tableStyle = table.style;

  const firstTd = table.rows[0].cells[0];

  const generalTd = (() => {
    if (table.rows.length > 1) {
      if (table.rows[1].cells.length > 1) {
        return table.rows[1].cells[1];
      }
      return table.rows[1].cells[0] || firstTd;
    }
    return firstTd;
  })();
  const tableBorder = parseBorder(table);

  let directives: TableDirectives = {};
  try {
    directives = JSON.parse(atob(data.wptbTableDirectives || "{}"));
  } catch (_) {
    /* empty */
  }

  const innerBorder = parseInnerBorder(table, tableBorder, directives);

  return {
    cols: parseInt(data.tableColumns || "1"),
    rows: table.rows.length,
    alignment: (data.wptbTableAlignment as any) || defaultTableProps.alignment,
    enableMaxWidth: data.wptbApplyTableContainerMaxWidth ? true : false,
    maxWidth: data.wptbTableContainerMaxWidth
      ? data.wptbTableContainerMaxWidth + "px"
      : undefined,
    sortHorizontal: {
      enable: data.wptbSortableTableHorizontal == "1",
    },
    sortVertical: {
      enable: data.wptbSortableTableVertical == "1",
    },
    colSpacing: (data.borderSpacingColumns || "3") + "px",
    rowSpacing: (data.borderSpacingRows || "3") + "px",
    cellMinWidth: (data.wptbTdWidthAuto || "120") + "px",
    cellPadding: generalTd?.style.padding || defaultTableProps.cellPadding,

    // Global Font Style
    fontColor: hexToRgbA(tBodyData.globalFontColor),
    linkColor: hexToRgbA(tBodyData.globalLinkColor),
    fontSize: parseInt(tBodyData.globalFontSize || "15") + "px",

    // Border
    tableBorder,
    enableInnerBorder: !!innerBorder,
    innerBorder,
    headerInnerBorder: firstTd ? firstTd.style.borderStyle != "none" : false,
    innerBorderRadius: getInnerBorderRadius(directives), // PRO
    separateRowCol: tableStyle.borderCollapse == "separate", // PRO
    rowBorderOnly: directives.innerBorders?.active == "row", // PRO
    columnBorderOnly: directives.innerBorders?.active == "column", // PRO

    // Other
    scrollX: !!data.wptbHorizontalScrollStatus,
    disableThemeStyles: data.wptbDisableThemeStyles === "1",
    extraStyles: atob(data.wptbExtraStyles || ""),
    stickyFirstColumn: data.wptbFirstColumnSticky === "true", // PRO
    stickyTopRow: table.querySelector("tr")?.dataset?.wptbStickyRow === "true", // PRO
    paginationEnable: data.wptbPaginationEnable === "true", // PRO
    paginationTopRowAsHeader: data.wptbProPaginationTopRowHeader === "true", // PRO
    rowsPerPage: parseInt(data.wptbRowsPerPage || "10"), // PRO
    dyanamicPageSize: data.wptbRowsChangeable === "true", // PRO

    searchEnable: data.wptbSearchEnable === "true", // PRO
    searchKeepHeader: data.wptbProSearchTopRowHeader === "true", // PRO
    searchPosition: data.wptbSearchbarPosition || "left", // PRO
    role: table.getAttribute("role") || "table",

    // Colors
    headerBg: hexToRgbA(data.wptbHeaderBackgroundColor),
    evenRowBg: hexToRgbA(data.wptbEvenRowBackgroundColor),
    oddRowBg: hexToRgbA(data.wptbOddRowBackgroundColor),

    hoverHeaderBg: data.wptbHeaderHoverBackgroundColor,
    hoverEvenRowBg: data.wptbEvenRowHoverBackgroundColor,
    hoverOddRowBg: data.wptbOddRowHoverBackgroundColor,

    responsive: parseResponsive(table),
  };
};

export default function parseTable(table: string): TableState | null {
  theDiv.innerHTML = table;
  const el = theDiv.querySelector("table")!;
  if (!el) {
    return null;
  }

  const innerBlocks: Record<string, BlockState> = {};
  const allProps: Record<string, Record<string, any>> = {};

  const takenIndexes: Record<number, { from: number; to: number }[]> = {};

  const regedBlocks = Object.values(BLOCK_MAP);

  const rows: Record<string, string[]> = {};
  const rowOrder: string[] = [];

  let maxCol = 0;

  const tableProps = parseTableProps(el);
  allProps[TABLE_ID] = tableProps;

  const columns: Record<number, ColumnProps> = {};

  for (let i = 0; i < el.rows.length; i++) {
    const rowId = uuidv4();
    rowOrder.push(rowId);
    const row = el.rows[i];
    const rowProps = parseRowProps(row, i, tableProps);

    const cellOrder: string[] = [];
    let col = 0;
    for (let j = 0; j < row.cells.length; j++) {
      const cell = row.cells[j];
      const blockOrder: string[] = [];
      const cellId = uuidv4();
      cellOrder.push(cellId);

      if (cell.style.height) {
        rowProps.height = cell.style.height;
      }
      if (cell.style.width) {
        columns[col] = {
          ...columns[col],
          width: cell.style.width,
        };
      }
      if (cell.classList.contains("wptb-hide-on-mobile")) {
        columns[col] = {
          ...columns[col],
          hideOnMobile: true,
        };
      }

      const hMatch = cell.className.match(/wptb-col-highlighted-([0-9]+)/)?.[1];
      if (hMatch) {
        columns[col] = {
          ...columns[col],
          highlight: true,
          highlightScale: parseInt(hMatch),
        };
      }

      Array.from(cell.children).forEach((child: any) => {
        for (const block of regedBlocks) {
          if (block.isBlock?.(child) && block.parse) {
            const [blockId, pb] = block.parse(
              child,
              allProps,
              innerBlocks,
              tableProps
            );
            blockOrder.push(blockId);
            pb.parentId = cellId;
            innerBlocks[blockId] = pb as BlockState;
            break;
          }
        }
      });

      innerBlocks[cellId] = {
        name: "cell",
        blockOrder,
        parentId: rowId,
      };

      const takenCols = takenIndexes[i];
      if (takenCols) {
        for (const { from, to } of takenCols) {
          if (from <= col && col <= to) {
            col = to + 1;
          }
        }
      }

      const colSpan = cell.colSpan || 1;
      const cellProps = parseCellProps(cell, tableProps.innerBorder);

      Object.assign(cellProps, {
        col,
        row: i,
      });
      allProps[cellId] = cellProps;
      const rowSpan = cell.rowSpan || 1;
      for (let k = 0; k < rowSpan; k++) {
        if (!takenIndexes[i + k]) {
          takenIndexes[i + k] = [];
        }
        takenIndexes[i + k].push({
          from: col,
          to: col + colSpan - 1,
        });
      }
      col += colSpan;
    }

    maxCol = Math.max(maxCol, col);
    rows[rowId] = cellOrder;
    allProps[rowId] = rowProps;
  }

  tableProps.cols = maxCol;
  tableProps.columns = columns;
  const fVal: TableState = {
    desktop: {
      blockOrder: rowOrder,
      innerBlocks: rows,
    },
    tablet: {
      blockOrder: rowOrder,
      innerBlocks: rows,
    },
    mobile: {
      blockOrder: rowOrder,
      innerBlocks: rows,
    },
    blocksProps: allProps,
    blocksPropsResponsive: {},
    innerBlocks,
  };
  return fVal;
}
