import { useSelector } from "react-redux";
import Row from "./Row";
import { RootState } from "../../store";
import { ResponsiveDeviceName, TableProps } from "@wptb/dev/types";
import { CSSProperties, useEffect, useMemo, useRef } from "react";
import { TABLE_ID } from "@/store/table";
import {
  calcTableMinWidth,
  getTableAttrs,
  getTableStyle,
  getTBodyAttrs,
} from "./get-attrs";
import { CellMerging } from "../actions/panel/merging";

interface TableRCProps {
  id: string;
}

const Table = ({ id }: TableRCProps) => {
  const tblEl = useRef<HTMLTableElement>(null);
  const table = useSelector((state: RootState) => {
    const device = state.editorSettings.devicePreview;
    return state.table[id][device];
  });

  const previewDevice = useSelector(
    (state: RootState) => state.editorSettings.devicePreview
  );

  const responsiveMode = useSelector((state: RootState) => {
    const device: ResponsiveDeviceName = state.editorSettings.devicePreview;
    if (device === "desktop") {
      return false;
    }
    const reponsive: TableProps["responsive"] =
      state.table[id]?.blocksProps[TABLE_ID]?.responsive;
    const deviceOpts = reponsive?.[device];
    return (
      reponsive?.enable &&
      deviceOpts?.disable !== true &&
      deviceOpts?.stackDirection
    );
  });

  const props = useSelector((state: RootState) => {
    return state.table[id].blocksProps[TABLE_ID] as TableProps;
  });

  const { tblStyle, tbodyAttrs, tableAttrs, containerStyle } = useMemo(() => {
    if (!props) {
      return {
        tableAttrs: {},
        tblStyle: {},
        tbodyAttrs: {},
      };
    }
    const calced = calcTableMinWidth(props);
    const containerStyle: CSSProperties = {};
    if (!props?.alignment || props.alignment === "center") {
      containerStyle.marginLeft = "auto";
      containerStyle.marginRight = "auto";
    } else if (props.alignment === "right") {
      containerStyle.marginLeft = "auto";
    }

    containerStyle.maxWidth = `${Math.max(700, calced.total + 50)}px`;

    if (props?.enableMaxWidth) {
      containerStyle.maxWidth = props?.maxWidth;
      if (previewDevice === "desktop") {
        containerStyle.width = props?.maxWidth;
      }
    }

    return {
      tableAttrs: getTableAttrs(props),
      tblStyle: getTableStyle(
        props,
        responsiveMode ? undefined : calced.minWidth
      ),
      tbodyAttrs: getTBodyAttrs(props),
      containerStyle,
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [props, table.blockOrder, responsiveMode, previewDevice]);

  useEffect(() => {
    if (!tblEl.current) {
      return;
    }
    tblEl.current.addEventListener("pointerup", (e: any) => {
      if (!["TD", "TR", "TABLE"].includes(e.target.tagName)) {
        e.stopPropagation();
      }
    });
  }, []);

  return (
    <div
      className="wptb-builder-table-container"
      style={containerStyle}
      onPointerDownCapture={CellMerging.unMarkCells}
    >
      <table {...tableAttrs} style={tblStyle} ref={tblEl}>
        <tbody {...tbodyAttrs} key={table.blockOrder.length}>
          {table.blockOrder.map((rowId, index) => (
            <Row
              key={`${rowId}-${index}`}
              tableId={id}
              blockOrder={table.innerBlocks[rowId]}
              id={rowId}
              tableProps={props}
              rowIndex={index}
            />
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default Table;
