import { KeyboardEvent } from "react";

interface RangeControlProps {
  label?: string;
  min?: number;
  max?: number;
  step?: number;
  value?: number;
  onChange?: (value: number) => void;
  allowOutOfRange?: boolean;
}

export default function RangeControl({
  label,
  min = 0,
  max = 100,
  step = 1,
  value = 0,
  onChange,
  allowOutOfRange,
}: RangeControlProps) {
  if (allowOutOfRange) {
    min = Math.min(min, value);
    max = Math.max(max, value);
  }

  const localOnChange = (value: number) => {
    if (allowOutOfRange) {
      onChange?.(value);
    } else if (value < min) {
      onChange?.(min);
    } else if (value > max) {
      onChange?.(max);
    } else {
      onChange?.(value);
    }
  };

  const keyDown = (e: KeyboardEvent<HTMLInputElement>) => {
    if (e.key === "ArrowUp") {
      e.preventDefault();
      oneUp();
    } else if (e.key === "ArrowDown") {
      e.preventDefault();
      oneDown();
    }
  };
  const oneUp = () => {
    onChange?.(value + step);
  };

  const oneDown = () => {
    onChange?.(value - step);
  };

  return (
    <div className="wptb-range-control wptb-control">
      {label && <label>{label}</label>}
      <div className="wptb-range-control-input-area">
        <input
          className="wptb-range-slider"
          type="range"
          min={min}
          max={max}
          step={step}
          value={value || 0}
          onInput={(e: any) => localOnChange(+e.target.value || 0)}
        />
        <div className="wptb-range-control-value">
          <input
            type="number"
            value={value}
            onKeyDown={keyDown}
            onInput={(e: any) => localOnChange(+e.target.value || 0)}
          />
          <div>
            <button type="button" onClick={oneUp} />
            <button type="button" onClick={oneDown} />
          </div>
        </div>
      </div>
    </div>
  );
}
