import ControlLabel from "../control-label";

interface SelectControlProps {
  label?: string;
  value?: string;
  onChange?: (value: string) => void;
  onReset?: () => void;
  options: {
    label: string;
    value: string;
  }[];
}
export default function SelectControl({
  label,
  value,
  onChange,
  onReset,
  options,
}: SelectControlProps) {
  return (
    <div className="wptb-select-control wptb-control">
      <ControlLabel
        label={label}
        onReset={onReset}
        className="wptb-select-control-label"
      />
      <select value={value || ""} onChange={(e) => onChange?.(e.target.value)}>
        {options.map((option) => (
          <option key={option.value} value={option.value}>
            {option.label}
          </option>
        ))}
      </select>
    </div>
  );
}
