import type { Delta } from "quill/core";
import {
  createElement,
  forwardRef,
  HTMLAttributes,
  useEffect,
  useImperativeHandle,
  useRef,
} from "react";
import { initQuill, QuillRef, RichTextQuillProps } from "./quill";

interface RichTextProps {
  tagName?: keyof JSX.IntrinsicElements;
  onChange?: (value: string) => void;
  value?: string | Delta;
  preventNewLine?: boolean;
  toolbar?: any;
  props?: HTMLAttributes<HTMLDivElement>;
  placeholder?: string;
  onAlignmentChange?: (alignment: string) => void;
  onSplit?: (beforeCursorHtml: Delta, afterCursorHtml: Delta) => void;
}

export interface RichTextRef {
  focus: (position?: number) => void;
  length: () => number;
}

const RichText = forwardRef<RichTextRef, RichTextProps>(
  (
    {
      tagName: Wrapper = "div",
      onChange,
      preventNewLine,
      value = "",
      toolbar,
      props,
      placeholder,
      onAlignmentChange,
      onSplit,
    }: RichTextProps,
    ref
  ) => {
    const elRef = useRef<HTMLElement>();
    const quillRef = useRef<QuillRef>();
    const quillProps = useRef<RichTextQuillProps>({
      onChange,
      onAlignmentChange,
      onSplit,
      preventNewLine,
      toolbar,
      placeholder,
    });

    useImperativeHandle(ref, () => ({
      focus: (position?: number) => {
        quillRef.current?.quill.focus();
        quillRef.current?.quill.setSelection(position || 0);
      },
      length: () => quillRef.current?.quill.getLength() || 0,
    }));

    useEffect(() => {
      const el = elRef.current;
      if (!el || el.dataset.wptbHasQuill) {
        if (quillRef.current?.isFocused) {
          document.body.appendChild(quillRef.current.toolbar);
        }
        return () => quillRef.current?.cleanup();
      }
      el.dataset.wptbHasQuill = "true";
      quillRef.current = initQuill(el, quillProps.current);
      return () => quillRef.current?.cleanup();
      /**
       * We are using elRef.current as dependency not because
       * it is used in the useEffect here, but
       * BECAUSE THIS EFFECT SHOULD RE-RUN ON REF CHANGE
       */
      // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [elRef.current]);

    useEffect(() => {
      quillProps.current.onChange = onChange;
      quillProps.current.onAlignmentChange = onAlignmentChange;
      quillProps.current.onSplit = onSplit;
      quillProps.current.preventNewLine = preventNewLine;
    }, [onChange, onAlignmentChange, onSplit, preventNewLine]);

    useEffect(() => {
      quillRef.current?.setValue(value);
    }, [value]);

    return createElement(Wrapper, { ...props, ref: elRef });
  }
);
export default RichText;
