import { Fragment, useEffect, useMemo, useRef } from "react";
import { BLOCK_MAP } from "@/block_registry";
import { useSelector } from "react-redux";
import { RootState } from "@/store";
import CellControl from "./core-controls/CellControl";
import { CellProps, RowProps } from "@wptb/dev/types";
import TimeTravel from "@/store/time-travel";
import { EnhancementGroup } from "@/builder/components/enhanced-control";
import RowColControl from "./core-controls/RowColControl";

interface Props {
  id: string;
  blockPath: string[];
}

const BlockControl = ({ id, blockPath }: Props) => {
  const elRef = useRef<HTMLDivElement>(null);

  const table = useSelector((state: RootState) => state.table[id]);

  const { row, cell, block, others } = useMemo(() => {
    const others: {
      name: string;
      props: Record<string, any>;
      blockId: string;
      blockPath: string[];
    }[] = [];

    const { blocksProps, innerBlocks } = table;

    const rowId = blockPath[1];
    const rowProps = blocksProps[rowId];
    const cellId = blockPath[2];
    const blockId = blockPath[3];
    const cellBlock = innerBlocks[cellId];
    if (!cellBlock) {
      return { others };
    }

    const row = !rowProps
      ? null
      : {
          name: "row",
          props: rowProps as RowProps,
          blockId: rowId,
          blockPath: blockPath.slice(0, 2),
        };

    const cell = {
      name: cellBlock.name,
      props: blocksProps[cellId] as CellProps,
      blockId: cellId,
      blockPath: blockPath.slice(0, 3),
    };

    let block;
    if (blockId) {
      block = {
        name: innerBlocks[blockId].name,
        props: blocksProps[blockId],
        blockId: blockId,
        blockPath: blockPath.slice(0, 4),
      };
    }

    for (let i = 4; i < blockPath.length; i++) {
      const bId = blockPath[i];
      others.push({
        name: innerBlocks[bId].name,
        props: blocksProps[bId],
        blockId: bId,
        blockPath: blockPath.slice(0, i + 1),
      });
    }

    return {
      row,
      cell,
      block,
      others,
    };
  }, [table, blockPath]);

  useEffect(() => {
    const el = elRef.current;
    if (!el) {
      return;
    }
    const stopUnactive = (evt: any) => {
      evt.stopPropagation();
    };

    el.addEventListener("pointerup", stopUnactive);

    return () => {
      el?.removeEventListener("pointerup", stopUnactive);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [elRef.current]);

  const ControlRC = block && BLOCK_MAP[block.name]?.Control;

  const updateBlockProps = (p: any) =>
    TimeTravel.updateProps(block!.blockId, p);
  const setCellProps = (p: any) => TimeTravel.updateProps(cell!.blockId, p);
  const setRowProps = (p: any) => TimeTravel.updateProps(row!.blockId, p);

  return (
    <div className="wptb-block-control wptb-controls" ref={elRef}>
      {others.map(({ name, props, blockId, blockPath }) => {
        const ControlRC2 = BLOCK_MAP[name]?.Control;
        const updateBlockProps = (p: any) => TimeTravel.updateProps(blockId, p);
        return (
          ControlRC2 && (
            <Fragment key={blockId}>
              <EnhancementGroup
                name={name}
                props={props}
                setProps={updateBlockProps}
                blockPath={blockPath}
              />
              <ControlRC2
                blockPath={blockPath}
                setBlockProps={updateBlockProps}
                blockProps={props}
              />
            </Fragment>
          )
        );
      })}

      {ControlRC && (
        <>
          <EnhancementGroup
            name={block.name}
            props={block.props}
            setProps={updateBlockProps}
            blockPath={block.blockPath}
          />
          <ControlRC
            blockPath={block.blockPath}
            setBlockProps={updateBlockProps}
            blockProps={block.props}
          />
        </>
      )}

      {cell && (
        <>
          <EnhancementGroup
            name="cell"
            props={cell.props}
            setProps={setCellProps}
            blockPath={cell.blockPath}
          />
          <CellControl
            id={id}
            blockProps={cell.props}
            setBlockProps={setCellProps}
            blockPath={cell.blockPath}
          />
          <RowColControl
            id={id}
            rowProps={row?.props}
            setRowProps={setRowProps}
            col={cell.props.col}
          />
        </>
      )}
    </div>
  );
};

export default BlockControl;
