import { useMemo } from "react";
import { convertUnits } from "@wptb/utils/style";
import UnitControl from "../unit-control";
import ControlLabel from "../control-label";

export type AllowedUnits = Record<
  string,
  { step?: number; min?: number; max?: number }
>;

interface SizeControlProps {
  label?: string;
  value?: number | string;
  onChange?: (value: string) => void;
  onReset?: () => void;
  allowOutOfRange?: boolean;
  allowOutOfRangeMin?: boolean;
  allowOutOfRangeMax?: boolean;
  allowedUnits?: AllowedUnits;
}

const defaultAllowedUnits = {
  px: { step: 1, min: 0, max: 100 },
  em: { step: 0.2, min: 0, max: 10 },
  rem: { step: 0.2, min: 0, max: 10 },
  "%": { step: 1, min: 0, max: 100 },
};

export default function SizeControl({
  label,
  value = 0,
  onChange,
  onReset,
  allowedUnits,
  allowOutOfRange,
  allowOutOfRangeMin,
  allowOutOfRangeMax,
}: SizeControlProps) {
  const { numVal, unit, min, max, step } = useMemo(() => {
    const numVal = parseFloat(value as any) || 0;
    const unit = value?.toString().replace(numVal.toString(), "") || "px";
    const range = allowedUnits?.[unit] || defaultAllowedUnits[unit];
    let min = range?.min ?? 0;
    let max = range?.max ?? 100;
    if (allowOutOfRange) {
      min = Math.min(min, numVal);
      max = Math.max(max, numVal);
    }
    return {
      numVal,
      unit,
      min,
      max,
      step: range?.step ?? 1,
    };
  }, [value, allowedUnits, allowOutOfRange]);

  const localOnChange = (value: number) => {
    if (allowOutOfRange) {
      onChange?.(`${value}${unit}`);
    } else if (value < min) {
      if (!allowOutOfRangeMin) {
        onChange?.(`${min}${unit}`);
      } else {
        onChange?.(`${value}${unit}`);
      }
    } else if (value > max) {
      if (!allowOutOfRangeMax) {
        onChange?.(`${max}${unit}`);
      } else {
        onChange?.(`${value}${unit}`);
      }
    } else {
      onChange?.(`${value}${unit}`);
    }
  };

  return (
    <div className="wptb-size-control wptb-control">
      <ControlLabel label={label} onReset={onReset} />
      <div className="wptb-size-control-input-area">
        <input
          type="range"
          className="wptb-range-slider"
          min={min}
          max={max}
          step={step}
          value={numVal}
          onChange={(e) => localOnChange(+e.target.value)}
        />
        <UnitControl
          value={value}
          onValueChange={localOnChange}
          onUnitChange={(newUnit) =>
            onChange?.(`${convertUnits(unit, newUnit, numVal)}${newUnit}`)
          }
          unit={unit}
          step={step}
          allowedUnits={allowedUnits || defaultAllowedUnits}
        />
      </div>
    </div>
  );
}
