import cls from "classnames";
import { ENHANCED_CONTROLS } from "@/block_registry";
import { showUpsell } from "../upsell-modal";

interface UpsellControlProps {
  name: string;
  children: any;
  noOverlayText?: boolean;
}

const UpsellControl = ({
  name,
  children,
  noOverlayText,
}: UpsellControlProps) => {
  const ctrl = ENHANCED_CONTROLS[name];
  if (!ctrl) {
    return (
      <>
        <div
          className={cls("wptb-upsell-control", {
            "wptb-upsell-control-no-overlay-text": noOverlayText,
          })}
          onClickCapture={(evt) => {
            showUpsell(name);
            evt.preventDefault();
          }}
        >
          {children}
        </div>
      </>
    );
  }
  return <>{ctrl}</>;
};

export default UpsellControl;
