import { EnhancementGroup } from "@/builder/components/enhanced-control";
import UpsellControl from "@/builder/components/upsell-control";
import { RootState } from "@/store";
import { TABLE_ID } from "@/store/table";
import TimeTravel from "@/store/time-travel";
import PanelBody from "@wptb/ui/components/panel-body";
import SizeControl from "@wptb/ui/components/size-control";
import SwitchControl from "@wptb/ui/components/switch-control";
import { ColumnProps, RowProps, TableProps } from "@wptb/dev/types";
import { useSelector } from "react-redux";
import { ColorControl } from "@wptb/ui/components";

interface CtrlProps {
  id: string;
  col: number;
  rowProps?: RowProps;
  setRowProps: (props: Partial<RowProps>) => void;
}

const RowColControl = ({ id, col, rowProps, setRowProps }: CtrlProps) => {
  const tableProps: TableProps = useSelector(
    (state: RootState) => state.table[id].blocksProps[TABLE_ID]
  );
  const colProps = tableProps.columns?.[col] || {};
  const setColProps = (props: Partial<ColumnProps>) => {
    TimeTravel.updateProps(TABLE_ID, {
      columns: {
        ...tableProps.columns,
        [col]: {
          ...colProps,
          ...props,
        },
      },
    });
  };

  return (
    <PanelBody title="Row & Column Control" className="wptb-settings">
      <EnhancementGroup name="column" props={colProps} setProps={setColProps} />
      <SizeControl
        label="Column Width"
        value={colProps.width}
        onChange={(value) => setColProps({ width: value })}
        onReset={() => setColProps({ width: undefined })}
        allowedUnits={{
          px: {
            max: 500,
          },
        }}
        allowOutOfRange
      />
      <UpsellControl name="global.highlight_col" noOverlayText>
        <SwitchControl
          label="Highlight this column"
          checked={colProps.highlight as any}
          onChange={(value) => setColProps({ highlight: value })}
        />
      </UpsellControl>
      <SwitchControl
        label="Hide this column on mobile"
        checked={colProps.hideOnMobile as any}
        onChange={(value) => setColProps({ hideOnMobile: value })}
      />
      {rowProps && (
        <>
          <EnhancementGroup
            name="row"
            props={rowProps}
            setProps={setRowProps}
          />
          <SizeControl
            label="Row Height"
            value={rowProps.height}
            onChange={(value) => setRowProps({ height: value })}
            onReset={() => setRowProps({ height: undefined })}
            allowOutOfRange
            allowedUnits={{
              px: {
                max: 500,
              },
            }}
          />
          <UpsellControl name="global.highlight_row" noOverlayText>
            <SwitchControl
              label="Highlight this row"
              checked={rowProps.highlight as any}
            />
          </UpsellControl>
          <UpsellControl name="row.background" noOverlayText>
            <ColorControl label="Row Background" value={rowProps.background} />
          </UpsellControl>
        </>
      )}
    </PanelBody>
  );
};

export default RowColControl;
