import Quill, { Delta } from "quill/core";

import Snow from "quill/themes/snow";
import Toolbar from "quill/modules/toolbar";

import Bold from "quill/formats/bold";
import Italic from "quill/formats/italic";
import Underline from "quill/formats/underline";
import Strike from "quill/formats/strike";
import { AlignStyle as Align } from "quill/formats/align";

import { makeLinkPopup, Link } from "./link";
import getToolbar, { ToolbarOptions } from "./toolbar";

Quill.register("modules/toolbar", Toolbar);

Quill.register("themes/snow", Snow);
Quill.register("formats/bold", Bold);
Quill.register("formats/italic", Italic);
Quill.register("formats/underline", Underline);
Quill.register("formats/strike", Strike);
Quill.register("formats/align", Align);

Quill.register(Link);

export interface RichTextQuillProps {
  onSplit?: (beforeCursorHtml: string, afterCursorHtml: string) => void;
  onChange?: (value: string) => void;
  onAlignmentChange?: (alignment: string) => void;
  preventNewLine?: boolean;
  toolbar?: ToolbarOptions;
  placeholder?: string;
}

export interface QuillRef {
  toolbar: HTMLElement;
  isFocused: boolean;
  quill: Quill;
  setValue: (value: string | Delta) => void;
  cleanup: () => void;
}

const defaultToolbar: ToolbarOptions = [
  ["bold", "italic", "underline", "strike"],
  ["link"],
  ["align"],
];

export const initQuill = (
  el: HTMLElement,
  props: RichTextQuillProps
): QuillRef => {
  const toolBar = getToolbar(props.toolbar || defaultToolbar);
  const linkPopup = makeLinkPopup();
  const quill = new Quill(el, {
    theme: "snow",
    modules: {
      toolbar: {
        container: toolBar,
        handlers: {
          link: () => {
            linkPopup.show(el, toolBar);
          },
          align: (value: string) => {
            if (props.onAlignmentChange) {
              props.onAlignmentChange(value || "left");
            } else {
              quill.format("align", value);
            }
          },
        },
      },
    },
    placeholder: props.placeholder ?? "Content here...",
  });
  linkPopup.setQuill(quill);
  quill.root.setAttribute("spellcheck", "false");

  let isSettingContent = false,
    hasUpdatedHere = false;

  quill.on("text-change", () => {
    if (isSettingContent) return;
    hasUpdatedHere = true;
    props.onChange?.(quill.root.innerHTML);
  });

  const setValue = (value: string | Delta) => {
    if (hasUpdatedHere) {
      hasUpdatedHere = false;
      return;
    }
    isSettingContent = true;
    if (typeof value === "string") {
      quill.clipboard.dangerouslyPasteHTML(value, "api");
    } else {
      quill.setContents(value, "api");
      hasUpdatedHere = true;
      props.onChange?.(quill.root.innerHTML);
    }
    isSettingContent = false;
    quill.blur();
  };

  const returnVal: QuillRef = {
    toolbar: toolBar,
    isFocused: false,
    quill,
    setValue,
    cleanup: () => {
      linkPopup.remove();
      toolBar.remove();
    },
  };

  quill.on("selection-change", (range, oldRange) => {
    if (!oldRange) {
      returnVal.isFocused = true;
      document.body.appendChild(toolBar);
      const box = el.getBoundingClientRect();
      const tBox = toolBar.getBoundingClientRect();
      toolBar.style.top = `${box.top}px`;
      toolBar.style.left = `${box.left - (tBox.width - box.width) / 2}px`;

      const currentAlign = quill.getFormat().align || "";
      const alignEl = toolBar.querySelector(
        `.ql-align[value="${currentAlign}"]`
      );
      if (alignEl) {
        toolBar
          .querySelector(".ql-aligns .ql-wptb-active")
          ?.classList.remove("ql-wptb-active");
        alignEl.classList.add("ql-wptb-active");
        toolBar.querySelector(".ql-aligns .ql-picker-label").innerHTML =
          alignEl.innerHTML;
      }
    } else if (!range) {
      returnVal.isFocused = false;
      toolBar.remove();
    }
  });

  const keyboard = quill.getModule("keyboard") as any;

  keyboard.bindings["Enter"] = [
    {
      key: 13,
      handler: () => {
        if (props.onSplit) {
          const selection = quill.getSelection();
          if (selection) {
            const cursorPosition = selection.index;

            const delta = quill.getContents();

            const beforeCursorDelta = delta.slice(0, cursorPosition);
            const afterCursorDelta = delta.slice(cursorPosition);

            props.onSplit(
              deltaToHtml(beforeCursorDelta),
              deltaToHtml(afterCursorDelta)
            );
          }
          return false;
        }
        if (props.preventNewLine) {
          return false;
        }
        return true;
      },
    },
  ];

  return returnVal;
};

const deltaToHtml = (delta: Delta) => {
  const tempContainer = document.createElement("div");
  const tempQuill = new Quill(tempContainer);
  tempQuill.setContents(delta);
  return tempQuill.root.innerHTML;
};
