import { ResetIcon } from "../../icons";

interface ControlLabelProps {
  label: string;
  children?: React.ReactNode;
  onReset?: () => void;
  className?: string;
}

const ControlLabel = ({
  className = "",
  children,
  label,
  onReset,
}: ControlLabelProps) => {
  return (
    <div className={`wptb-control-label ${className}`}>
      <label>{label}</label>
      {onReset && (
        <button className="wptb-control-reset" onClick={onReset} title="Reset">
          {ResetIcon}
        </button>
      )}
      {children}
    </div>
  );
};

export default ControlLabel;
