import UpsellControl from "@/builder/components/upsell-control";
import PanelBody from "@wptb/ui/components/panel-body";
import RadioGroup from "@wptb/ui/components/radio-group";
import SwitchControl from "@wptb/ui/components/switch-control";
import {
  AlignBottomIcon,
  AlignMiddleIcon,
  AlignTopIcon,
} from "@wptb/ui/icons/alignment";
import { BlockControlProps } from "@wptb/dev/types";
import { CellProps } from "@wptb/dev/types";
import ColorControl from "@wptb/ui/components/color-control";
import { BorderControl } from "@wptb/ui/components";

const CellControl = ({
  blockProps,
  setBlockProps,
}: BlockControlProps<CellProps> & {
  id: string;
}) => {
  return (
    <>
      <PanelBody title="Cell Control" className="wptb-settings">
        <RadioGroup
          label="Vertical Alignment"
          value={blockProps.vAlign}
          items={[
            { label: "Top", value: "top", icon: AlignTopIcon },
            { label: "Middle", value: "middle", icon: AlignMiddleIcon },
            { label: "Bottom", value: "bottom", icon: AlignBottomIcon },
          ]}
          onChange={(value) => setBlockProps({ vAlign: value })}
        />
        <UpsellControl name="cell.bg" noOverlayText>
          <ColorControl label="Cell Background" value={blockProps.background} />
        </UpsellControl>
        <UpsellControl name="cell.border" noOverlayText>
          <BorderControl label="Cell Border" value={blockProps.border} />
        </UpsellControl>
        <UpsellControl name="cell.empty" noOverlayText>
          <SwitchControl
            label="Empty Cell"
            checked={blockProps.isEmpty as any}
          />
        </UpsellControl>
      </PanelBody>
    </>
  );
};
export default CellControl;
