import { KeyboardEvent } from "react";

const defaultAllowedUnits = {
  px: { step: 1 },
  em: { step: 0.2 },
  rem: { step: 0.2 },
  "%": { step: 1 },
};

interface UnitControlProps {
  value?: string | number;
  onValueChange?: (value: number) => void;
  onUnitChange?: (unit: string) => void;
  onChange?: (value: string) => void;
  unit?: string;
  step?: number;
  allowedUnits?: Record<
    string,
    {
      [key: string]: number;
      step?: number;
    }
  >;
  autoUnit?: boolean;
}
export default function UnitControl({
  value,
  onValueChange,
  onUnitChange,
  onChange,
  autoUnit,
  unit = "px",
  allowedUnits = defaultAllowedUnits,
}: UnitControlProps) {
  const numVal = parseFloat(value as any) || 0;

  if (autoUnit) {
    unit = value?.toString().replace(/[^a-z]/gi, "") || "px";
  }

  const oneUp = () => {
    const v = numVal + (allowedUnits[unit]?.step || 1);
    onValueChange?.(v);
    onChange?.(`${v}${unit}`);
  };

  const oneDown = () => {
    const v = numVal - (allowedUnits[unit]?.step || 1);
    onValueChange?.(v);
    onChange?.(`${v}${unit}`);
  };

  const arrowUpDown = (e: KeyboardEvent) => {
    if (e.key === "ArrowUp") {
      e.preventDefault();
      oneUp();
    } else if (e.key === "ArrowDown") {
      e.preventDefault();
      oneDown();
    }
  };

  return (
    <div className="wptb-unit-control wptb-control">
      <input
        type="number"
        value={numVal || ""}
        onKeyDown={arrowUpDown}
        onInput={(e: any) => {
          onValueChange?.(+e.target.value || 0);
          onChange?.(`${+e.target.value || 0}${unit}`);
        }}
      />
      <select
        disabled
        onPointerDown={() => {
          WPTB.utils.notify("This is an upcoming feature", "error");
        }}
        onChange={(e) => {
          onUnitChange?.(e.target.value);
          onChange?.(`${numVal}${e.target.value}`);
        }}
        value={unit}
      >
        {Object.keys(allowedUnits).map((u) => (
          <option key={u} value={u}>
            {u}
          </option>
        ))}
      </select>
      <div>
        <button type="button" onClick={oneUp} />
        <button type="button" onClick={oneDown} />
      </div>
    </div>
  );
}
