import cls from "classnames";

interface TabProps {
  active: string;
  setActive: (tab: string) => void;
  tabs: Record<
    string,
    {
      title: string;
      component: any;
    }
  >;
  props?: any;
}
const Tab = ({ active, tabs, setActive, props }: TabProps) => {
  const Component = tabs[active].component;
  return (
    <>
      <div className="wptb-tabs">
        {Object.keys(tabs).map((tab) => (
          <button
            key={tab}
            type="button"
            className={cls("wptb-tab-btn", {
              "wptb-active": active === tab,
            })}
            onClick={() => setActive(tab)}
          >
            {tabs[tab].title}
          </button>
        ))}
      </div>
      <Component {...props} />
    </>
  );
};

export default Tab;
