import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { NotificationType, UINotification, UIState } from "@wptb/dev/types";

const initialState: UIState = {
  notifications: {
    top: 0,
    items: [],
  },
};

let id = 0;

export const uiSlice = createSlice({
  name: "ui",
  initialState,
  reducers: {
    addNotification: (
      state,
      action: PayloadAction<{
        message: string | React.ReactNode;
        type: NotificationType;
        duration?: number;
        captureId?: {
          value: number;
        };
      }>
    ) => {
      const { message, type, duration } = action.payload;
      const newId = id++;
      state.notifications.items.push({
        id: newId,
        message,
        type,
        spawned_at: Date.now(),
        duration,
      });
      if (action.payload.captureId) {
        action.payload.captureId.value = newId;
      }
    },

    setNotificationItems: (
      state,
      action: PayloadAction<UIState["notifications"]["items"]>
    ) => {
      state.notifications.items = action.payload;
    },

    updateNotification: (
      state,
      action: PayloadAction<Partial<UINotification> & { id: number }>
    ) => {
      const notification = state.notifications.items.find(
        (n) => n.id === action.payload.id
      );
      if (!notification) {
        return;
      }
      Object.assign(notification, action.payload);
    },

    clearNotification: (state, action: PayloadAction<number>) => {
      state.notifications.items = state.notifications.items.filter(
        (n) => n.id !== action.payload
      );
    },

    setUpsell: (state, action: PayloadAction<string | undefined>) => {
      state.upsell = action.payload;
    },
  },
});

export const {
  addNotification,
  setNotificationItems,
  updateNotification,
  clearNotification,
  setUpsell,
} = uiSlice.actions;
