import { TableProps } from "@wptb/dev/types";
import { BorderSide, SpacingValue } from "@wptb/dev/types";
import { TableDirectives } from "@wptb/utils/parser";

export const toResponsiveDirectives = (
  newVal: TableProps["responsive"]
): string | undefined => {
  if (!newVal) {
    return;
  }

  const tab = newVal.tablet;
  const mobile = newVal.mobile;

  const old: any = {
    responsiveEnabled: newVal.enable,
    responsiveMode: "auto",
    relativeWidth: newVal.widthTarget,
    preserveRowColor: false,
    headerFullyMerged: false,
    breakpoints: {
      desktop: {
        name: "desktop",
        width: 1024,
      },
      tablet: {
        name: "tablet",
        width: parseInt(tab.maxWidth),
      },
      mobile: {
        name: "mobile",
        width: parseInt(mobile.maxWidth),
      },
    },
    modeOptions: {
      auto: {
        disabled: {
          desktop: false,
          tablet: tab.disable,
          mobile: mobile.disable,
        },
        topRowAsHeader: {
          desktop: false,
          tablet: tab.topRowAsHeader,
          mobile: mobile.topRowAsHeader,
        },
        repeatMergedHeader: {
          desktop: true,
          tablet: true,
          mobile: true,
        },
        staticTopRow: {
          desktop: false,
          tablet: tab.staticTopRow,
          mobile: mobile.staticTopRow,
        },
        cellStackDirection: {
          desktop: "row",
          tablet: tab.stackDirection,
          mobile: mobile.stackDirection,
        },
        cellsPerRow: {
          desktop: 1,
          tablet: tab.itemsPerHead,
          mobile: mobile.itemsPerHead,
        },
      },
    },
  };

  return btoa(JSON.stringify(old));
};

export const toDirectives = (props: TableProps) => {
  let active: any = "all";
  if (props.columnBorderOnly) {
    active = "column";
  } else if (props.rowBorderOnly) {
    active = "row";
  }
  const borderWidth = parseInt((props.innerBorder as BorderSide)?.width || "0");
  const borderRadius = parseInt((props.innerBorderRadius as string) || "0");

  const dir: TableDirectives = {
    innerBorders: {
      active,
      borderWidth,
      borderRadiuses: {
        all: borderRadius,
        row: borderRadius,
        column: borderRadius,
      },
    },
  };
  return btoa(JSON.stringify(dir));
};

export const toSpacingStr = (value?: SpacingValue) => {
  if (typeof value === "object") {
    return `${value.top} ${value.right} ${value.bottom} ${value.left}`;
  }
  return value;
};

export const convertQuillToTinyMCE = (html?: string): string => {
  const tempDiv = document.createElement("div");
  tempDiv.innerHTML = html || "";

  const paragraphs = tempDiv.querySelectorAll("p");

  if (paragraphs.length === 0) {
    return tempDiv.innerHTML;
  }

  let combinedContent = "";
  paragraphs.forEach((p, index) => {
    combinedContent += p.innerHTML;
    if (index < paragraphs.length - 1) {
      combinedContent += "<br>";
    }
  });

  return combinedContent;
};