import store from "@/store";
import { setActiveBlock } from "@/store/editor";
import { addColumn, addRow, removeCol, removeRow } from "@/store/table";
import TimeTravel from "@/store/time-travel";

export default class RowColActions {
  static deleteCol() {
    const { activeBlock, devicePreview } = store.getState().editorSettings;
    const cellId = activeBlock?.[2];
    if (!cellId) {
      return;
    }
    store.dispatch(setActiveBlock());
    TimeTravel.captured(removeCol, {
      cellId,
      device: devicePreview,
    });
  }

  static deleteRow() {
    const { activeBlock, devicePreview } = store.getState().editorSettings;
    const cellId = activeBlock?.[2];
    if (!cellId) {
      return;
    }
    store.dispatch(setActiveBlock());
    TimeTravel.captured(removeRow, {
      cellId,
      device: devicePreview,
    });
  }

  static addCol(after: boolean) {
    const { activeBlock, devicePreview } = store.getState().editorSettings;
    const cellId = activeBlock?.[2];
    TimeTravel.captured(addColumn, {
      cellId,
      after,
      device: devicePreview,
    });
  }

  static addRow(after: boolean) {
    const { activeBlock, devicePreview } = store.getState().editorSettings;
    const cellId = activeBlock?.[2];
    TimeTravel.captured(addRow, {
      cellId,
      after,
      device: devicePreview,
    });
  }
}
