import { CSSProperties, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { TABLE_ID } from "../store/table";
import Toolbar from "./control-panel/Toolbar";
import ControlPanel from "./control-panel";
import Table from "./blocks/Table";
import FloatingActions from "./actions/floating";
import { setActiveBlock, setSettings } from "@/store/editor";
import { DownloadingLoopIcon } from "@wptb/ui/icons/loaders";
import { TableProps } from "@wptb/dev/types";
import TimeTravel from "@/store/time-travel";
import { RootState } from "@/store";
import BuilderCtx from "./ctx";
import { isBuilderDiry } from "@/public/utils";
import cn from "classnames";

interface Props {
  id: string;
  onSave: (data: [string, number]) => void;
  onClose: () => void;
  isLoaded?: boolean;
}

const BuilderApp = ({ id, onSave, onClose, isLoaded }: Props) => {
  const dispatch = useDispatch();

  const tableProps = useSelector((state: RootState) => {
    return state.table[id]?.blocksProps[TABLE_ID] as TableProps;
  });

  const deviceWidth = useSelector((state: RootState) => {
    return state.editorSettings.previewWidth;
  });

  useEffect(() => {
    const closeWarning = (evt: BeforeUnloadEvent) => {
      if (isBuilderDiry()) {
        evt.preventDefault();
        evt.returnValue = true;
      }
    };
    addEventListener("beforeunload", closeWarning);

    return () => {
      removeEventListener("beforeunload", closeWarning);
    };
  }, []);

  useEffect(() => {
    FloatingActions.init();
    BuilderCtx.init(id);
    TimeTravel.init();

    dispatch(
      setSettings({ devicePreview: "desktop", previewWidth: undefined })
    );

    const unactiveEvt = () => {
      dispatch(setActiveBlock());
    };
    document.addEventListener("pointerup", unactiveEvt);

    return () => {
      document.removeEventListener("pointerup", unactiveEvt);
    };
  }, [id, dispatch]);

  const prevewAreaStyle = deviceWidth
    ? ({
        width: deviceWidth,
        maxWidth: deviceWidth,
        minWidth: deviceWidth,
        overflow: "auto",
        paddingLeft: "0px",
        paddingRight: "0px",
      } as CSSProperties)
    : undefined;

  return (
    <div
      className={cn("wptb-builder-page", {
        "wptb-builder-panel-right":
          WPTB_CFG.SETTINGS.general?.panel_location === "right",
        "wptb-builder-full-screen":
          !!WPTB_CFG.SETTINGS.general?.take_over_entire_screen,
      })}
    >
      <div className="wptb-builder-sidebar">
        <ControlPanel id={id} />
      </div>
      <div className="wptb-builder-content">
        <Toolbar id={id} onSave={onSave} onClose={onClose} />
        <div className="wptb-builder-preview-area" style={prevewAreaStyle}>
          {isLoaded && tableProps ? (
            <Table id={id} />
          ) : (
            <div className="wptb-table-loader">
              <DownloadingLoopIcon />
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default BuilderApp;
