import Modal from "@wptb/ui/components/modal";
import { UpsellBlocks, EnhancementUpsells } from "@/upsells";
import { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import store, { RootState } from "@/store";
import { setUpsell } from "@/store/ui";

interface UpsellModalProps {
  name?: string;
  onClose?: () => void;
}

const upsellFlat: {
  name: string;
  icon: any;
  title: string;
}[] = [];
if (!WPTB_CFG.IS_PRO) {
  for (const name in UpsellBlocks) {
    const { title, icon } = UpsellBlocks[name as keyof typeof UpsellBlocks];
    upsellFlat.push({
      name,
      icon,
      title,
    });
  }

  for (const name in EnhancementUpsells) {
    const { enhancements, icon } =
      EnhancementUpsells[name as keyof typeof EnhancementUpsells];
    for (const name2 in enhancements) {
      upsellFlat.push({
        name: `${name}.${name2}`,
        icon,
        title: enhancements[name2 as keyof typeof enhancements],
      });
    }
  }
}
const UpsellModal = ({ onClose, name }: UpsellModalProps) => {
  const [curUpsell, setCurUpsell] = useState(upsellFlat[0]);
  const [idx, setIdx] = useState(0);
  useEffect(() => {
    const idx = upsellFlat.findIndex((u) => u.name === name);
    if (idx > -1) {
      setCurUpsell(upsellFlat[idx]);
      setIdx(idx);
    } else {
      setCurUpsell(upsellFlat[0]);
      setIdx(0);
    }
  }, [name]);

  const next = () => {
    const newIdx = (idx + 1) % upsellFlat.length;
    setIdx(newIdx);
    setCurUpsell(upsellFlat[newIdx]);
  };

  const prev = () => {
    const newIdx = (idx - 1 + upsellFlat.length) % upsellFlat.length;
    setIdx(newIdx);
    setCurUpsell(upsellFlat[newIdx]);
  };

  const body = (
    <>
      <button type="button" className="wptb-upsell-modal-prev" onClick={prev}>
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width="1em"
          height="1em"
          viewBox="0 0 24 24"
        >
          <g fill="none" fillRule="evenodd">
            <path d="M24 0v24H0V0zM12.593 23.258l-.011.002l-.071.035l-.02.004l-.014-.004l-.071-.035q-.016-.005-.024.005l-.004.01l-.017.428l.005.02l.01.013l.104.074l.015.004l.012-.004l.104-.074l.012-.016l.004-.017l-.017-.427q-.004-.016-.017-.018m.265-.113l-.013.002l-.185.093l-.01.01l-.003.011l.018.43l.005.012l.008.007l.201.093q.019.005.029-.008l.004-.014l-.034-.614q-.005-.019-.02-.022m-.715.002a.02.02 0 0 0-.027.006l-.006.014l-.034.614q.001.018.017.024l.015-.002l.201-.093l.01-.008l.004-.011l.017-.43l-.003-.012l-.01-.01z" />
            <path
              fill="currentColor"
              d="M7.94 13.06a1.5 1.5 0 0 1 0-2.12l5.656-5.658a1.5 1.5 0 1 1 2.121 2.122L11.122 12l4.596 4.596a1.5 1.5 0 1 1-2.12 2.122l-5.66-5.658Z"
            />
          </g>
        </svg>
      </button>
      <button type="button" className="wptb-upsell-modal-next" onClick={next}>
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width="1em"
          height="1em"
          viewBox="0 0 24 24"
        >
          <g fill="none" fillRule="evenodd">
            <path d="M24 0v24H0V0zM12.593 23.258l-.011.002l-.071.035l-.02.004l-.014-.004l-.071-.035q-.016-.005-.024.005l-.004.01l-.017.428l.005.02l.01.013l.104.074l.015.004l.012-.004l.104-.074l.012-.016l.004-.017l-.017-.427q-.004-.016-.017-.018m.265-.113l-.013.002l-.185.093l-.01.01l-.003.011l.018.43l.005.012l.008.007l.201.093q.019.005.029-.008l.004-.014l-.034-.614q-.005-.019-.02-.022m-.715.002a.02.02 0 0 0-.027.006l-.006.014l-.034.614q.001.018.017.024l.015-.002l.201-.093l.01-.008l.004-.011l.017-.43l-.003-.012l-.01-.01z" />
            <path
              fill="currentColor"
              d="M16.06 10.94a1.5 1.5 0 0 1 0 2.12l-5.656 5.658a1.5 1.5 0 1 1-2.121-2.122L12.879 12L8.283 7.404a1.5 1.5 0 0 1 2.12-2.122l5.658 5.657Z"
            />
          </g>
        </svg>
      </button>
    </>
  );
  return (
    <Modal
      body={body}
      onClose={onClose}
      title={curUpsell.title}
      icon={curUpsell.icon}
      className="wptb-upsell-modal"
    >
      <div className="wptb-upsell-modal-description">
        <p>
          <strong>{curUpsell.title}</strong> is not available on your free plan.
          Please get the <strong style={{ color: "#ffb636" }}>PRO</strong>{" "}
          add-on to unlock all exclusive features.
        </p>
        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512">
          <path
            fill="#b1b4b5"
            d="M376.749 349.097c-13.531 0-24.5-10.969-24.5-24.5V181.932c0-48.083-39.119-87.203-87.203-87.203c-48.083 0-87.203 39.119-87.203 87.203v82.977c0 13.531-10.969 24.5-24.5 24.5s-24.5-10.969-24.5-24.5v-82.977c0-75.103 61.1-136.203 136.203-136.203s136.203 61.1 136.203 136.203v142.665c0 13.531-10.969 24.5-24.5 24.5"
          />
          <path
            fill="#ffb636"
            d="M414.115 497.459H115.977c-27.835 0-50.4-22.565-50.4-50.4V274.691c0-27.835 22.565-50.4 50.4-50.4h298.138c27.835 0 50.4 22.565 50.4 50.4v172.367c0 27.836-22.565 50.401-50.4 50.401"
          />
          <path
            fill="#ffd469"
            d="M109.311 456.841h-2.525c-7.953 0-14.4-6.447-14.4-14.4V279.309c0-7.953 6.447-14.4 14.4-14.4h2.525c7.953 0 14.4 6.447 14.4 14.4v163.132c0 7.953-6.447 14.4-14.4 14.4"
          />
        </svg>
      </div>
      <p className="wptb-upsell-modal-code">
        Limited time: Use code <span>WPTB10</span> to get a 10% discount.
      </p>
      <a href="#" className="wptb-upsell-modal-unlock">
        Unlock Now
      </a>
    </Modal>
  );
};

export const showUpsell = (name?: string) => {
  store.dispatch(setUpsell(name));
};

export const UpsellView = () => {
  const upsell = useSelector((state: RootState) => state.ui.upsell);
  return upsell ? (
    <UpsellModal name={upsell} onClose={() => showUpsell()} />
  ) : null;
};
