import { v4 as uuidv4 } from "uuid";
import { PayloadAction } from "@reduxjs/toolkit";
import {
  ResponsiveDeviceName,
  TablesState,
  TableStateDevice,
} from "@wptb/dev/types";
import { TABLE_ID } from ".";
import { CellProps, RowProps, TableProps } from "@wptb/dev/types";
import { removeWithInnerBlocks } from "./utils";
import { toResponsive } from "./responsive";
import TimeTravelCapturer from "../time-travel/capturer";


export const internalRemoveCol = (
  tables: TablesState,
  action: PayloadAction<{
    tableId: string;
    cellId: string;
    capturer: TimeTravelCapturer;
    device?: ResponsiveDeviceName;
  }>
) => {
  const { cellId, device, capturer, tableId } = action.payload;

  const state = tables[tableId];

  const table: TableStateDevice = state.desktop;
  const blocksProps = state.blocksProps;
  const tableProps = blocksProps[TABLE_ID] as TableProps;

  if (tableProps.cols === 1) {
    return;
  }

  const { col, colspan } = blocksProps[cellId] as CellProps;

  for (let i = 0; i < table.blockOrder.length; i++) {
    const rowId = table.blockOrder[i];
    const cells = table.innerBlocks[rowId];
    const newCells: string[] = [];
    for (let j = 0; j < cells.length; j++) {
      const cellId = cells[j];
      const cell = blocksProps[cellId] as CellProps;
      if (cell.col >= col && cell.col + cell.colspan <= col + colspan) {
        state.innerBlocks[cellId].blockOrder.forEach((blockId) => {
          removeWithInnerBlocks(state, blockId, capturer);
        });
        capturer.removeBlock(cellId);
        continue;
      }
      newCells.push(cellId);
      if (cell.col > col) {
        blocksProps[cellId] = {
          ...cell,
          col: cell.col - colspan,
        };
        capturer.props(cellId);
      } else if (cell.col + cell.colspan > col) {
        blocksProps[cellId] = {
          ...cell,
          colspan: cell.colspan - colspan,
        };
        capturer.props(cellId);
      }
    }

    table.innerBlocks[rowId] = newCells;
  }

  const columns: Record<string, any> = {};
  if (tableProps.columns) {
    for (const c in tableProps.columns) {
      const cIdx = parseInt(c);
      if (cIdx == col) {
        continue;
      }
      if (cIdx > col) {
        columns[cIdx - colspan] = tableProps.columns[c];
      } else {
        columns[c] = tableProps.columns[c];
      }
    }
  }

  tableProps.columns = columns;
  tableProps.cols = tableProps.cols - colspan;
  capturer.props(TABLE_ID);
  capturer.table();

  state.tablet = state.desktop;
  state.mobile = state.desktop;
  if (device) {
    state[device] = toResponsive(state, device);
  }
};

export const internalRemoveRow = (
  tables: TablesState,
  action: PayloadAction<{
    tableId: string;
    cellId: string;
    capturer: TimeTravelCapturer;
    device?: ResponsiveDeviceName;
  }>
) => {
  const { cellId, device, capturer, tableId } = action.payload;
  const state = tables[tableId];

  const table: TableStateDevice = state.desktop;
  const blocksProps = state.blocksProps;
  const tableProps: TableProps = blocksProps[TABLE_ID];

  if (tableProps.rows === 1) {
    return;
  }

  const { row, rowspan } = blocksProps[cellId] as CellProps;

  for (let i = row; i < row + rowspan; i++) {
    const rowId = table.blockOrder[i];
    const cells = table.innerBlocks[rowId];
    for (let j = 0; j < cells.length; j++) {
      const cellId = cells[j];
      state.innerBlocks[cellId].blockOrder.forEach((blockId) => {
        removeWithInnerBlocks(state, blockId, capturer);
      });
      capturer.removeBlock(cellId);
    }
    delete table.innerBlocks[rowId];
    delete blocksProps[rowId];
    table.blockOrder = table.blockOrder.filter((id) => id !== rowId);
    capturer.removeBlock(rowId);
  }

  for (let i = 0; i < row; i++) {
    const rowId = table.blockOrder[i];
    const cells = table.innerBlocks[rowId];
    for (let j = 0; j < cells.length; j++) {
      const cellId = cells[j];
      const cell = blocksProps[cellId] as CellProps;
      if (i + cell.rowspan > row) {
        blocksProps[cellId].rowspan = cell.rowspan - rowspan;
        capturer.props(cellId);
      }
    }
  }
  for (let i = row; i < table.blockOrder.length; i++) {
    const rowId = table.blockOrder[i];
    const cells = table.innerBlocks[rowId];
    for (let j = 0; j < cells.length; j++) {
      const cellId = cells[j];
      const cell = blocksProps[cellId] as CellProps;
      blocksProps[cellId].row = cell.row - rowspan;
      capturer.props(cellId);
    }
  }

  tableProps.rows -= rowspan;
  capturer.props(TABLE_ID);
  capturer.table();

  state.mobile = state.desktop;
  state.tablet = state.desktop;
  if (device) {
    state[device] = toResponsive(state, device);
  }
};

export const internalAddColumn = (
  tables: TablesState,
  action: PayloadAction<{
    tableId: string;
    cellId?: string;
    after?: boolean;
    device?: ResponsiveDeviceName;
    capturer: TimeTravelCapturer;
  }>
) => {
  const { cellId, after, device, capturer, tableId } = action.payload;

  const state = tables[tableId];

  const table = state.desktop;
  const blocksProps = state.blocksProps;
  const innerBlocks = state.innerBlocks;

  const tableProps: TableProps = blocksProps[TABLE_ID];

  let target: number;
  if (cellId) {
    target = blocksProps[cellId].col;
    target += after ? 1 : 0;
  } else if (after) {
    target = tableProps.cols;
  } else {
    target = 0;
  }

  for (let i = 0; i < table.blockOrder.length; i++) {
    const rowId = table.blockOrder[i];
    const cells = table.innerBlocks[rowId];
    let hasAdded = false;
    for (let j = 0; j < cells.length; j++) {
      const cellId = cells[j];
      const cell = blocksProps[cellId] as CellProps;
      if (cell.col < target) {
        if (cell.col + cell.colspan > target) {
          cell.colspan = cell.colspan + 1;
          hasAdded = true;
          capturer.props(cellId);
        }
        continue;
      }
      if (!hasAdded) {
        const id = uuidv4();
        blocksProps[id] = {
          row: i,
          rowspan: 1,
          col: target,
          colspan: 1,
        } as CellProps;
        innerBlocks[id] = {
          name: "cell",
          blockOrder: [],
          parentId: rowId,
        };
        cells.splice(j, 0, id);
        hasAdded = true;
        j++;
        capturer.addBlock(id);
      }
      cell.col = cell.col + 1;
      capturer.props(cellId);
    }

    if (!hasAdded) {
      const id = uuidv4();
      blocksProps[id] = {
        row: i,
        rowspan: 1,
        col: target,
        colspan: 1,
      } as CellProps;
      innerBlocks[id] = {
        name: "cell",
        blockOrder: [],
        parentId: rowId,
      };
      cells.push(id);
      hasAdded = true;
      capturer.addBlock(id);
    }
  }

  const columns: Record<string, any> = {};
  if (tableProps.columns) {
    for (const c in tableProps.columns) {
      const cIdx = parseInt(c);
      if (cIdx < target) {
        columns[c] = tableProps.columns[c];
      } else {
        columns[cIdx + 1] = tableProps.columns[c];
      }
    }
  }

  tableProps.columns = columns;
  tableProps.cols += 1;
  capturer.props(TABLE_ID);
  capturer.table();

  state.mobile = state.desktop;
  state.tablet = state.desktop;
  if (device) {
    state[device] = toResponsive(state, device);
  }
};

export const internalAddRow = (
  tables: TablesState,
  action: PayloadAction<{
    tableId: string;
    cellId?: string;
    after?: boolean;
    device?: ResponsiveDeviceName;
    capturer: TimeTravelCapturer;
  }>
) => {
  const { cellId, after, device, capturer, tableId } = action.payload;

  const state = tables[tableId];

  const table = state.desktop;
  const blocksProps = state.blocksProps;
  const innerBlocks = state.innerBlocks;

  const tableProps: TableProps = blocksProps[TABLE_ID];

  let target: number;
  if (cellId) {
    target = blocksProps[cellId].row;
    target += after ? 1 : 0;
  } else if (after) {
    target = tableProps.rows;
  } else {
    target = 0;
  }

  tableProps.rows += 1;

  const cols = tableProps.cols;
  if (!target || target >= table.blockOrder.length) {
    const cells: string[] = [];
    const rowId = uuidv4();
    blocksProps[rowId] = {} as RowProps;

    for (let i = 0; i < cols; i++) {
      const id = uuidv4();
      cells.push(id);
      innerBlocks[id] = {
        name: "cell",
        blockOrder: [],
        parentId: rowId,
      };
      blocksProps[id] = {
        row: target,
        rowspan: 1,
        col: i,
        colspan: 1,
      };
      capturer.addBlock(id);
    }
    table.innerBlocks[rowId] = cells;
    capturer.addBlock(rowId);
    if (!target) {
      table.blockOrder.unshift(rowId);
    } else {
      table.blockOrder.push(rowId);
      return;
    }
  }
  const filled: Record<number, boolean> = {};

  for (let i = 0; i < table.blockOrder.length; i++) {
    if (!target && !i) {
      continue;
    }
    if (i === target) {
      const newCells: string[] = [];
      const rowId = uuidv4();
      blocksProps[rowId] = {} as RowProps;
      for (let j = 0; j < cols; j++) {
        if (filled[j]) {
          continue;
        }
        const id = uuidv4();
        blocksProps[id] = {
          row: i,
          rowspan: 1,
          col: j,
          colspan: 1,
        };
        innerBlocks[id] = {
          name: "cell",
          blockOrder: [],
          parentId: rowId,
        };
        newCells.push(id);
        capturer.addBlock(id);
      }
      capturer.addBlock(rowId);
      
      table.innerBlocks[rowId] = newCells;
      table.blockOrder.splice(i, 0, rowId);
      i++;
    }

    const rowId = table.blockOrder[i];
    const cells = table.innerBlocks[rowId];
    for (let j = 0; j < cells.length; j++) {
      const cellId = cells[j];
      const cell = blocksProps[cellId] as CellProps;
      if (cell.row < target) {
        if (cell.row + cell.rowspan > target) {
          cell.rowspan = cell.rowspan + 1;
          filled[cell.col] = true;
          capturer.props(cellId);
        }
        continue;
      }
      cell.row = cell.row + 1;
      capturer.props(cellId);
    }
  }

  capturer.props(TABLE_ID);
  capturer.table();

  state.mobile = state.desktop;
  state.tablet = state.desktop;
  if (device) {
    state[device] = toResponsive(state, device);
  }
};

export const internalSortRows = (
  tables: TablesState,
  action: PayloadAction<{
    tableId: string;
    data: {
      from: number;
      end: number;
    }[];
    capturer: TimeTravelCapturer;
    device?: ResponsiveDeviceName;
    byCol: number;
    order: "asc" | "desc";
  }>
) => {
  const { data, capturer, device, byCol, order, tableId } = action.payload;

  const state = tables[tableId];

  const table = state.desktop;
  const blocksProps = state.blocksProps;

  const newOrder: string[] = [];

  for (let i = 0; i < data.length; i++) {
    const { from, end } = data[i];
    for (let j = from; j <= end; j++) {
      const newRow = newOrder.length;
      const rowId = table.blockOrder[j];
      newOrder.push(rowId);
      const cells = table.innerBlocks[rowId];
      for (let k = 0; k < cells.length; k++) {
        const cellId = cells[k];
        const cell = blocksProps[cellId] as CellProps;
        cell.row = newRow;
        capturer.props(cellId);
      }
    }
  }

  const tableProps = blocksProps[TABLE_ID] as TableProps;
  tableProps.sortVertical = {
    enable: true,
    by: byCol,
    order,
  };

  table.blockOrder = newOrder;
  capturer.props(TABLE_ID);
  capturer.table();

  state.mobile = state.desktop;
  state.tablet = state.desktop;
  if (device) {
    state[device] = toResponsive(state, device);
  }
};

export const internalSortColumns = (
  tables: TablesState,
  action: PayloadAction<{
    tableId: string;
    data: {
      from: number;
    }[];
    capturer: TimeTravelCapturer;
    device?: ResponsiveDeviceName;
    byRow: number;
    order: "asc" | "desc";
  }>
) => {
  const { data, capturer, device, byRow, order, tableId } = action.payload;

  const state = tables[tableId];

  const table = state.desktop;
  const blocksProps = state.blocksProps;

  const columnMapping: Record<number, number> = {};
  data.forEach((item, newIndex) => {
    columnMapping[item.from] = newIndex;
  });

  table.blockOrder.forEach((rowId) => {
    const cells = table.innerBlocks[rowId];
    const newCells: string[] = [];
    const cellPropsMap: Record<number, string> = {};

    cells.forEach((cellId) => {
      const cell = blocksProps[cellId] as CellProps;
      const colIndex = cell.col || 0;
      cellPropsMap[colIndex] = cellId;
    });

    data.forEach((item, newColIndex) => {
      const cellId = cellPropsMap[item.from];
      if (cellId) {
        newCells.push(cellId);
        const cell = blocksProps[cellId] as CellProps;
        cell.col = newColIndex;
        capturer.props(cellId);
      }
    });

    table.innerBlocks[rowId] = newCells;
  });

  const tableProps = blocksProps[TABLE_ID] as TableProps;
  tableProps.sortHorizontal = {
    enable: true,
    by: byRow,
    order,
  };

  capturer.props(TABLE_ID);
  capturer.table();

  state.mobile = state.desktop;
  state.tablet = state.desktop;
  if (device) {
    state[device] = toResponsive(state, device);
  }
};
