import { v4 as uuidv4 } from "uuid";
import { BlockState, TableState } from "@wptb/dev/types";
import { BLOCK_MAP } from "@/block_registry";
import TimeTravelCapturer from "../time-travel/capturer";

export const removeWithInnerBlocks = (
  table: TableState,
  id: string,
  capturer: TimeTravelCapturer
) => {
  const inner = table.innerBlocks[id];
  if (inner) {
    for (const blockId of inner.blockOrder) {
      removeWithInnerBlocks(table, blockId, capturer);
    }
  }
  const parent = table.innerBlocks[inner.parentId];
  if (parent) {
    parent.blockOrder = parent.blockOrder.filter((blockId) => blockId !== id);
  }
  delete table.blocksProps[id];
  delete table.innerBlocks[id];
  capturer.removeBlock(id);
};

export const createBlock = (
  table: TableState,
  name: string,
  parentId: string,
  capturer: TimeTravelCapturer,
  props?: any
): string => {
  const id = uuidv4();
  const { defaultInnerBlocks, defaultProps } = BLOCK_MAP[name];

  table.blocksProps[id] = { ...defaultProps, ...props };
  const block: BlockState = {
    name,
    parentId,
    blockOrder: [],
  };

  if (defaultInnerBlocks) {
    for (const [name, props] of defaultInnerBlocks) {
      const iid = createBlock(table, name, id, capturer, props);
      block.blockOrder.push(iid);
    }
  }

  table.innerBlocks[id] = block;
  capturer.addBlock(id);

  return id;
};

export const cloneBlock = (
  table: TableState,
  blockId: string,
  capturer: TimeTravelCapturer,
  newParentId?: string
): string => {
  const newId = uuidv4();
  const inner = table.innerBlocks[blockId];
  const newBlock: BlockState = {
    name: inner.name,
    parentId: newParentId || inner.parentId,
    blockOrder: [],
  };
  table.innerBlocks[newId] = newBlock;
  table.blocksProps[newId] = table.blocksProps[blockId];

  inner.blockOrder.forEach((id) => {
    newBlock.blockOrder.push(cloneBlock(table, id, capturer, newId));
  });

  capturer.addBlock(newId);

  return newId;
};
