import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { EditorSettings } from "@wptb/dev/types";

const initialState: EditorSettings = {
  devicePreview: "desktop",
  isSaving: false,
  timeTravel: {
    total: 0,
    cursor: 0,
    lastSaved: 0,
  },
};

export const editorSettingsSlice = createSlice({
  name: "editorSettings",
  initialState,
  reducers: {
    setSettings: (state, action: PayloadAction<Partial<EditorSettings>>) => {
      Object.assign(state, action.payload);
    },
    setActiveBlock: (state, action: PayloadAction<string[] | undefined>) => {
      state.activeBlock = action.payload;
    },

    timeTravel: (state, action: PayloadAction<number>) => {
      const cursor = action.payload;
      state.timeTravel.cursor = cursor;
    },
    timeTravelAdded: (state, action: PayloadAction<number>) => {
      state.timeTravel.cursor = action.payload;
      state.timeTravel.total = action.payload;
      if (
        state.timeTravel.lastSaved &&
        state.timeTravel.lastSaved >= state.timeTravel.cursor
      ) {
        state.timeTravel.lastSaved = undefined;
      }
    },

    startSaving: (state) => {
      state.isSaving = true;
      state.timeTravel.lastSaved = state.timeTravel.cursor;
    },
  },
});

export const {
  setSettings,
  setActiveBlock,
  timeTravel,
  timeTravelAdded,
  startSaving,
} = editorSettingsSlice.actions;

export const editorReducer = editorSettingsSlice.actions;
