import store from "@/store";
import { setSettings, startSaving } from "@/store/editor";
import { TABLE_ID } from "@/store/table";
import { BlockState, TableStateDevice } from "@wptb/dev/types";
import { CellProps, RowProps, TableProps } from "@wptb/dev/types";
import { getCellBorderWithRadius, getSingleBorderStr } from "@wptb/utils/style";
import { WPTB_BlockSerializer } from "./blocks";
import { toDirectives, toResponsiveDirectives } from "./utils";
import notify from "@/public/notify";
import BuilderCtx from "@/builder/ctx";
import { calcTableMinWidth } from "@/builder/blocks/get-attrs";


export const toOldProps = (
  blocksProps: Record<string, any>,
  table: TableStateDevice,
  innerBlocks: Record<string, any>
) => {

  
  
  const p: TableProps = blocksProps[TABLE_ID];
  const { minWidth, maxWidth, total, cellMinWidth } = calcTableMinWidth(p);
  
  const tableProps = {
    alignment: p.alignment || "center",
    enableMaxWidth: p.enableMaxWidth ? 1 : false,
    maxWidth,
    minWidth,
    cellMinWidth,
    sortHorizontal: p.sortHorizontal?.enable ? 1 : false,
    sortVertical: p.sortVertical?.enable ? 1 : false,
    borderCollapse:
      (p.separateRowCol ? "separate" : "collapse") + " !important",
    tableSpacingX: parseInt(p.colSpacing || "3"),
    tableSpacingY: parseInt(p.rowSpacing || "3"),
    cols: p.cols,
    cellsWidthAutoCount: p.cols,
    tdSumMaxWidth: total,

    // Global Font Style
    fontColor: p.fontColor || "#000000",
    linkColor: p.linkColor || "#000000",
    fontSize: parseInt(p.fontSize || "15"),

    // Border
    tableBorder: getSingleBorderStr(p.tableBorder as any),
    headerInnerBorder: p.headerInnerBorder,
    // rowBorderOnly: null, // It's in the directives
    // columnBorderOnly: null, // It's in the directives

    // Other
    scrollX: p.scrollX,
    disableThemeStyles: p.disableThemeStyles ? 1 : false,
    extraStyles: btoa(p.extraStyles || ""),
    stickyFirstColumn: p.stickyFirstColumn,
    stickyTopRow: p.stickyTopRow,
    paginationEnable: p.paginationEnable ? "true" : false,
    paginationTopRowAsHeader: (!!p.paginationTopRowAsHeader).toString(),
    rowsPerPage: p.rowsPerPage || "10",
    rowsChangeable: p.dyanamicPageSize ? "true" : false,

    searchEnable: p.searchEnable ? "true" : false,
    searchKeepHeader: (!!p.searchKeepHeader).toString(),
    searchPosition: p.searchPosition || "left",
    role: p.role || "table",

    directives: toDirectives(p),
    responsiveDirectives: toResponsiveDirectives(p.responsive),

    // Colors
    headerBg: p.headerBg,
    evenRowBg: p.evenRowBg,
    oddRowBg: p.oddRowBg,
    hoverHeaderBg: p.hoverHeaderBg,
    hoverEvenRowBg: p.hoverEvenRowBg,
    hoverOddRowBg: p.hoverOddRowBg,
  };

  const rows: any = [];

  table.blockOrder.forEach((rowId, yIndex) => {
    const rowProps: RowProps = blocksProps[rowId];
    const oCells = table.innerBlocks[rowId];
    const cells: any = [];
    oCells.forEach((cellId, xIndex) => {
      const cell: BlockState = innerBlocks[cellId];
      const cp: CellProps = blocksProps[cellId];
      const blocks: any = [];
      cell.blockOrder.forEach((id) => {
        const bb = innerBlocks[id];
        const bp = blocksProps[id];
        const bll =
          WPTB_BlockSerializer.text(bp, bb.name, p) ||
          WPTB_BlockSerializer.button(bp, bb.name) ||
          WPTB_BlockSerializer.image(bp, bb.name) ||
          WPTB_BlockSerializer.list(bp, bb.name, blocksProps, bb.blockOrder) ||
          WPTB_BlockSerializer.shortCode(bp, bb.name) ||
          WPTB_BlockSerializer.html(bp, bb.name) ||
          WPTB_BlockSerializer.starRating(bp, bb.name) ||
          WPTB_BlockSerializer.circleRating(bp, bb.name) ||
          WPTB_BlockSerializer.icon(bp, bb.name, blocksProps, bb.blockOrder) ||
          WPTB_BlockSerializer.ribbon(bp, bb.name) ||
          WPTB_BlockSerializer.styledList(
            bp,
            bb.name,
            blocksProps,
            bb.blockOrder
          ) ||
          WPTB_BlockSerializer.textIcon(bp, bb.name, blocksProps) ||
          WPTB_BlockSerializer.progressBar(bp, bb.name) ||
          WPTB_BlockSerializer.badge(bp, bb.name);
        if (bll) {
          blocks.push(bll);
        }
      });

      const column = p.columns?.[cp.col] || {};
      const width = column.width;
      const height = rowProps.height;

      cells.push({
        props: {
          autoWidth: (!!width).toString(),
          autoHeight: (!!height).toString(),
          width,
          height,
          padding: p.cellPadding,
          background: cp.background,
          ...getCellBorderWithRadius(cp, p),
          vAlign: cp.vAlign || "center",
          isEmpty: cp.isEmpty,
          xIndex,
          yIndex,
          xSort: cp.sortedHorizontal,
          ySort: cp.sortedVertical,
          colspan: cp.colspan,
          rowspan: cp.rowspan,
          hideOnMobile: column.hideOnMobile || false,
          highlighted: column.highlight ? column.highlightScale : undefined,
        },
        blocks,
      });
    });

    let background: any = rowProps.background;

    if (yIndex === 0) {
      background ||= p.headerBg;
    }

    if (yIndex % 2 === 0) {
      background ||= p.oddRowBg;
    } else {
      background ||= p.evenRowBg;
    }

    rows.push({
      props: {
        background,
        highlighted: rowProps.highlight ? rowProps.highlightScale : undefined,
      },
      cells,
    });
  });

  return {
    props: tableProps,
    rows,
  };
};

export const saveTable = async (
  id: string,
  onSave: (data: [string, number]) => void
) => {
  store.dispatch(startSaving());
  const {
    id: tableId,
    title,
    desktop: table,
    blocksProps,
    innerBlocks,
  } = store.getState().table[id];
  const oldProps = JSON.stringify(toOldProps(blocksProps, table, innerBlocks));

  const newEditor: any = {
    isSaving: false,
  };

  try {
    const id = tableId ? tableId : undefined;
    const res = await fetch(WPTB_CFG.ADMIN_URL + "?action=save_table", {
      method: "POST",
      headers: {
        "Content-type": "application/json",
      },
      body: JSON.stringify({
        id,
        title,
        content: oldProps,
        security_code: WPTB_CFG.SECURITY_CODE,
      }),
    });
    const data = await res.json();
    notify("Table saved successfully!");
    if (!id) {
      newEditor.tableId = data[1];
      BuilderCtx.init(newEditor.tableId);
    }
    onSave(data);
  } catch (e: any) {
    notify("Table save failed: " + e.message, "error", 20000);
  }

  store.dispatch(setSettings(newEditor));
};


export const saveTablePreview = async (
  id: string,
  previewId: number,
) => {
  const {
    id: tableId,
    title,
    desktop: table,
    blocksProps,
    innerBlocks,
  } = store.getState().table[id];

  if (!tableId) {
    notify("Non-saved tables cannot be previewed.", "error", 2000);
    return;
  }
  window.open(
    `${WPTB_CFG.HOME_URL}/?post_type=wptb-tables&p=${id}&_wpnonce=${WPTB_CFG.NONCE.preview}&preview_id=${previewId}&ts=1`,
    "_blank"
  );

  const oldProps = JSON.stringify(toOldProps(blocksProps, table, innerBlocks));

  try {
    await fetch(WPTB_CFG.ADMIN_URL + "?action=save_table", {
      method: "POST",
      headers: {
        "Content-type": "application/json",
      },
      body: JSON.stringify({
        id: tableId,
        title,
        content: oldProps,
        security_code: WPTB_CFG.SECURITY_CODE,
        preview_saving: previewId,
      }),
    });
  } catch (e: any) {
    notify("Table save failed: " + e.message, "error", 20000);
  }
};
