import PanelBody from "@wptb/ui/components/panel-body";
import Element from "@/builder/components/Element";
import { FREE_BLOCKS, PRO_BLOCKS } from "@/block_registry";
import { showUpsell } from "@/builder/components/upsell-modal";

export default function Elements() {
  return (
    <div className="wptb-controls">
      <PanelBody title="BASIC" initialOpen className="wptb-elements-grid">
        {FREE_BLOCKS.map(({ name, title, icon }) => (
          <Element key={name} name={name} title={title} icon={icon} />
        ))}
      </PanelBody>
      <PanelBody title="PRO" initialOpen className="wptb-elements-grid">
        {PRO_BLOCKS.map(({ name, title, icon, isUpsell }) => (
          <Element
            key={name}
            name={name}
            title={title}
            icon={icon}
            isUpsell={isUpsell}
            showUpsell={showUpsell}
          />
        ))}
      </PanelBody>
    </div>
  );
}
