import { BlockState, TableState } from "@wptb/dev/types";
import store from "..";
import BuilderCtx from "@/builder/ctx";

export interface TimeTravelEntry {
  shouldRenderResponsive: boolean;
  updatedProps: Record<
    string,
    {
      from: any;
      to: any;
    }
  >;
  addedProps: Record<string, any>;
  removedProps: Record<string, any>;
  innerBlocksOrder: Record<
    string,
    {
      from: string[];
      to: string[];
    }
  >;
  blockParentChange: Record<
    string,
    {
      from: string;
      to: string;
    }
  >;
  blockPass: Record<string, BlockState>;
  table?: {
    from: TableState["desktop"];
    to: TableState["desktop"];
  };
}

export default class TimeTravelCapturer {
  private state: TableState;

  private captureTable: boolean = false;

  private propsChange: string[] = [];

  private movedBlocks: string[] = [];
  private addedBlocks: string[] = [];
  private removedBlocks: string[] = [];

  private cells: string[] = [];

  static init(): TimeTravelCapturer {
    return new TimeTravelCapturer(store.getState().table[BuilderCtx.TABLE_ID!]);
  }

  constructor(state: TableState) {
    this.state = state;
  }

  props(blockId: string) {
    this.propsChange.push(blockId);
  }

  addBlock(blockId: string) {
    this.addedBlocks.push(blockId);
  }

  removeBlock(blockId: string) {
    this.removedBlocks.push(blockId);
  }

  moveBlock(blockId: string) {
    this.movedBlocks.push(blockId);
  }

  cell(cellId: string) {
    this.cells.push(cellId);
  }

  table() {
    this.captureTable = true;
  }

  getChanges(shouldRenderResponsive: boolean): TimeTravelEntry {
    const {
      blocksProps: oldProps,
      desktop: oldTable,
      innerBlocks: oldInnerBlocks,
    } = this.state;
    const {
      blocksProps: newProps,
      desktop: newTable,
      innerBlocks: newInnerBlocks,
    } = store.getState().table[BuilderCtx.TABLE_ID!];

    const addedProps: TimeTravelEntry["addedProps"] = {};
    const removedProps: TimeTravelEntry["removedProps"] = {};
    const updatedProps: TimeTravelEntry["updatedProps"] = {};

    const blockPass: TimeTravelEntry["blockPass"] = {};
    const innerBlocksOrder: TimeTravelEntry["innerBlocksOrder"] = {};
    const blockParentChange: TimeTravelEntry["blockParentChange"] = {};

    for (let i = 0; i < this.propsChange.length; i++) {
      const blockId = this.propsChange[i];
      updatedProps[blockId] = {
        from: oldProps[blockId],
        to: newProps[blockId],
      };
    }

    for (let i = 0; i < this.addedBlocks.length; i++) {
      const blockId = this.addedBlocks[i];
      addedProps[blockId] = newProps[blockId];

      const block = newInnerBlocks[blockId];

      if (!block) {
        continue;
      }

      blockPass[blockId] = block;

      const parentBlock = newInnerBlocks[block.parentId];
      if (parentBlock) {
        // Parent is not a row
        innerBlocksOrder[block.parentId] = {
          from: oldInnerBlocks[block.parentId]?.blockOrder || [],
          to: newInnerBlocks[block.parentId].blockOrder,
        };
      }
    }

    for (let i = 0; i < this.removedBlocks.length; i++) {
      const blockId = this.removedBlocks[i];
      removedProps[blockId] = oldProps[blockId];

      const block = oldInnerBlocks[blockId];
      if (!block) {
        continue;
      }

      blockPass[blockId] = block;

      const parentBlock = oldInnerBlocks[block.parentId];
      if (parentBlock) {
        // Parent is not a row
        innerBlocksOrder[block.parentId] = {
          from: oldInnerBlocks[block.parentId].blockOrder,
          to: newInnerBlocks[block.parentId]?.blockOrder || [],
        };
      }
    }

    for (let i = 0; i < this.movedBlocks.length; i++) {
      const blockId = this.movedBlocks[i];
      const newParent = newInnerBlocks[blockId].parentId;
      const oldParent = oldInnerBlocks[blockId].parentId;

      innerBlocksOrder[oldParent] = {
        from: oldInnerBlocks[oldParent].blockOrder,
        to: newInnerBlocks[oldParent]?.blockOrder || [], // Can be undefined on merge
      };

      innerBlocksOrder[newParent] = {
        from: oldInnerBlocks[newParent].blockOrder,
        to: newInnerBlocks[newParent].blockOrder,
      };

      blockParentChange[blockId] = {
        from: oldParent,
        to: newParent,
      };
    }

    let table: TimeTravelEntry["table"] = undefined;
    if (this.captureTable) {
      table = {
        from: oldTable,
        to: newTable,
      };

      for (let i = 0; i < oldTable.blockOrder.length; i++) {
        const rowId = oldTable.blockOrder[i];
        if (!oldProps[rowId]) {
          addedProps[rowId] = newProps[rowId];
        } else if (!newProps[rowId]) {
          removedProps[rowId] = oldProps[rowId];
        } else {
          updatedProps[rowId] = {
            from: oldProps[rowId],
            to: newProps[rowId],
          };
        }
      }
    }

    return {
      table,
      addedProps,
      removedProps,
      updatedProps,
      blockPass,
      innerBlocksOrder,
      blockParentChange,
      shouldRenderResponsive,
    };
  }
}
