import cn from "classnames";

interface SwitchControlProps {
  label?: string;
  checked?: boolean;
  onChange?: (value: boolean) => void;
  rtl?: boolean;
}

const SwitchControl = ({
  label,
  checked,
  onChange,
  rtl,
}: SwitchControlProps) => {
  return (
    <label
      className={cn("wptb-switch-control wptb-control", {
        "wptb-switch-control-rtl": rtl,
      })}
    >
      {label && <p className="wptb-switch-control-label">{label}</p>}
      <input
        type="checkbox"
        checked={checked}
        className="btn"
        onChange={() => onChange?.(!checked)}
      />
      <i />
    </label>
  );
};

export default SwitchControl;
