import { useDispatch, useSelector } from "react-redux";

import { ResponsiveDeviceProps, TableProps } from "@wptb/dev/types";
import SwitchControl from "@wptb/ui/components/switch-control";
import SelectControl from "@wptb/ui/components/select-control";

import { RootState } from "@/store";
import Tab from "@wptb/ui/components/tab";
import RangeControl from "@wptb/ui/components/range-control";
import UpsellControl from "@/builder/components/upsell-control";
import PanelBody from "@wptb/ui/components/panel-body";
import { setSettings } from "@/store/editor";
import { TABLE_ID } from "@/store/table";
import TimeTravel from "@/store/time-travel";
import { EnhancementGroup } from "@/builder/components/enhanced-control";
import { SizeControl } from "@wptb/ui/components";
import { ResponsiveDeviceName } from "@wptb/dev/types";

type ResponsiveProps = NonNullable<TableProps["responsive"]>;

interface TabProps {
  props?: ResponsiveDeviceProps;
  setProps: (props: Partial<ResponsiveDeviceProps>) => void;
}

const DeviceTab = ({ props, setProps }: TabProps) => {
  const dispatch = useDispatch();
  const setMaxWidth = (value: string) => {
    dispatch(setSettings({ previewWidth: value }));
    setProps({ maxWidth: value });
  };

  return (
    <div className="wptb-control-group">
      <EnhancementGroup name="responsive" props={props} setProps={setProps} />
      <UpsellControl name="global.disable_responsive" noOverlayText>
        <SwitchControl
          label="Disable Breakpoint"
          checked={props?.disable as any}
          onChange={(checked) => setProps({ disable: checked })}
        />
      </UpsellControl>
      <SizeControl
        label="Max Width"
        value={props?.maxWidth}
        onChange={setMaxWidth}
        allowedUnits={{ px: { max: 1500, min: 300 } }}
        allowOutOfRange
      />
      <SwitchControl
        label="Top Row As Header"
        checked={props?.topRowAsHeader as any}
        onChange={(checked) => setProps({ topRowAsHeader: checked })}
      />
      <SwitchControl
        label="Static Top Row"
        checked={props?.staticTopRow as any}
        onChange={(checked) => setProps({ staticTopRow: checked })}
      />
      <SelectControl
        label="Stack Direction"
        value={props?.stackDirection}
        onChange={(value) => setProps({ stackDirection: value as any })}
        options={[
          { label: "Row", value: "row" },
          { label: "Column", value: "column" },
        ]}
      />
      <RangeControl
        label="Items Per Head"
        value={props?.itemsPerHead}
        onChange={(value) => setProps({ itemsPerHead: Math.max(value, 1) })}
        min={1}
        max={10}
        allowOutOfRange
      />
    </div>
  );
};

const DesktopTab = () => {
  return (
    <div className="wptb-control-group">
      <div className="wptb-responsive-warning">
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width="1em"
          height="1em"
          viewBox="0 0 512 512"
        >
          <path
            fill="currentColor"
            d="M16 352a48.05 48.05 0 0 0 48 48h133.88l-4 32H144a16 16 0 0 0 0 32h224a16 16 0 0 0 0-32h-49.88l-4-32H448a48.05 48.05 0 0 0 48-48v-48H16Zm240-16a16 16 0 1 1-16 16a16 16 0 0 1 16-16M496 96a48.05 48.05 0 0 0-48-48H64a48.05 48.05 0 0 0-48 48v192h480Z"
          />
        </svg>
        <p>You can set responsive properties only for mobile or tablet.</p>
      </div>
    </div>
  );
};

interface ResProps {
  id: string;
}

export default function Responsive({ id }: ResProps) {
  const dispatch = useDispatch();

  const responsiveProps: ResponsiveProps = useSelector(
    (state: RootState) => state.table[id].blocksProps[TABLE_ID]?.responsive
  );
  const previewWidth = useSelector(
    (state: RootState) => state.editorSettings.previewWidth
  );

  const activeTab = useSelector(
    (store: RootState) => store.editorSettings.devicePreview
  );

  const setAttrs = (attrs: Partial<ResponsiveProps>) => {
    TimeTravel.updateProps(TABLE_ID, {
      responsive: {
        ...responsiveProps,
        ...attrs,
      },
    });
  };

  const setDeviceProps = (
    device: "mobile" | "tablet",
    props: Partial<ResponsiveDeviceProps>
  ) => {
    if (!responsiveProps) {
      return;
    }
    setAttrs({
      ...responsiveProps,
      [device]: {
        ...responsiveProps[device],
        ...props,
      },
    });
  };
  const setDevicePreview = (devicePreview: ResponsiveDeviceName) => {
    if (devicePreview === "tablet") {
      const tabletWidth = responsiveProps?.tablet?.maxWidth || "700px";
      dispatch(
        setSettings({
          devicePreview,
          previewWidth: tabletWidth,
        })
      );
      return;
    }
    if (devicePreview === "mobile") {
      const mobileWidth = responsiveProps?.mobile?.maxWidth || "300px";
      dispatch(
        setSettings({
          devicePreview,
          previewWidth: mobileWidth,
        })
      );
      return;
    }

    dispatch(
      setSettings({
        devicePreview,
        previewWidth: undefined,
      })
    );
  };

  const setPreviewWidth = (previewWidth: string) => {
    const mobileWidth = responsiveProps?.mobile?.maxWidth || "300px";
    const tabletWidth = responsiveProps?.tablet?.maxWidth || "700px";
    if (previewWidth < mobileWidth) {
      dispatch(setSettings({ devicePreview: "mobile", previewWidth }));
    } else if (previewWidth < tabletWidth) {
      dispatch(setSettings({ devicePreview: "tablet", previewWidth }));
    } else {
      dispatch(setSettings({ devicePreview: "desktop", previewWidth }));
    }
  };

  return (
    <div className="wptb-controls wptb-settings">
      <PanelBody title="General" initialOpen>
        <SwitchControl
          label="Enable Responsive Table"
          checked={responsiveProps?.enable as any}
          onChange={(v) =>
            setAttrs({
              ...responsiveProps,
              enable: v,
            })
          }
        />
        <SelectControl
          label="Width Target"
          value={responsiveProps?.widthTarget}
          onChange={(v) =>
            setAttrs({ ...responsiveProps, widthTarget: v as any })
          }
          options={[
            { label: "Window", value: "window" },
            { label: "Container", value: "container" },
          ]}
        />
        <SizeControl
          label="Preview Width"
          value={previewWidth}
          onChange={setPreviewWidth}
          allowOutOfRange
          allowedUnits={{
            px: {
              max: 2000,
            },
          }}
        />
      </PanelBody>
      <PanelBody initialOpen title="Breakpoints">
        <Tab
          active={activeTab}
          setActive={setDevicePreview as any}
          tabs={{
            mobile: { title: "Mobile", component: DeviceTab },
            tablet: { title: "Tablet", component: DeviceTab },
            desktop: { title: "Desktop", component: DesktopTab },
          }}
          props={
            activeTab === "desktop"
              ? {}
              : {
                  props: responsiveProps?.[activeTab],
                  setProps: (props: any) => setDeviceProps(activeTab, props),
                }
          }
        />
      </PanelBody>
    </div>
  );
}
