import { TextareaHTMLAttributes } from "react";

interface LongTextControlProps
  extends Omit<TextareaHTMLAttributes<HTMLTextAreaElement>, "onChange"> {
  label?: string;
  value?: string;
  onChange?: (value: string) => void;
}

const LongTextControl = ({
  label,
  value,
  onChange,
  ...props
}: LongTextControlProps) => {
  return (
    <label className="wptb-long-text-control wptb-control">
      {label && <p className="wptb-long-text-control-label">{label}</p>}
      <textarea
        value={value || ""}
        onChange={(e) => onChange?.(e.target.value)}
        {...props}
      />
    </label>
  );
};

export default LongTextControl;
