import { TABLE_ID } from "@/store/table";
import {
  BadgeBlockProps,
  ButtonBlockProps,
  CircleRatingBlockProps,
  CustomHtmlBlockProps,
  IconBlockIconProps,
  IconBlockProps,
  ImageBlockProps,
  ListBlockProps,
  ListItemBlockProps,
  ProgressBarBlockProps,
  RibbonBlockProps,
  ShortcodeBlockProps,
  StarRatingBlockProps,
  StyledListBlockProps,
  StyledListItemBlockProps,
  TableProps,
  TextBlockProps,
  TextIconBlockProps,
} from "@wptb/dev/types";
import { convertQuillToTinyMCE, toSpacingStr } from "./utils";

export const WPTB_BlockSerializer = {
  text(props: TextBlockProps, type: string, p: TableProps) {
    if (type !== "text") {
      return;
    }

    const linkColor = props.linkColor || p.linkColor;
    let text = props.text!;

    if (linkColor) {
      const div = document.createElement("div");
      div.innerHTML = text;
      div.querySelectorAll("a").forEach((a) => {
        a.style.color = linkColor;
      });
      text = div.innerHTML;
    }

    return {
      type: "text",
      props: {
        text: text,
        color: props.color || p.fontColor,
        fontSize: props.fontSize || p.fontSize,
        padding: toSpacingStr(props.padding),
        margin: toSpacingStr(props.margin),
      },
    };
  },

  image(props: ImageBlockProps, type: string) {
    if (type !== "image") {
      return;
    }

    return {
      type: "image",
      props: {
        src: props.src,
        alignment: props.alignment || "center",
        alt: props.alt,
        imgHeight: props.imgHeight,
        imgWidth: props.imgWidth,

        sizeRelativeTo: "container",
        size: props.size,
        width: props.width,

        url: props.url,
        linkRel: props.linkRel,
        linkTarget: props.linkTarget,
        convertToAbsolute: props.convertToAbsolute ? "true" : false,

        padding: toSpacingStr(props.padding),
        margin: toSpacingStr(props.margin),
      },
    };
  },

  button(props: ButtonBlockProps, type: string) {
    if (type !== "button") {
      return;
    }
    return {
      type: "button",
      props: {
        text: convertQuillToTinyMCE(props.text),
        fontSize: props.fontSize,
        size: props.size || "m",
        width: props.fullWidth ? "100%" : null,
        borderRadius: props.borderRadius,
        background: props.background,
        color: props.color,
        buttonAlignment: props.buttonAlignment,
        contentAlignment: props.contentAlignment,
        id: props.id,
        // Url
        url: props.url,
        linkRel: props.linkRel,
        linkTarget: props.linkTarget,
        convertToAbsolute: !!props.convertToAbsolute,

        // Hover
        hoverBg: props.hoverBg,
        hoverColor: props.hoverColor,
        hoverScale: props.hoverScale,

        // Icon
        icon: props.icon || "",
        iconPosition: props.iconPosition,
        iconSize: props.iconSize,

        // Label
        hasLabel: props.hasLabel,
        labelBg: props.labelBg,
        labelColor: props.labelColor,
        labelText: props.labelText,

        padding: toSpacingStr(props.padding),
        margin: toSpacingStr(props.margin),
      },
    };
  },

  list(
    props: ListBlockProps,
    type: string,
    bp: Record<string, any>,
    bo: string[]
  ) {
    if (type !== "list") {
      return;
    }

    const items: any = [];

    bo.forEach((id) => {
      const ip: ListItemBlockProps = bp[id];
      items.push({
        text: convertQuillToTinyMCE(ip.text),
        alignment: ip.alignment || props.alignment,
        toolTip: ip.toolTip,
        tooltipPosision: ip.tooltipPosision,
      });
    });

    return {
      type: "list",
      props: {
        type: props.type,
        listIcon: "wptb-list-style-type-" + props.listIcon,
        color: props.color,
        fontSize: props.fontSize,
        itemSpacing: props.itemSpacing,

        padding: toSpacingStr(props.padding),
        margin: toSpacingStr(props.margin),
      },
      items,
    };
  },

  starRating(props: StarRatingBlockProps, type: string) {
    if (type !== "star-rating") {
      return;
    }

    return {
      type: "starRating",
      props: {
        starSize: props.starSize,
        starColor: props.starColor,
        starCount: props.maxValue,
        alignment: props.alignment,
        showRating: props.showRating,
        fontSize: props.fontSize,
        color: props.color,
        value: props.value,

        padding: toSpacingStr(props.padding),
        margin: toSpacingStr(props.margin),
      },
    };
  },

  html(props: CustomHtmlBlockProps, type: string) {
    if (type !== "custom-html") {
      return;
    }
    return {
      type: "customHtml",
      props: {
        html: props.html,

        padding: toSpacingStr(props.padding),
        margin: toSpacingStr(props.margin),
      },
    };
  },

  shortCode(props: ShortcodeBlockProps, type: string) {
    if (type !== "shortcode") {
      return;
    }

    return {
      type: "shortcode",
      props: {
        shortcode: props.shortcode,

        padding: toSpacingStr(props.padding),
        margin: toSpacingStr(props.margin),
      },
    };
  },

  /*** PRO ***/
  circleRating(props: CircleRatingBlockProps, type: string) {
    if (type !== "circle-rating") {
      return;
    }

    return {
      type: "circleRating",
      props: {
        size: props.size,
        color: props.color,
        ratingType: props.ratingType,
        value: props.value,
        total: props.total,

        margin: toSpacingStr(props.margin),
        padding: toSpacingStr(props.padding),
      },
    };
  },

  icon(
    props: IconBlockProps,
    type: string,
    bp: Record<string, any>,
    bo: string[]
  ) {
    if (type !== "icon") {
      return;
    }
    const icons: any = [];
    const mainProps = {
      count: props.count,
      size: props.size,
      alignment: props.alignment,
      color: props.color,

      padding: toSpacingStr(props.padding),
      margin: toSpacingStr(props.margin),
    };

    bo.forEach((itemId) => {
      const props: IconBlockIconProps = bp[itemId];
      icons.push({
        icon: props.icon,
        color: props.color || mainProps.color,
        url: props.url,
        linkRel: props.linkRel?.join(" "),
        linkTarget: props.linkTarget,
        convertToAbsolute: props.convertToAbsolute,
      });
    });

    return {
      type: "icon",
      props: mainProps,
      icons,
    };
  },

  ribbon(props: RibbonBlockProps, type: string) {
    if (type !== "ribbon") {
      return;
    }

    const ind = props.individual || {};

    const styleObj: any = {
      color: props.color || "rgb(0, 0, 0)",
      top: ind.positionY || "0%",
      "z-index": 9999,
    };

    if (props.type === "corner") {
      styleObj.top = "-5px";
      styleObj.left = "-5px";
    } else if (props.type === "side") {
      if (ind.side === "right") {
        styleObj.right = "-20px";
      } else {
        styleObj.left = "-20px";
      }
    } else if (props.type === "icon") {
      if (ind.side === "right") {
        styleObj.left = "calc(100% - 75px)";
      } else {
        styleObj.left = "-20px";
      }
    } else {
      styleObj.left = ind.positionX || "0%";
    }

    let style = "";

    for (const p in styleObj) {
      style += `${p}: ${styleObj[p]};`;
    }

    const mainProps = {
      type: props.type + "Ribbon",
      modifications: btoa(
        JSON.stringify({
          class: {
            mainWrap: {
              element: {
                relative: "mainWrap",
                query: "",
              },
              class: [
                "wptb-ribbon-bookmark-main-wrap",
                "wptb-plugin-filter-box-shadow-md",
              ],
            },
          },
          style: {
            textWrap: {
              element: {
                relative: "textWrap",
                query: "",
              },
              style: {
                backgroundColor: {
                  default: "",
                  value: "#FFFFFF",
                },
              },
            },
            svgWrapper: {
              element: {
                relative: "element",
                query: "#wptbBookmarkRibbonElementEnd",
              },
              style: {
                color: {
                  default: "",
                  value: "#FFFFFF",
                },
                width: {
                  default: "",
                  value: "70px",
                },
              },
            },
            mainWrap: {
              element: {
                relative: "mainWrap",
                query: "",
              },
              style: {
                width: {
                  default: "",
                  value: "70px",
                },
              },
            },
          },
        })
      ),
      side: ind.side || "left",
      xOffset: parseInt(ind.positionX || "0"),
      yOffset: parseInt(ind.positionY || "0"),
      width: parseInt(ind.width || "70px"),
      style: style,

      background: props.background,
      borderColor: props.borderColor,
      color: props.color,
      fontSize: props.fontSize,
      text: convertQuillToTinyMCE(props.text),

      icon: ind.icon || "star",
      enableAnimation: !ind.animation ? undefined : 1,
      animationType: ind.animation || "beat",
    };

    return {
      type: "ribbon",
      props: mainProps,
    };
  },

  styledList(
    props: StyledListBlockProps,
    type: string,
    bp: Record<string, any>,
    bo: string[]
  ) {
    if (type !== "styled-list") {
      return;
    }

    const items: any = [];

    const tableProps: TableProps = bp[TABLE_ID];

    bo.forEach((id) => {
      const ip: StyledListItemBlockProps = bp[id];
      items.push({
        text: convertQuillToTinyMCE(ip.text),
        alignment: ip.alignment || props.alignment,
        toolTip: ip.toolTip,
        tooltipPosision: ip.tooltipPosision,
      });
    });

    return {
      type: "styledList",
      props: {
        icon: props.icon,
        iconSize: props.iconSize,
        iconColor: props.iconColor,
        iconSpacing: props.iconSpacing,
        color: props.color || tableProps.fontColor,
        fontSize: props.fontSize || tableProps.fontSize,
        itemSpacing: props.itemSpacing,

        padding: toSpacingStr(props.padding),
        margin: toSpacingStr(props.margin),
      },
      items,
    };
  },

  textIcon(props: TextIconBlockProps, type: string, bp: Record<string, any>) {
    if (type !== "text-icon") {
      return;
    }

    const tableProps: TableProps = bp[TABLE_ID];

    return {
      type: "textIcon",
      props: {
        spaceBetween: props.iconSpacing,
        alignment: props.alignment,
        fontSize: props.fontSize || tableProps.fontSize,
        color: props.color || tableProps.fontColor,
        text: convertQuillToTinyMCE(props.text),
        icon: props.icon,
        iconLocation: props.iconSide,
        iconSize: props.iconSize,
        iconColor: props.iconColor,

        padding: toSpacingStr(props.padding),
        margin: toSpacingStr(props.margin),
      },
    };
  },

  progressBar(props: ProgressBarBlockProps, type: string) {
    if (type !== "progress-bar") {
      return;
    }

    return {
      type: "progressBar",
      props: {
        value: props.value,
        thickness: parseInt(props.thickness),
        primaryColor: props.primaryColor,
        secondaryColor: props.secondaryColor,
        labelColor: props.color,

        padding: toSpacingStr(props.padding),
        margin: toSpacingStr(props.margin),
      },
    };
  },
  badge(props: BadgeBlockProps, type: string) {
    if (type !== "badge") {
      return;
    }

    const ALIGNS = {
      left: "flex-start",
      center: "center",
      right: "flex-end",
    };

    return {
      type: "badge",
      props: {
        fontSize: props.fontSize,
        color: props.color,
        background: props.background,
        alignment: ALIGNS[(props.alignment as keyof typeof ALIGNS) || "center"],
        text: convertQuillToTinyMCE(props.text),

        padding: toSpacingStr(props.padding),
        margin: toSpacingStr(props.margin),
      },
    };
  },
};
