import { SortingValue } from "@wptb/dev/types";
import {
  FloatingCtx,
  moveColHandle,
  moveRowHandle,
  rowColIndicator,
} from "./helpers";
import store from "@/store";
import TimeTravel from "@/store/time-travel";
import { sortColumns, sortRows } from "@/store/table";
import { showUpsell } from "@/builder/components/upsell-modal";
import { ENHANCEMENT_MAP } from "@/block_registry";

export default class RowColActions {
  static isRowMoving = false;

  static targetCell?: string;
  static destCell?: string;
  static targetEl?: HTMLElement;

  static onCellEnter(el: HTMLTableCellElement, id: string) {
    if (!FloatingCtx.isRowColMoving) {
      RowColActions.targetEl = el;
      RowColActions.targetCell = id;
      el.appendChild(moveColHandle);
      el.appendChild(moveRowHandle);
      return;
    }
    RowColActions.destCell = id;

    const box = el.getBoundingClientRect();

    if (RowColActions.isRowMoving) {
      rowColIndicator.style.top = box.top + "px";
      rowColIndicator.style.height = box.height + "px";
    } else {
      rowColIndicator.style.left = box.left + "px";
      rowColIndicator.style.width = box.width + "px";
    }
  }

  static onCellLeave() {
    if (FloatingCtx.isRowColMoving) {
      return;
    }
    RowColActions.targetEl = undefined;
    moveColHandle.remove();
    moveRowHandle.remove();
  }

  static startMovingRow(evt: MouseEvent) {
    if (!RowColActions.targetEl) {
      return;
    }
    evt.preventDefault();
    FloatingCtx.isRowColMoving = true;
    RowColActions.isRowMoving = true;
    RowColActions.destCell = undefined;

    moveRowHandle.classList.add("wptb-row-col-moving");
    document.addEventListener("mouseup", RowColActions.onRowMoveEnd);

    const rowEl = RowColActions.targetEl?.closest("tr");
    if (!rowEl) {
      return;
    }

    const rowBox = rowEl.getBoundingClientRect();
    rowColIndicator.style.display = "block";
    rowColIndicator.style.height = rowBox.height + "px";
    rowColIndicator.style.width = rowBox.width + "px";
    rowColIndicator.style.top = rowBox.top + "px";
    rowColIndicator.style.left = rowBox.left + "px";
  }

  static onRowMoveEnd() {
    document.removeEventListener("mouseup", RowColActions.onRowMoveEnd);
    moveRowHandle.classList.remove("wptb-row-col-moving");
    FloatingCtx.isRowColMoving = false;

    const makeMove = ENHANCEMENT_MAP["move-row"];
    if (!makeMove) {
      showUpsell("row.move");
    } else if (RowColActions.targetCell && RowColActions.destCell) {
      makeMove(RowColActions.targetCell, RowColActions.destCell);
    }

    rowColIndicator.style.display = "none";
    RowColActions.destCell = undefined;
  }

  static startMovingCol(evt: MouseEvent) {
    if (!RowColActions.targetEl) {
      return;
    }
    evt.preventDefault();
    RowColActions.isRowMoving = false;
    FloatingCtx.isRowColMoving = true;
    RowColActions.destCell = undefined;

    moveColHandle.classList.add("wptb-row-col-moving");
    document.addEventListener("mouseup", RowColActions.onColMoveEnd);

    const colBox = RowColActions.targetEl!.getBoundingClientRect();
    const tBox =
      RowColActions.targetEl!.closest("table")!.getBoundingClientRect();
    rowColIndicator.style.display = "block";
    rowColIndicator.style.height = tBox.height + "px";
    rowColIndicator.style.width = colBox.width + "px";
    rowColIndicator.style.top = tBox.top + "px";
    rowColIndicator.style.left = colBox.left + "px";
  }

  static onColMoveEnd() {
    document.removeEventListener("mouseup", RowColActions.onColMoveEnd);
    moveColHandle.classList.remove("wptb-row-col-moving");
    FloatingCtx.isRowColMoving = false;
    const makeMove = ENHANCEMENT_MAP["move-column"];
    if (!makeMove) {
      showUpsell("row.move");
    } else if (RowColActions.targetCell && RowColActions.destCell) {
      makeMove(RowColActions.targetCell, RowColActions.destCell);
    }
    rowColIndicator.style.display = "none";
    RowColActions.destCell = undefined;
  }

  static sortTableV(
    cellEL: HTMLTableCellElement,
    byCol: number,
    oldCfg?: SortingValue
  ) {
    const device = store.getState().editorSettings.devicePreview;

    if (device !== "desktop") {
      return;
    }

    const indexedRows: {
      originalIndex: number;
      content: string;
      end: number;
    }[] = [];

    const prevMap: Record<number, number> = {};
    const firstColMap: Record<number, number> = {};

    const tableEl: HTMLTableElement = cellEL.closest("table")!;
    const allRows = tableEl.tBodies[0].rows;
    for (let i = 0; i < allRows.length; i++) {
      const cells = allRows[i].cells;
      let prev = prevMap[i];
      let maxEnd = 0;
      let content: string = "";
      let col = firstColMap[i] || 0;
      for (let j = 0; j < cells.length; j++) {
        if (j === 0 && cells[j].rowSpan > 1) {
          for (let k = 1; k < cells[j].rowSpan; k++) {
            firstColMap[i + k] = cells[j].colSpan;
          }
        }
        if (col === byCol) {
          content = cells[j].textContent?.trim().toLowerCase() || "";
        }
        col += cells[j].colSpan;
        maxEnd = Math.max(maxEnd, i + cells[j].rowSpan - 1);
      }
      if (typeof prev !== "number") {
        prev = indexedRows.length;
        indexedRows.push({
          originalIndex: i,
          content: content,
          end: maxEnd,
        });
      } else {
        indexedRows[prev].end = maxEnd;
        indexedRows[prev].content += content;
      }
      for (let j = i; j <= maxEnd; j++) {
        prevMap[j] = prev;
      }
    }

    const head = indexedRows.shift();

    const newOrder =
      oldCfg?.by == byCol && oldCfg?.order === "asc" ? "desc" : "asc";

    if (newOrder === "asc") {
      indexedRows.sort((a, b) => a.content.localeCompare(b.content));
    } else {
      indexedRows.sort((a, b) => b.content.localeCompare(a.content));
    }

    const sortingData: any = [
      {
        from: head?.originalIndex || 0,
        end: head?.end || 0,
      },
    ];
    indexedRows.forEach((item) => {
      sortingData.push({
        from: item.originalIndex,
        end: item.end,
      });
    });

    if (sortingData.length < 2) {
      return;
    }

    TimeTravel.captured(sortRows, {
      data: sortingData,
      device: device,
      byCol,
      order: newOrder,
    });
  }

  static sortTableH(
    cellEL: HTMLTableCellElement,
    byRow: number,
    oldCfg?: SortingValue
  ) {
    const device = store.getState().editorSettings.devicePreview;

    if (device !== "desktop") {
      return;
    }

    const tableEl: HTMLTableElement = cellEL.closest("table")!;
    const allRows = tableEl.tBodies[0].rows;

    const totalColumns = Array.from(allRows[0].cells).reduce(
      (sum, cell) => sum + (cell.colSpan || 1),
      0
    );

    const indexedColumns: {
      originalIndex: number;
      content: string;
      cells: HTMLTableCellElement[];
    }[] = [];
    for (let colIndex = 0; colIndex < totalColumns; colIndex++) {
      indexedColumns[colIndex] = {
        originalIndex: colIndex,
        content: "",
        cells: [],
      };
    }
    const rowSpanMap: Record<
      number,
      { cell: HTMLTableCellElement; span: number }
    > = {};

    for (let rowIndex = 0; rowIndex < allRows.length; rowIndex++) {
      const row = allRows[rowIndex];
      const cells = row.cells;
      let colIndex = 0;

      for (let cellIndex = 0; cellIndex < cells.length; cellIndex++) {
        while (rowSpanMap[colIndex]?.span > 0) {
          rowSpanMap[colIndex].span--;
          if (rowSpanMap[colIndex].span === 0) {
            delete rowSpanMap[colIndex];
          }
          colIndex++;
        }

        const cell = cells[cellIndex];
        const rowSpan = cell.rowSpan || 1;
        const colSpan = cell.colSpan || 1;

        if (rowSpan > 1) {
          rowSpanMap[colIndex] = { cell, span: rowSpan - 1 };
        }

        for (let span = 0; span < colSpan; span++) {
          const currentColIndex = colIndex + span;

          if (rowIndex === byRow) {
            indexedColumns[currentColIndex].content =
              cell.textContent?.trim().toLowerCase() || "";
          }

          indexedColumns[currentColIndex].cells.push(cell);
        }

        colIndex += colSpan;
      }

      Object.keys(rowSpanMap).forEach((key) => {
        const colIdx = parseInt(key, 10);
        rowSpanMap[colIdx].span--;
        if (rowSpanMap[colIdx].span === 0) {
          delete rowSpanMap[colIdx];
        }
      });
    }
    const newOrder =
      oldCfg?.by == byRow && oldCfg?.order === "asc" ? "desc" : "asc";
    if (newOrder === "asc") {
      indexedColumns.sort((a, b) => a.content.localeCompare(b.content));
    } else {
      indexedColumns.sort((a, b) => b.content.localeCompare(a.content));
    }

    const sortingData = indexedColumns.map((item) => ({
      from: item.originalIndex,
    }));

    if (sortingData.length < 2) {
      return;
    }

    TimeTravel.captured(sortColumns, {
      data: sortingData,
      device: device,
      byRow,
      order: newOrder,
    });
  }
}

moveRowHandle.addEventListener("mousedown", RowColActions.startMovingRow);
moveColHandle.addEventListener("mousedown", RowColActions.startMovingCol);
moveColHandle.addEventListener("click", evt => evt.stopPropagation());
moveRowHandle.addEventListener("click", evt => evt.stopPropagation());