import { useState } from "react";
import cls from "classnames";
import { Border, BorderSide } from "@wptb/dev/types";
import { convertUnits } from "@wptb/utils/style";
import UnitControl from "../unit-control";
import ControlLabel from "../control-label";

interface BorderControlProps {
  label?: string;
  value?: Border | BorderSide | string;
  onChange?: (value: Border | BorderSide) => void;
  onReset?: () => void;
}

interface BorderInputProps {
  value?: BorderSide | string;
  onChange: (value: BorderSide) => void;
  isVertical?: boolean;
}

const units = {
  px: {
    max: 20,
    step: 1,
  },
  em: {
    max: 5,
    step: 0.2,
  },
  rem: {
    max: 5,
    step: 0.2,
  },
};

export default function BorderControl({
  label,
  value,
  onChange,
  onReset,
}: BorderControlProps) {
  const isSplitInit = false;
  if (!value) {
    value = {
      width: "0px",
      color: "transparent",
      style: "solid",
    };
  } else if (typeof value === "string") {
    const [width, style, ...color] = value.split(" ");
    value = {
      width,
      style,
      color: color.join(" "),
    } as BorderSide;
  } else if (
    "top" in value &&
    "right" in value &&
    "bottom" in value &&
    "left" in value
  ) {
    // isSplitInit = true;
    value = value.top;
  } else if (
    !("width" in value) ||
    !("color" in value) ||
    !("style" in value)
  ) {
    value = {
      width: "0px",
      color: "transparent",
      style: "solid",
    };
  }
  const [isSplit] = useState(isSplitInit);

  const changeSplit = () => {
    // if (isSplit) {
    //   onChange?.((value as any).top);
    //   setIsSplit(false);
    // } else {
    //   onChange?.({
    //     top: value as any,
    //     right: value as any,
    //     bottom: value as any,
    //     left: value as any,
    //   });
    //   setIsSplit(true);
    // }
    WPTB.utils.notify("This is an upcoming feature", "error");
  };

  return (
    <div className="wptb-border-control wptb-control">
      <ControlLabel label={label} onReset={onReset}>
        <button type="button" onClick={changeSplit} className="wptb-split-btn">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 24 24"
            width="24"
            height="24"
            focusable="false"
          >
            {isSplit ? (
              <path d="M17.031 4.703 15.576 4l-1.56 3H14v.03l-2.324 4.47H9.5V13h1.396l-1.502 2.889h-.95a3.694 3.694 0 0 1 0-7.389H10V7H8.444a5.194 5.194 0 1 0 0 10.389h.17L7.5 19.53l1.416.719L15.049 8.5h.507a3.694 3.694 0 0 1 0 7.39H14v1.5h1.556a5.194 5.194 0 0 0 .273-10.383l1.202-2.304Z" />
            ) : (
              <path d="M10 17.389H8.444A5.194 5.194 0 1 1 8.444 7H10v1.5H8.444a3.694 3.694 0 0 0 0 7.389H10v1.5ZM14 7h1.556a5.194 5.194 0 0 1 0 10.39H14v-1.5h1.556a3.694 3.694 0 0 0 0-7.39H14V7Zm-4.5 6h5v-1.5h-5V13Z" />
            )}
          </svg>
        </button>
      </ControlLabel>
      {isSplit ? (
        <>
          <BorderInput
            value={(value as any)?.top}
            onChange={(top) => onChange?.({ ...value, top } as any)}
          />
          <div className="wptb-border-control-vertical">
            <BorderInput
              isVertical
              value={(value as any)?.left}
              onChange={(left) => onChange?.({ ...value, left } as any)}
            />
            <BorderInput
              isVertical
              value={(value as any)?.right}
              onChange={(right) => onChange?.({ ...value, right } as any)}
            />
          </div>
          <BorderInput
            value={(value as any)?.bottom}
            onChange={(bottom) => onChange?.({ ...value, bottom } as any)}
          />
        </>
      ) : (
        <BorderInput
          value={value as any}
          onChange={(value) => onChange?.(value)}
        />
      )}
    </div>
  );
}

function BorderInput({ value, onChange, isVertical }: BorderInputProps) {
  if (!value) {
    value = {
      width: "0px",
      color: "transparent",
      style: "solid",
    };
  } else if (typeof value === "string") {
    const [width, style, ...color] = value.split(" ");
    value = {
      width,
      style,
      color: color.join(" "),
    } as BorderSide;
  }

  const numVal = parseInt(value?.width) || 0;
  const unit = value?.width?.replace(numVal.toString(), "") || "px";

  return (
    <div
      className={cls("wptb-border-control-input", {
        "wptb-vertical": isVertical,
      })}
    >
      <div className="wptb-border-control-input-group">
        <label className="wptb-border-control-input-color">
          <input
            type="color"
            value={value?.color}
            onChange={(e) => onChange({ ...value, color: e.target.value })}
          />
          <div
            className="wptb-border-control-input-color-preview"
            style={{ backgroundColor: value?.color }}
          />
        </label>
        <div className="wptb-border-control-input-width">
          <UnitControl
            value={numVal}
            unit={unit}
            onValueChange={(width) =>
              onChange({ ...value, width: `${width}${unit}` })
            }
            onUnitChange={(newUnit) =>
              onChange({
                ...value,
                width: `${convertUnits(unit, newUnit, numVal)}${newUnit}`,
              })
            }
            allowedUnits={units}
          />
        </div>
      </div>
      <div className="wptb-border-control-input-range">
        <input
          type="range"
          className="wptb-range-slider"
          min="0"
          max={Math.max(units[unit]?.max || 20, numVal)}
          value={numVal}
          onChange={(e) => {
            onChange({
              ...value,
              width: `${e.target.value}${unit}`,
            });
          }}
        />
      </div>
    </div>
  );
}
