import { useState } from "react";
import cls from "classnames";

interface PanelBodyProps {
  children: any;
  initialOpen?: boolean;
  title: string;
  className?: string;
}

export default function PanelBody({
  children,
  initialOpen,
  title,
  className,
}: PanelBodyProps) {
  const [open, setOpen] = useState(initialOpen);
  return (
    <div className={cls("wptb-panel-body", className, { "wptb-open": open })}>
      <div className="wptb-panel-body-header">
        <button
          type="button"
          className="wptb-panel-body-title"
          onClick={() => setOpen(!open)}
        >
          {title} <span>{open ? "▼" : "▶"}</span>
        </button>
      </div>
      {open && <div className="wptb-panel-body-content">{children}</div>}
    </div>
  );
}
