import { ENHANCED_CONTROLS, ENHANCEMENT_MAP } from "@/block_registry";

interface EnhancementGroupProps {
  name: string;
  props?: any;
  setProps?: (props: any) => void;
  blockPath?: string[];
}

export const EnhancementGroup = ({
  name,
  props,
  setProps,
  blockPath,
}: EnhancementGroupProps) => {
  const Cmp = ENHANCEMENT_MAP[name];
  if (!Cmp) {
    return null;
  }
  return (
    <Cmp blockProps={props} setBlockProps={setProps} blockPath={blockPath} />
  );
};

interface EnhancedControlProps {
  name: string;
  children: any;
}

const EnhancedControl = ({ name, children }: EnhancedControlProps) => {
  ENHANCED_CONTROLS[name] = children;
  return null;
};

export default EnhancedControl;
