import { PayloadAction } from "@reduxjs/toolkit";
import { TablesState } from "@wptb/dev/types";
import { cloneBlock, createBlock, removeWithInnerBlocks } from "./utils";
import TimeTravelCapturer from "../time-travel/capturer";

export const internalAddBlock = <T = any>(
  tables: TablesState,
  action: PayloadAction<{
    tableId: string;
    parentId: string;
    blockName: string;
    props?: T;
    index?: number;
    siblingId?: string;
    after?: boolean;
    newId?: {
      current: string;
    };
    capturer: TimeTravelCapturer;
  }>
) => {
  const {
    tableId,
    parentId,
    blockName,
    props,
    index,
    siblingId,
    after = true,
    newId,
    capturer,
  } = action.payload;

  const state = tables[tableId];

  const target = state.innerBlocks[parentId];
  if (!target) {
    throw new Error(`Parent block ${parentId} not found`);
  }
  const blockId = createBlock(state, blockName, parentId, capturer, props);

  if (newId) {
    newId.current = blockId;
  }

  if (index !== undefined) {
    target.blockOrder.splice(index, 0, blockId);
    return;
  }
  if (!siblingId) {
    if (after) {
      target.blockOrder.push(blockId);
    } else {
      target.blockOrder.unshift(blockId);
    }
    return;
  }

  let idx = target.blockOrder.indexOf(siblingId);
  if (after) {
    idx += 1;
  }
  target.blockOrder.splice(idx, 0, blockId);
};

export const internalRemoveBlock = (
  tables: TablesState,
  action: PayloadAction<{
    tableId: string;
    blockId: string;
    capturer: TimeTravelCapturer;
  }>
) => {
  const { blockId, capturer, tableId } = action.payload;

  const state = tables[tableId];

  const target = state.innerBlocks[blockId];
  if (!target) {
    throw new Error(`Block ${blockId} not found`);
  }
  removeWithInnerBlocks(state, blockId, capturer);
};

export const internalDuplicateBlock = (
  tables: TablesState,
  action: PayloadAction<{
    tableId: string;
    blockId: string;
    capturer: TimeTravelCapturer;
  }>
) => {
  const { blockId, capturer, tableId } = action.payload;

  const state = tables[tableId];

  const target = state.innerBlocks[blockId];
  if (!target) {
    throw new Error(`Block ${blockId} not found`);
  }
  const parent = state.innerBlocks[target.parentId];
  const idx = parent.blockOrder.indexOf(blockId);
  parent.blockOrder.splice(idx + 1, 0, cloneBlock(state, blockId, capturer));
};

export const internalmoveBlockInto = (
  tables: TablesState,
  action: PayloadAction<{
    tableId: string;
    blockId: string;
    targetId: string;
    siblingId?: string;
    after?: boolean;
    capturer: TimeTravelCapturer;
  }>
) => {
  const { blockId, targetId, siblingId, after, capturer, tableId } =
    action.payload;

  const state = tables[tableId];

  const subject = state.innerBlocks[blockId];
  if (!subject) {
    throw new Error(`Block ${blockId} not found`);
  }
  const newParent = state.innerBlocks[targetId];
  if (!newParent) {
    throw new Error(`Parent block ${targetId} not found`);
  }

  capturer.moveBlock(blockId);

  const oldParent = state.innerBlocks[subject.parentId];

  if (oldParent) {
    oldParent.blockOrder = oldParent.blockOrder.filter((id) => id !== blockId);
  }

  subject.parentId = targetId;

  if (!siblingId) {
    if (after) {
      newParent.blockOrder.push(blockId);
    } else {
      newParent.blockOrder.unshift(blockId);
    }
    return;
  }
  if (after) {
    newParent.blockOrder.splice(
      newParent.blockOrder.indexOf(siblingId) + 1,
      0,
      blockId
    );
  } else {
    newParent.blockOrder.splice(
      newParent.blockOrder.indexOf(siblingId),
      0,
      blockId
    );
  }
};
