import { useMemo, useRef, useState } from "react";
import { createPortal } from "react-dom";

interface PopUpProps {
  children: React.ReactNode;
  content: React.ReactNode;
  className?: string;
}

const PopUp = ({ children, content, className }: PopUpProps) => {
  const elRef = useRef<HTMLDivElement>(null);
  const contentRef = useRef<HTMLDivElement>(null);
  const [open, setOpen] = useState(false);
  const [show, setShow] = useState(false);
  const position = useMemo(
    () => () => {
      if (!elRef.current || !contentRef.current) {
        requestAnimationFrame(position);
        return;
      }
      const to = elRef.current.getBoundingClientRect();
      const content = contentRef.current.getBoundingClientRect();

      let top = to.top - content.height - 10;
      let left = to.left - content.width / 2;
      if (top < 10) {
        top = to.bottom + 10;
      }
      if (left < 10) {
        left = to.right + 10;
      }

      contentRef.current.style.top = `${top}px`;
      contentRef.current.style.left = `${left}px`;
    },
    []
  );

  return (
    <>
      <div
        className={`wptb-pop-up ${className || ""}`}
        ref={elRef}
        onMouseEnter={() => {
          setShow(true);
          position();
        }}
        onMouseLeave={() => {
          setShow(false);
        }}
        onPointerUp={() => {
          setOpen(!open);
          setShow(false);
          position();
        }}
      >
        {children}
      </div>
      {(open || show) &&
        createPortal(
          <div className="wptb-pop-up-content" ref={contentRef}>
            {content}
          </div>,
          document.body
        )}
    </>
  );
};

export default PopUp;
