import { createPortal } from "react-dom";

interface ModalProps {
  title?: string;
  onClose?: () => void;
  children?: React.ReactNode;
  icon?: any;
  body?: any;
  className?: string;
}

const Modal = ({
  onClose,
  title,
  children,
  icon,
  body,
  className,
}: ModalProps) => {
  return createPortal(
    <div
      className={`wptb-modal ${className || ""}`}
      onPointerUp={(evt) => evt.stopPropagation()}
    >
      <div className="wptb-modal-body">
        <div className="wptb-modal-icon">{icon}</div>
        <div className="wptb-modal-content">
          <h2 className="wptb-modal-title">{title}</h2>
          {children}
        </div>
        <button type="button" className="wptb-modal-close" onClick={onClose}>
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="1em"
            height="1em"
            viewBox="0 0 24 24"
          >
            <path
              fill="currentColor"
              d="M20 6.91L17.09 4L12 9.09L6.91 4L4 6.91L9.09 12L4 17.09L6.91 20L12 14.91L17.09 20L20 17.09L14.91 12z"
            />
          </svg>
        </button>
        {body}
      </div>
    </div>,
    document.body
  );
};

export default Modal;
