import { v4 as uuidv4 } from "uuid";
import { PayloadAction } from "@reduxjs/toolkit";
import {
  MergeBoundary,
  ResponsiveDeviceName,
  TablesState,
  TableStateDevice,
} from "@wptb/dev/types";
import { CellProps, TableProps } from "@wptb/dev/types";
import { TABLE_ID } from ".";
import { toResponsive } from "./responsive";
import TimeTravelCapturer from "../time-travel/capturer";

export const internalMergeCells = (
  tables: TablesState,
  action: PayloadAction<{
    tableId: string;
    cells: string[];
    boundary: MergeBoundary;
    capturer: TimeTravelCapturer;
    device?: ResponsiveDeviceName;
  }>
) => {
  const {
    cells,
    boundary: { top, right, bottom, left },
    device,
    capturer,
    tableId,
  } = action.payload;

  const state = tables[tableId];

  const table: TableStateDevice = state.desktop;
  const innerBlocks = state.innerBlocks;
  const blocksProps = state.blocksProps;

  const targetId = cells.shift()!;

  const target = innerBlocks[targetId];
  if (!target) {
    return;
  }
  const tableProps: TableProps = blocksProps[TABLE_ID];

  capturer.table();

  for (let i = 0; i < cells.length; i++) {
    const cellId = cells[i];
    const cellBlock = innerBlocks[cellId];
    const rowId = cellBlock.parentId;
    table.innerBlocks[rowId] = table.innerBlocks[rowId].filter(
      (id) => id !== cellId
    );
    cellBlock.blockOrder.forEach((id) => {
      innerBlocks[id].parentId = targetId;
      target.blockOrder.push(id);
      capturer.moveBlock(id);
    });
    delete innerBlocks[cellId];
    delete blocksProps[cellId];
    capturer.removeBlock(cellId);
  }

  let rowShift = 0,
    colShift = 0,
    colspan = right - left + 1,
    rowspan = bottom - top + 1;

  if (bottom - top === tableProps.rows - 1) {
    colShift = right - left;
  }

  if (right - left === tableProps.cols - 1) {
    rowShift = bottom - top;
  }

  if (rowShift) {
    for (let i = top + 1; i < table.blockOrder.length; i++) {
      const rowId = table.blockOrder[i];
      const cells = table.innerBlocks[rowId];
      if (!cells.length) {
        table.blockOrder.splice(i, 1);
        delete blocksProps[rowId];
        delete table.innerBlocks[rowId];
        capturer.removeBlock(rowId);
        i--;
        continue;
      }
      cells.forEach((id) => {
        const cell = blocksProps[id];
        cell.row = cell.row - rowShift;
        capturer.props(id);
      });
    }
    tableProps.rows = tableProps.rows - rowShift;
    rowspan = 1;
  }

  if (colShift) {
    const columns = tableProps.columns || {};
    for (let i = 0; i < table.blockOrder.length; i++) {
      const rowId = table.blockOrder[i];
      const cells = table.innerBlocks[rowId];
      cells.forEach((id) => {
        const cell: CellProps = blocksProps[id];
        if (cell.col <= left) {
          return;
        }
        cell.col = cell.col - colShift;
      });
    }
    for (const col in columns) {
      const coldIdx = parseInt(col);
      if (coldIdx <= left) {
        columns[coldIdx] = columns[col];
      } else if (coldIdx >= right) {
        columns[coldIdx - colShift] = columns[col];
      }
    }
    tableProps.columns = columns;
    tableProps.cols = tableProps.cols - colShift;
    colspan = 1;
  }

  blocksProps[targetId] = {
    ...blocksProps[targetId],
    rowspan,
    colspan,
  };
  capturer.props(targetId);
  capturer.props(TABLE_ID);

  state.mobile = state.desktop;
  state.tablet = state.desktop;

  if (device) {
    state[device] = toResponsive(state, device);
  }
};

export const internalSplitCell = (
  tables: TablesState,
  action: PayloadAction<{
    tableId: string;
    cellId: string;
    device?: ResponsiveDeviceName;
    capturer: TimeTravelCapturer;
  }>
) => {
  const { cellId, device, tableId, capturer } = action.payload;

  const state = tables[tableId];

  const blocksProps = state.blocksProps;
  const innerBlocks = state.innerBlocks;

  const rowId = innerBlocks[cellId].parentId;
  const cellBlockProps = blocksProps[cellId] as CellProps;
  const { colspan, rowspan, col } = cellBlockProps;

  if (colspan === 1 && rowspan === 1) {
    return;
  }
  const table: TableStateDevice = state.desktop;
  const rowIdx = table.blockOrder.indexOf(rowId);


  for (let i = rowIdx; i < rowspan + rowIdx; i++) {
    const cRowId = table.blockOrder[i];
    const cells = table.innerBlocks[cRowId];
    let colIdx = col;
    let cell = blocksProps[cells[colIdx]];
    while (cell?.col > col) {
      colIdx--;
      cell = blocksProps[cells[colIdx]];
    }
    colIdx++;
    let c = col;
    if (i == rowIdx) {
      c++;
    }
    for (; c < col + colspan; c++) {
      const id = uuidv4();
      blocksProps[id] = {
        ...cellBlockProps,
        row: i,
        col: c,
        colspan: 1,
        rowspan: 1,
      };
      innerBlocks[id] = {
        name: "cell",
        blockOrder: [],
        parentId: cRowId,
      };
      cells.splice(colIdx, 0, id);
      capturer.addBlock(id);
      colIdx++;
    }
  }
  cellBlockProps.rowspan = 1;
  cellBlockProps.colspan = 1;
  capturer.props(cellId);
  capturer.table();

  state.mobile = state.desktop;
  state.tablet = state.desktop;
  if (device) {
    state[device] = toResponsive(state, device);
  }
};
