import { MouseEvent as MouseEventReact } from "react";
import {
  blockActionsEl,
  FloatingCtx,
  moveIndicator,
  moveIndicatorTd,
  targetIndicator,
  canBeInsertedInto,
  getTargetBlock,
} from "./helpers";
import { duplicateBlock, moveBlockInto, removeBlock } from "@/store/table";
import TimeTravel from "@/store/time-travel";
import BuilderCtx from "@/builder/ctx";

export default class BlockActions {
  static overCellId?: string;

  static target?: string;
  static targetEl?: HTMLElement | null;
  static targetName?: string;

  static moveTarget?: string;
  static positionAfter?: boolean;
  static moveTargetName?: string;
  static moveTargetSibling?: string;

  static onStartBlockMove() {
    if (!BlockActions.target) {
      return;
    }

    FloatingCtx.isMoving = true;

    BlockActions.targetEl?.classList.add("wptb-block-action-pending");
    blockActionsEl.classList.add("wptb-floating-block-actions-hidden");
    targetIndicator.innerHTML = "MOVE HERE";

    BlockActions.moveTarget = undefined;
    BlockActions.moveTargetSibling = undefined;

    document.addEventListener("mouseup", BlockActions.onMovingDrop);
  }

  static async onCellEnter(evt: MouseEventReact, target: string) {
    if (!FloatingCtx.isMoving) {
      return;
    }
    document.body.appendChild(targetIndicator);
    if (!canBeInsertedInto(BlockActions.targetName!, "cell")) {
      return;
    }
    BlockActions.overCellId = target;
    BlockActions.moveTarget = target;
    BlockActions.moveTargetName = "cell";
    BlockActions.moveTargetSibling = undefined;
    BlockActions.positionAfter = moveIndicatorTd(
      evt.currentTarget as any,
      evt.clientY
    );
  }

  static async onCellMove(evt: any) {
    if (!FloatingCtx.isMoving || evt.wptbMovePosOk) {
      return;
    }
    BlockActions.positionAfter = moveIndicatorTd(
      evt.currentTarget! as any,
      evt.clientY
    );
  }

  static async onCellLeave() {
    if (!FloatingCtx.isMoving) {
      return;
    }
    targetIndicator.remove();
    BlockActions.overCellId = undefined;
    BlockActions.moveTarget = undefined;
    BlockActions.moveTargetSibling = undefined;
  }

  static async onBlockEnter(
    evt: MouseEventReact,
    name: string,
    target: string
  ) {
    if (
      FloatingCtx.isInserting ||
      FloatingCtx.isRowColMoving ||
      // @ts-expect-error custom event prop
      evt.wptbMoveTargetOk
    ) {
      return;
    }
    const el = evt.currentTarget as HTMLElement;
    if (!FloatingCtx.isMoving) {
      BlockActions.targetName = name;
      BlockActions.target = target;
      BlockActions.targetEl = el;
      el.classList.add("wptb-block-actions-visible");
      blockActionsEl.classList.remove("wptb-floating-block-actions-hidden");
      const box = el.getBoundingClientRect();
      blockActionsEl.style.bottom = window.innerHeight - box.top + "px";
      blockActionsEl.style.right = document.body.clientWidth - box.right + "px";
      return;
    }

    if (BlockActions.targetEl === el) {
      BlockActions.moveTarget = undefined;
      BlockActions.moveTargetName = undefined;
      BlockActions.moveTargetSibling = undefined;
      return;
    }

    if (canBeInsertedInto(BlockActions.targetName!, name)) {
      // @ts-expect-error custom event prop
      evt.wptbMoveTargetOk = true;
      BlockActions.moveTarget = target;
      BlockActions.moveTargetName = name;
      BlockActions.moveTargetSibling = undefined;
      BlockActions.positionAfter = moveIndicator(
        evt.currentTarget as any,
        evt.clientY
      );

      return;
    }
    const ptarget = getTargetBlock(target, BuilderCtx.TABLE_ID!)?.parentId;
    const ptName = ptarget
      ? getTargetBlock(ptarget, BuilderCtx.TABLE_ID!)?.name
      : undefined;

    if (!canBeInsertedInto(BlockActions.targetName!, ptName!)) {
      BlockActions.moveTarget = undefined;
      BlockActions.moveTargetName = undefined;
      BlockActions.moveTargetSibling = undefined;
      return;
    }
    // @ts-expect-error custom event prop
    evt.wptbMoveTargetOk = true;
    BlockActions.moveTarget = ptarget;
    BlockActions.moveTargetName = ptName;
    BlockActions.moveTargetSibling = target;
    BlockActions.positionAfter = moveIndicator(
      evt.currentTarget as any,
      evt.clientY
    );
  }

  static onBlockMove(evt: any, _: string, id: string) {
    if (!FloatingCtx.isMoving) {
      return;
    }
    if (id !== BlockActions.moveTargetSibling || evt.wptbMovePosOk) {
      return;
    }
    evt.wptbMovePosOk = true;
    BlockActions.positionAfter = moveIndicator(evt.currentTarget!, evt.clientY);
  }

  static async onBlockLeave(el: HTMLElement) {
    if (FloatingCtx.isInserting) {
      return;
    }
    el.classList.remove("wptb-block-actions-visible");
    blockActionsEl.classList.add("wptb-floating-block-actions-hidden");
    if (FloatingCtx.isMoving) {
      if (BlockActions.moveTarget && BlockActions.moveTargetName !== "cell") {
        BlockActions.moveTarget = BlockActions.overCellId;
      }
      BlockActions.moveTargetSibling = undefined;
      return;
    }
  }

  static onMovingDrop() {
    document.removeEventListener("mouseup", BlockActions.onMovingDrop);
    FloatingCtx.isMoving = false;
    BlockActions.targetEl?.classList.remove("wptb-block-action-pending");
    targetIndicator.remove();
    if (!BlockActions.target || !BlockActions.moveTarget) {
      return;
    }

    TimeTravel.captured(
      moveBlockInto,
      {
        blockId: BlockActions.target,
        targetId: BlockActions.moveTarget,
        siblingId: BlockActions.moveTargetSibling,
        after: BlockActions.positionAfter,
      },
      false
    );

    BlockActions.target = undefined;
    BlockActions.targetEl = undefined;
    BlockActions.moveTarget = undefined;
    BlockActions.moveTargetName = undefined;
    BlockActions.moveTargetSibling = undefined;
    BlockActions.positionAfter = undefined;
  }

  static onRemoveBlock() {
    if (!BlockActions.target) {
      return;
    }

    TimeTravel.captured(
      removeBlock,
      {
        blockId: BlockActions.target,
      },
      false
    );

    BlockActions.target = undefined;
    BlockActions.targetEl = undefined;
  }
  static onDuplicateBlock() {
    if (!BlockActions.target) {
      return;
    }

    TimeTravel.captured(
      duplicateBlock,
      {
        blockId: BlockActions.target,
      },
      false
    );

    BlockActions.target = undefined;
    BlockActions.targetEl = undefined;
  }

  static onActionHover() {
    if (FloatingCtx.isInserting || FloatingCtx.isMoving) {
      return;
    }
    BlockActions.targetEl?.classList.add("wptb-block-actions-visible");
  }

  static clearTarget() {
    BlockActions.targetEl?.classList.remove("wptb-block-actions-visible");
    if (FloatingCtx.isMoving) {
      return;
    }

    BlockActions.target = undefined;
    BlockActions.targetEl = undefined;
  }
}

blockActionsEl.addEventListener("mouseenter", BlockActions.onActionHover);
blockActionsEl.addEventListener("mouseleave", BlockActions.clearTarget);

blockActionsEl.children[0].addEventListener(
  "mousedown",
  BlockActions.onStartBlockMove
);
blockActionsEl.children[1].addEventListener(
  "click",
  BlockActions.onDuplicateBlock
);
blockActionsEl.children[2].addEventListener(
  "click",
  BlockActions.onRemoveBlock
);
